
import React, { useState } from 'react';
import { FileText, Check } from 'lucide-react';

export const RightsAnalysisChecklist = () => {
    const [checks, setChecks] = useState({
        ownership: false, mortgage: false, lease: false, seizure: false, provisionalSeizure: false,
        rental: false, auction: false, trust: false
    });

    const checkItems = [
        { key: 'ownership', label: ' ', desc: '    ', risk: 'low' },
        { key: 'mortgage', label: '()', desc: '    ', risk: 'high' },
        { key: 'lease', label: '', desc: '    ', risk: 'medium' },
        { key: 'seizure', label: '', desc: '    ', risk: 'high' },
        { key: 'provisionalSeizure', label: '/', desc: '   ', risk: 'high' },
        { key: 'rental', label: ' ', desc: '  ', risk: 'medium' },
        { key: 'auction', label: '', desc: '   ', risk: 'high' },
        { key: 'trust', label: '', desc: '   ', risk: 'medium' },
    ];

    const getRiskColor = (risk) => {
        if (risk === 'high') return 'text-red-600 bg-red-50 border-red-200';
        if (risk === 'medium') return 'text-orange-600 bg-orange-50 border-orange-200';
        return 'text-green-600 bg-green-50 border-green-200';
    };

    const checkedHighRisk = checkItems.filter(item => item.risk === 'high' && checks[item.key]).length;
    const totalChecked = Object.values(checks).filter(Boolean).length;
    const safetyScore = Math.max(0, 100 - (checkedHighRisk * 30) - (totalChecked * 5));

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-2 flex items-center">
                    <FileText className="w-5 h-5 mr-3 text-violet-600" />  
                </h3>
                <p className="text-xs text-slate-400 mb-6">    </p>

                <div className="space-y-3">
                    {checkItems.map(item => (
                        <button
                            key={item.key}
                            onClick={() => setChecks({ ...checks, [item.key]: !checks[item.key] })}
                            className={`w-full p-4 rounded-2xl border-2 text-left transition-all ${checks[item.key] ? getRiskColor(item.risk) : 'bg-slate-50 border-slate-100'}`}
                        >
                            <div className="flex items-center justify-between">
                                <div>
                                    <p className="font-bold text-sm">{item.label}</p>
                                    <p className="text-xs opacity-70">{item.desc}</p>
                                </div>
                                <div className={`w-6 h-6 rounded-full border-2 flex items-center justify-center ${checks[item.key] ? 'bg-current border-current' : 'border-slate-300'}`}>
                                    {checks[item.key] && <Check className="w-4 h-4 text-white" />}
                                </div>
                            </div>
                        </button>
                    ))}
                </div>
            </div>

            {/*   */}
            <div className={`p-6 rounded-[2rem] ${safetyScore >= 70 ? 'bg-green-50 border border-green-200' : safetyScore >= 40 ? 'bg-yellow-50 border border-yellow-200' : 'bg-red-50 border border-red-200'}`}>
                <div className="text-center">
                    <p className="text-xs font-bold text-slate-500 mb-1">  </p>
                    <p className={`text-5xl font-black ${safetyScore >= 70 ? 'text-green-600' : safetyScore >= 40 ? 'text-yellow-600' : 'text-red-600'}`}>
                        {safetyScore}
                    </p>
                    <p className="text-sm text-slate-500 mt-2">
                        {safetyScore >= 70 ? '   ' : safetyScore >= 40 ? '  ' : '   '}
                    </p>
                </div>
            </div>
        </div>
    );
};
