import React, { useState, useEffect, useMemo, useId, memo } from 'react';
import { getSafeLocalStorage, debouncedSetStorage } from '../../utils/storage.js';
import { parseNumber } from '../../utils/math.js';
import { formatNumber, formatKoreanNumber } from '../../utils/formatters.js';
import { AlertTriangle, RotateCcw } from './Icons.js';

export const NumberInput = memo(({
    label,
    value,
    onChange,
    unit = '',
    placeholder = '0',
    color = 'blue',
    enableUnitToggle = true,
    showKorean = true,
    min = 0,
    max = null,
    required = false,
    errorMessage = ''
}) => {
    const [unitMode, setUnitMode] = useState(() => getSafeLocalStorage('unitMode', ''));
    const [isTouched, setIsTouched] = useState(false);
    const [hasError, setHasError] = useState(false);
    const inputId = useId();

    // Save unit mode preference (debounced)
    useEffect(() => {
        debouncedSetStorage('unitMode', unitMode);
    }, [unitMode]);

    // Validate input
    useEffect(() => {
        if (!isTouched) return;
        const numValue = parseNumber(value);
        let isValid = true;

        if (required && !value) isValid = false;
        if (min !== null && numValue < min) isValid = false;
        if (max !== null && numValue > max) isValid = false;

        setHasError(!isValid && value !== '');
    }, [value, min, max, required, isTouched]);

    const handleChange = (e) => {
        const rawValue = e.target.value.replace(/,/g, '');
        //   
        if (rawValue === '') {
            onChange('');
            return;
        }
        //     
        if (!isNaN(rawValue)) {
            let numValue = parseFloat(rawValue || 0);
            //  : 0  0 
            if (numValue < 0) numValue = 0;

            if (enableUnitToggle && unitMode === '') {
                onChange(String(Math.round(numValue * 10000)));
            } else {
                onChange(String(numValue));
            }
        }
    };

    const handleBlur = () => setIsTouched(true);

    const displayValue = useMemo(() => {
        if (!value) return '';
        if (enableUnitToggle && unitMode === '') {
            const manwon = Math.round(parseNumber(value) / 10000);
            return formatNumber(manwon);
        }
        return formatNumber(value);
    }, [value, unitMode, enableUnitToggle]);

    const koreanDisplay = useMemo(() => {
        if (!showKorean || !value) return '';
        return formatKoreanNumber(value);
    }, [value, showKorean]);

    const focusColors = {
        blue: 'focus:ring-blue-500',
        orange: 'focus:ring-orange-500',
        green: 'focus:ring-emerald-500',
        violet: 'focus:ring-violet-500',
        cyan: 'focus:ring-cyan-500'
    };

    const errorClasses = hasError ? 'ring-red-400 bg-red-50' : '';

    return (
        <div className="flex flex-col space-y-1.5">
            <div className="flex justify-between items-center">
                <label htmlFor={inputId} className="text-xs font-bold text-slate-500 uppercase tracking-widest ml-1">
                    {label}
                    {required && <span className="text-red-500 ml-0.5">*</span>}
                </label>
                {enableUnitToggle && (
                    <div className="flex text-[11px] font-bold">
                        <button
                            onClick={() => setUnitMode('')}
                            type="button"
                            className={`px-3 py-1.5 min-h-[32px] rounded-l-lg transition-all active:scale-95 ${unitMode === '' ? 'bg-slate-800 text-white shadow-sm' : 'bg-slate-100 text-slate-500 hover:bg-slate-200'}`}
                        ></button>
                        <button
                            onClick={() => setUnitMode('')}
                            type="button"
                            className={`px-3 py-1.5 min-h-[32px] rounded-r-lg transition-all active:scale-95 ${unitMode === '' ? 'bg-slate-800 text-white shadow-sm' : 'bg-slate-100 text-slate-500 hover:bg-slate-200'}`}
                        ></button>
                    </div>
                )}
            </div>
            <div className="relative group">
                <input
                    id={inputId}
                    type="text"
                    inputMode="numeric"
                    value={displayValue}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    onFocus={(e) => e.target.select()}
                    placeholder={placeholder}
                    aria-label={label}
                    aria-invalid={hasError}
                    className={`w-full pl-5 pr-14 py-4 text-right border-2 border-transparent bg-gradient-to-br from-slate-50 to-slate-100 rounded-2xl shadow-sm ${focusColors[color]} focus:ring-2 focus:bg-white focus:border-indigo-400 focus:scale-[1.01] outline-none transition-premium font-bold text-slate-800 text-lg group-hover:border-slate-200 group-hover:shadow-md ${hasError ? 'input-error-shake border-red-400 bg-red-50' : ''}`}
                />
                <span className="absolute right-4 top-1/2 transform -translate-y-1/2 text-slate-400 text-sm font-semibold pointer-events-none transition-colors group-focus-within:text-indigo-500">
                    {enableUnitToggle ? unitMode : unit}
                </span>
            </div>
            {hasError && (
                <p className="text-xs text-red-500 ml-1 flex items-center gap-1 animate-pulse">
                    <AlertTriangle size={12} /> {errorMessage || ' '}
                </p>
            )}
            {showKorean && koreanDisplay && !hasError && (
                <div className="text-xs text-slate-400 ml-1 font-medium">
                     {koreanDisplay}
                </div>
            )}
        </div>
    );
});

export const ToggleGroup = memo(({ options, value, onChange, color = 'blue' }) => {
    const activeClasses = {
        blue: 'bg-white text-blue-600 shadow-md ring-1 ring-black/5',
        green: 'bg-white text-emerald-600 shadow-md ring-1 ring-black/5',
        violet: 'bg-white text-violet-600 shadow-md ring-1 ring-black/5',
        orange: 'bg-white text-orange-600 shadow-md ring-1 ring-black/5',
    };

    return (
        <div role="radiogroup" className="flex bg-slate-100/80 p-1.5 rounded-2xl">
            {options.map((option) => (
                <button
                    role="radio"
                    aria-checked={value === option}
                    key={option}
                    onClick={() => onChange(option)}
                    className={`flex-1 py-3 min-h-[44px] text-xs font-bold rounded-xl transition-all active:scale-95 ${value === option
                        ? activeClasses[color] || activeClasses.blue
                        : 'text-slate-400 hover:text-slate-600 hover:bg-white/50'
                        }`}
                >
                    {option}
                </button>
            ))}
        </div>
    );
});

// Reset Button Component
export const ResetButton = ({ onClick, className = '' }) => (
    <button
        type="button"
        onClick={onClick}
        className={`p-2 text-slate-400 hover:text-red-500 hover:bg-red-50 rounded-xl transition-all active:scale-95 ${className}`}
        title=""
        aria-label=" "
    >
        <RotateCcw size={18} />
    </button>
);

// --- Constants for ToggleGroups ---
export const OPTIONS_PROP_TYPE = ['', '', ''];
export const OPTIONS_DEAL_TYPE = ['', ''];
export const OPTIONS_LOAN_METHOD = ['', '', ''];
export const OPTIONS_REGION = ['', '()'];
export const OPTIONS_COUNT = ['1', '2', '3+'];
export const OPTIONS_YEARS = ['1', '2', '3'];
