
import React, { useState, useMemo } from 'react';
import { parseNumber, formatNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';
import { OPTIONS_YEARS } from '../../utils/constants';

export const InsuranceCalculator = () => {
    const [deposit, setDeposit] = useState('');
    const [years, setYears] = useState('2');
    const [insuranceType, setInsuranceType] = useState('hug');

    const result = useMemo(() => {
        const depVal = parseNumber(deposit);
        const yearsVal = parseInt(years) || 1;

        //  ( , ,    )
        const rates = {
            hug: 0.00128,      // HUG   0.128%
            sgi: 0.00115,      // SGI  0.115%
            rental: 0.0005    //    0.05%
        };

        const rate = rates[insuranceType] || rates.hug;
        const fee = Math.floor(depVal * rate * yearsVal);
        const monthlyFee = Math.floor(fee / (yearsVal * 12));

        return { fee, monthlyFee, rate: rate * 100 };
    }, [deposit, years, insuranceType]);

    return (
        <div className="bg-white p-6 rounded-[2rem] shadow-sm border border-slate-100 mt-2">
            <div className="flex gap-2 mb-6 overflow-x-auto no-scrollbar">
                {[
                    { id: 'hug', label: 'HUG ' },
                    { id: 'sgi', label: 'SGI ' },
                    { id: 'rental', label: '' }
                ].map(t => (
                    <button
                        key={t.id}
                        onClick={() => setInsuranceType(t.id)}
                        className={`flex-1 py-3 px-3 rounded-xl text-xs font-bold transition-all whitespace-nowrap ${insuranceType === t.id ? 'bg-emerald-500 text-white shadow-lg' : 'bg-slate-100 text-slate-500'}`}
                    >
                        {t.label}
                    </button>
                ))}
            </div>

            <div className="space-y-4">
                <NumberInput label=" " value={deposit} onChange={setDeposit} color="green" enableUnitToggle={true} />

                <div>
                    <label className="text-xs font-bold text-slate-500 uppercase tracking-widest mb-2 block ml-1">  ()</label>
                    <div className="flex gap-2">
                        {OPTIONS_YEARS.map(y => (
                            <button
                                key={y}
                                onClick={() => setYears(y)}
                                className={`flex-1 py-3 rounded-xl text-sm font-bold transition-all ${years === y ? 'bg-emerald-500 text-white' : 'bg-slate-100 text-slate-500'}`}
                            >
                                {y}
                            </button>
                        ))}
                    </div>
                </div>
            </div>

            <div className="mt-6 p-6 bg-gradient-to-r from-emerald-50 to-teal-50 rounded-2xl">
                <div className="flex justify-between items-center mb-4">
                    <span className="text-sm font-medium text-emerald-600">  ( {result.rate.toFixed(3)}%)</span>
                    <span className="text-2xl font-black text-emerald-900">{formatNumber(result.fee)}</span>
                </div>
                <div className="flex justify-between items-center text-sm">
                    <span className="text-slate-500"> </span>
                    <span className="font-bold text-slate-700"> {formatNumber(result.monthlyFee)}/</span>
                </div>
                <p className="text-xs text-slate-400 mt-4">
                    *    , ,      .
                </p>
            </div>
        </div>
    );
};
