
import React, { useState, useMemo } from 'react';
import { RefreshCw } from 'lucide-react';
import { parseNumber, formatNumber, formatCompactNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const LoanRefinancingAnalyzer = () => {
    const [currentLoan, setCurrentLoan] = useState('');
    const [currentRate, setCurrentRate] = useState('5.0');
    const [remainingMonths, setRemainingMonths] = useState('300');
    const [newRate, setNewRate] = useState('4.0');
    const [newPeriod, setNewPeriod] = useState('360');
    const [refinanceFee, setRefinanceFee] = useState('0.5');

    const result = useMemo(() => {
        const loan = parseNumber(currentLoan);
        const oldRate = parseFloat(currentRate) || 0;
        const oldMonths = parseInt(remainingMonths) || 0;
        const nRate = parseFloat(newRate) || 0;
        const nMonths = parseInt(newPeriod) || 0;
        const fee = parseFloat(refinanceFee) || 0;
        if (loan <= 0 || oldRate <= 0 || nRate <= 0) return null;

        const calcMonthly = (principal, rate, months) => {
            const r = rate / 100 / 12;
            if (r <= 0) return principal / months;
            const factor = Math.pow(1 + r, months);
            return (principal * r * factor) / (factor - 1);
        };

        const oldMonthly = calcMonthly(loan, oldRate, oldMonths);
        const oldTotalInterest = oldMonthly * oldMonths - loan;
        const newMonthly = calcMonthly(loan, nRate, nMonths);
        const newTotalInterest = newMonthly * nMonths - loan;
        const refinanceCost = loan * (fee / 100);
        const interestSaved = oldTotalInterest - newTotalInterest;
        const netSaving = interestSaved - refinanceCost;
        const monthlySaved = oldMonthly - newMonthly;
        const breakEvenMonths = monthlySaved > 0 ? Math.ceil(refinanceCost / monthlySaved) : 0;

        return { oldMonthly, newMonthly, monthlySaved, oldTotalInterest, newTotalInterest, refinanceCost, interestSaved, netSaving, breakEvenMonths, isWorth: netSaving > 0 };
    }, [currentLoan, currentRate, remainingMonths, newRate, newPeriod, refinanceFee]);

    return (
        <div className="bg-white p-6 rounded-[2rem] shadow-sm border border-slate-100 card-depth mt-2">
            <h4 className="text-sm font-bold text-slate-800 mb-4 flex items-center">
                <RefreshCw className="w-4 h-4 mr-2 text-violet-500" />  
            </h4>
            <div className="space-y-4">
                <NumberInput label="  " value={currentLoan} onChange={setCurrentLoan} color="violet" />
                <div className="p-4 bg-slate-50 rounded-xl">
                    <p className="text-xs font-bold text-slate-600 mb-2">  </p>
                    <div className="grid grid-cols-2 gap-2">
                        <div><label className="text-[10px] text-slate-500">(%)</label><input type="number" value={currentRate} onChange={e => setCurrentRate(e.target.value)} className="w-full px-3 py-2 bg-white rounded-lg text-sm font-bold text-right outline-none" /></div>
                        <div><label className="text-[10px] text-slate-500">()</label><input type="number" value={remainingMonths} onChange={e => setRemainingMonths(e.target.value)} className="w-full px-3 py-2 bg-white rounded-lg text-sm font-bold text-right outline-none" /></div>
                    </div>
                </div>
                <div className="p-4 bg-violet-50 rounded-xl">
                    <p className="text-xs font-bold text-violet-600 mb-2">  </p>
                    <div className="grid grid-cols-2 gap-2">
                        <div><label className="text-[10px] text-slate-500">(%)</label><input type="number" value={newRate} onChange={e => setNewRate(e.target.value)} className="w-full px-3 py-2 bg-white rounded-lg text-sm font-bold text-right outline-none" /></div>
                        <div><label className="text-[10px] text-slate-500">()</label><input type="number" value={newPeriod} onChange={e => setNewPeriod(e.target.value)} className="w-full px-3 py-2 bg-white rounded-lg text-sm font-bold text-right outline-none" /></div>
                    </div>
                </div>
                <div><label className="text-xs font-bold text-slate-500 block mb-1">  (%)</label><input type="number" value={refinanceFee} onChange={e => setRefinanceFee(e.target.value)} step="0.1" className="w-full px-4 py-3 bg-slate-50 rounded-xl text-right font-bold outline-none" /></div>
            </div>
            {result && (
                <div className="mt-4 space-y-3">
                    <div className={`p-4 rounded-xl text-center ${result.isWorth ? 'bg-green-50' : 'bg-red-50'}`}>
                        <p className="text-xs text-slate-500 mb-1"> </p>
                        <p className={`text-3xl font-black ${result.isWorth ? 'text-green-600' : 'text-red-600'}`}>{result.netSaving > 0 ? '+' : ''}{formatCompactNumber(result.netSaving)}</p>
                        <p className="text-xs mt-1">{result.isWorth ? '  ' : '  '}</p>
                    </div>
                    <div className="grid grid-cols-2 gap-2 text-xs">
                        <div className="p-3 bg-slate-50 rounded-xl"><p className="text-slate-500"> </p><p className="font-bold">{formatNumber(Math.round(result.oldMonthly))}</p></div>
                        <div className="p-3 bg-violet-50 rounded-xl"><p className="text-slate-500">  </p><p className="font-bold text-violet-600">{formatNumber(Math.round(result.newMonthly))}</p></div>
                    </div>
                    {result.breakEvenMonths > 0 && (
                        <div className="p-3 bg-amber-50 rounded-xl text-center text-xs">
                            <p className="text-slate-500"></p>
                            <p className="font-bold text-amber-600">{result.breakEvenMonths} </p>
                        </div>
                    )}
                </div>
            )}
        </div>
    );
};
