import React, { useState, useMemo, useEffect, useRef } from 'react';
import { PieChart as PieChartIcon } from 'lucide-react';
import Chart from 'chart.js/auto';
import { formatNumber, formatCompactNumber, parseNumber } from '../../utils/math.js';
import { NumberInput } from '../ui/Inputs.js';

export const CostCompositionChart = () => {
    const [purchasePrice, setPurchasePrice] = useState('');
    const chartRef = useRef(null);
    const chartInstanceRef = useRef(null);

    const costs = useMemo(() => {
        const price = parseNumber(purchasePrice);
        if (!price) return null;

        const acquisitionTax = Math.round(price * 0.01);
        const brokerageFee = Math.round(price * 0.004);
        const registrationFee = Math.round(price * 0.002);
        const insuranceFee = Math.round(price * 0.001);
        const movingCost = 3000; //  300

        const total = acquisitionTax + brokerageFee + registrationFee + insuranceFee + movingCost;
        return { acquisitionTax, brokerageFee, registrationFee, insuranceFee, movingCost, total };
    }, [purchasePrice]);

    useEffect(() => {
        if (!costs || !chartRef.current) return;
        if (chartInstanceRef.current) chartInstanceRef.current.destroy();

        chartInstanceRef.current = new Chart(chartRef.current, {
            type: 'doughnut',
            data: {
                labels: ['', '', '', '', ''],
                datasets: [{
                    data: [costs.acquisitionTax, costs.brokerageFee, costs.registrationFee, costs.insuranceFee, costs.movingCost],
                    backgroundColor: ['#6366f1', '#8b5cf6', '#ec4899', '#f97316', '#14b8a6'],
                    borderWidth: 0
                }]
            },
            options: { responsive: true, maintainAspectRatio: false, plugins: { legend: { position: 'bottom' } } }
        });

        return () => {
            if (chartInstanceRef.current) chartInstanceRef.current.destroy();
        };
    }, [costs]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <PieChartIcon className="w-5 h-5 mr-3 text-indigo-600" />   
                </h3>
                <NumberInput label="" value={purchasePrice} onChange={setPurchasePrice} color="indigo" />
            </div>
            {costs && (
                <div className="space-y-4">
                    <div className="bg-white p-6 rounded-2xl shadow-sm border border-slate-100">
                        <div className="h-64"><canvas ref={chartRef}></canvas></div>
                    </div>
                    <div className="bg-indigo-50 p-5 rounded-2xl border border-indigo-200 text-center">
                        <p className="text-xs font-bold text-indigo-600 mb-1"> </p>
                        <p className="text-2xl font-black text-indigo-800">{formatCompactNumber(costs.total)}</p>
                    </div>
                </div>
            )}
        </div>
    );
};
