
import React from 'react';

export const TaxCalendar = () => {
    const taxEvents = [
        { month: 1, title: ' 1', desc: '6  (1~6)' },
        { month: 6, title: ' 1', desc: '7/16~7/31 ' },
        { month: 7, title: ' 1 ', desc: ' 50%' },
        { month: 9, title: ' 2 ', desc: ' 50%' },
        { month: 11, title: ' ', desc: '12/1~12/15 ' },
        { month: 12, title: ' ', desc: '12/1~12/15' }
    ];

    const currentMonth = new Date().getMonth() + 1;

    return (
        <div className="bg-white p-6 rounded-[2rem] shadow-sm border border-slate-100 mt-2">
            <h4 className="text-sm font-bold text-slate-800 mb-4"> {new Date().getFullYear()}  </h4>
            <div className="space-y-3">
                {taxEvents.map((event, idx) => {
                    const isPast = event.month < currentMonth;
                    const isCurrent = event.month === currentMonth;
                    return (
                        <div key={idx} className={`p-3 rounded-xl flex items-center gap-3 ${isCurrent ? 'bg-blue-50 border-2 border-blue-200' : isPast ? 'bg-slate-50 opacity-50' : 'bg-slate-50'}`}>
                            <div className={`w-10 h-10 rounded-xl flex items-center justify-center font-black text-sm ${isCurrent ? 'bg-blue-500 text-white' : 'bg-slate-200 text-slate-600'}`}>{event.month}</div>
                            <div>
                                <p className="font-bold text-sm">{event.title}</p>
                                <p className="text-xs text-slate-500">{event.desc}</p>
                            </div>
                            {isCurrent && <span className="ml-auto text-xs font-bold text-blue-600 bg-blue-100 px-2 py-1 rounded"></span>}
                        </div>
                    );
                })}
            </div>
        </div>
    );
};
