import React, { useState, useMemo } from 'react';
import { parseNumber, formatNumber, formatCompactNumber } from '../../utils/math.js';
import { ClipboardList } from '../ui/Icons.js';
import { NumberInput } from '../ui/Inputs.js';

// NEW:  
export const AmortizationScheduleTab = () => {
    const [loanAmount, setLoanAmount] = useState('');
    const [rate, setRate] = useState('4.5');
    const [period, setPeriod] = useState('360');
    const [method, setMethod] = useState('equal'); // equal: , principal: 

    const schedule = useMemo(() => {
        const principal = parseNumber(loanAmount);
        const monthlyRate = parseFloat(rate) / 100 / 12;
        const n = parseInt(period);
        if (!principal || !n || monthlyRate <= 0) return null;

        const items = [];
        let remaining = principal;

        if (method === 'equal') {
            // 
            const monthlyPayment = (principal * monthlyRate * Math.pow(1 + monthlyRate, n)) / (Math.pow(1 + monthlyRate, n) - 1);
            for (let i = 1; i <= Math.min(n, 12); i++) {
                const interest = remaining * monthlyRate;
                const principalPay = monthlyPayment - interest;
                remaining -= principalPay;
                items.push({ month: i, payment: Math.floor(monthlyPayment), principal: Math.floor(principalPay), interest: Math.floor(interest), remaining: Math.floor(Math.max(remaining, 0)) });
            }
        } else {
            // 
            const monthlyPrincipal = principal / n;
            for (let i = 1; i <= Math.min(n, 12); i++) {
                const interest = remaining * monthlyRate;
                const payment = monthlyPrincipal + interest;
                remaining -= monthlyPrincipal;
                items.push({ month: i, payment: Math.floor(payment), principal: Math.floor(monthlyPrincipal), interest: Math.floor(interest), remaining: Math.floor(Math.max(remaining, 0)) });
            }
        }

        return items;
    }, [loanAmount, rate, period, method]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <ClipboardList className="w-5 h-5 mr-3 text-purple-600" />  
                </h3>
                <div className="space-y-5">
                    <NumberInput label="" value={loanAmount} onChange={setLoanAmount} color="violet" />
                    <div className="flex gap-3">
                        <div className="flex-1">
                            <label className="text-xs font-bold text-slate-500 uppercase block mb-2 ml-1"> (%)</label>
                            <input type="number" value={rate} onChange={(e) => setRate(e.target.value)} className="w-full px-4 py-3 text-right bg-slate-50 rounded-2xl font-bold outline-none" />
                        </div>
                        <div className="flex-1">
                            <label className="text-xs font-bold text-slate-500 uppercase block mb-2 ml-1"></label>
                            <select value={period} onChange={(e) => setPeriod(e.target.value)} className="w-full px-4 py-3 bg-slate-50 rounded-2xl font-bold outline-none">
                                <option value="120">10</option><option value="240">20</option><option value="360">30</option>
                            </select>
                        </div>
                    </div>
                    <div className="flex gap-2">
                        <button onClick={() => setMethod('equal')} className={`flex-1 py-3 rounded-xl text-sm font-bold transition-all ${method === 'equal' ? 'bg-purple-500 text-white' : 'bg-slate-100 text-slate-500'}`}></button>
                        <button onClick={() => setMethod('principal')} className={`flex-1 py-3 rounded-xl text-sm font-bold transition-all ${method === 'principal' ? 'bg-purple-500 text-white' : 'bg-slate-100 text-slate-500'}`}></button>
                    </div>
                </div>
            </div>

            {schedule && schedule.length > 0 ? (
                <div className="bg-white rounded-2xl shadow-sm border border-slate-100 overflow-hidden">
                    <div className="overflow-x-auto">
                        <table className="w-full text-xs">
                            <thead className="bg-slate-50">
                                <tr>
                                    <th className="py-3 px-2 text-left font-bold text-slate-500"></th>
                                    <th className="py-3 px-2 text-right font-bold text-slate-500"></th>
                                    <th className="py-3 px-2 text-right font-bold text-slate-500"></th>
                                    <th className="py-3 px-2 text-right font-bold text-slate-500"></th>
                                    <th className="py-3 px-2 text-right font-bold text-slate-500"></th>
                                </tr>
                            </thead>
                            <tbody>
                                {schedule.map((row) => (
                                    <tr key={row.month} className="border-t border-slate-100">
                                        <td className="py-2.5 px-2 font-medium">{row.month}</td>
                                        <td className="py-2.5 px-2 text-right font-bold">{formatNumber(row.payment)}</td>
                                        <td className="py-2.5 px-2 text-right text-blue-600">{formatNumber(row.principal)}</td>
                                        <td className="py-2.5 px-2 text-right text-red-500">{formatNumber(row.interest)}</td>
                                        <td className="py-2.5 px-2 text-right text-slate-400">{formatCompactNumber(row.remaining)}</td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                    <p className="text-xs text-center text-slate-400 py-3">*  12  </p>
                </div>
            ) : (
                <div className="text-center py-20 text-slate-300 bg-white rounded-[2rem] border-2 border-dashed border-slate-200"> </div>
            )}
        </div>
    );
};
