
import React, { useState, useEffect, useRef } from 'react';
import { PieChart } from 'lucide-react';
import Chart from 'chart.js/auto';
import { parseNumber, formatNumber, formatCompactNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const LoanRepaymentChart = () => {
    const chartRef = useRef(null);
    const chartInstanceRef = useRef(null);
    const [loanAmount, setLoanAmount] = useState('');
    const [rate, setRate] = useState('4.5');
    const [period, setPeriod] = useState('360');
    const [viewYears, setViewYears] = useState('5');

    useEffect(() => {
        if (!chartRef.current || !window.Chart) return;
        if (chartInstanceRef.current) chartInstanceRef.current.destroy();

        const amount = parseNumber(loanAmount);
        const r = (parseFloat(rate) || 0) / 100 / 12;
        const months = parseInt(period) || 360;
        const years = parseInt(viewYears) || 5;
        if (amount <= 0 || r <= 0) return;

        const factor = Math.pow(1 + r, months);
        const monthly = (amount * r * factor) / (factor - 1);

        let balance = amount;
        const data = [];
        const maxMonths = Math.min(months, years * 12);

        for (let i = 1; i <= maxMonths; i++) {
            const interest = balance * r;
            const principal = monthly - interest;
            balance = Math.max(0, balance - principal);
            if (i % 12 === 0 || i === maxMonths) {
                data.push({ year: Math.ceil(i / 12), principal: amount - balance, interest: (monthly * i) - (amount - balance), balance });
            }
        }

        const ctx = chartRef.current.getContext('2d');
        chartInstanceRef.current = new Chart(ctx, {
            type: 'line',
            data: {
                labels: data.map(d => `${d.year}`),
                datasets: [{
                    label: ' ',
                    data: data.map(d => d.principal),
                    borderColor: '#6366f1',
                    backgroundColor: 'rgba(99,102,241,0.1)',
                    fill: true,
                    tension: 0.3,
                }, {
                    label: ' ',
                    data: data.map(d => d.interest),
                    borderColor: '#ec4899',
                    backgroundColor: 'rgba(236,72,153,0.1)',
                    fill: true,
                    tension: 0.3,
                }]
            },
            options: {
                responsive: true,
                maintainAspectRatio: true,
                plugins: { legend: { position: 'bottom', labels: { font: { size: 11, weight: 'bold' } } } },
                scales: { y: { beginAtZero: true, ticks: { callback: v => formatCompactNumber(v) } } }
            }
        });

        return () => { if (chartInstanceRef.current) chartInstanceRef.current.destroy(); };
    }, [loanAmount, rate, period, viewYears]);

    return (
        <div className="bg-white p-6 rounded-[2rem] shadow-sm border border-slate-100 card-depth mt-2">
            <h4 className="text-sm font-bold text-slate-800 mb-4 flex items-center">
                <PieChart className="w-4 h-4 mr-2 text-pink-500" />   
            </h4>
            <div className="space-y-4">
                <NumberInput label=" " value={loanAmount} onChange={setLoanAmount} color="pink" />
                <div className="grid grid-cols-3 gap-2">
                    <div><label className="text-[10px] text-slate-500">(%)</label><input type="number" value={rate} onChange={e => setRate(e.target.value)} className="w-full px-3 py-2 bg-slate-50 rounded-lg text-sm font-bold text-right outline-none" /></div>
                    <div><label className="text-[10px] text-slate-500">()</label><input type="number" value={period} onChange={e => setPeriod(e.target.value)} className="w-full px-3 py-2 bg-slate-50 rounded-lg text-sm font-bold text-right outline-none" /></div>
                    <div><label className="text-[10px] text-slate-500">()</label>
                        <select value={viewYears} onChange={e => setViewYears(e.target.value)} className="w-full px-2 py-2 bg-slate-50 rounded-lg text-sm font-bold outline-none">
                            <option value="1">1</option><option value="5">5</option><option value="10">10</option><option value="30"></option>
                        </select>
                    </div>
                </div>
            </div>
            <div className="mt-4">
                <canvas ref={chartRef} height="200"></canvas>
            </div>
            {parseNumber(loanAmount) > 0 && (
                <div className="mt-4 grid grid-cols-2 gap-2 text-xs text-center">
                    <div className="p-3 bg-indigo-50 rounded-xl">
                        <p className="text-slate-500"> </p>
                        <p className="font-bold text-indigo-600">{(() => {
                            const r = (parseFloat(rate) || 0) / 100 / 12;
                            const n = parseInt(period) || 360;
                            const p = parseNumber(loanAmount);
                            if (r <= 0 || p <= 0) return '0';
                            const f = Math.pow(1 + r, n);
                            return formatNumber(Math.round((p * r * f) / (f - 1)));
                        })()}</p>
                    </div>
                    <div className="p-3 bg-pink-50 rounded-xl">
                        <p className="text-slate-500"> </p>
                        <p className="font-bold text-pink-600">{(() => {
                            const r = (parseFloat(rate) || 0) / 100 / 12;
                            const n = parseInt(period) || 360;
                            const p = parseNumber(loanAmount);
                            if (r <= 0 || p <= 0) return '0';
                            const f = Math.pow(1 + r, n);
                            const m = (p * r * f) / (f - 1);
                            return formatCompactNumber(Math.round(m * n - p));
                        })()}</p>
                    </div>
                </div>
            )}
        </div>
    );
};
