
import React, { useState, useMemo } from 'react';
import { Building } from 'lucide-react';
import { parseNumber, formatCompactNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const RentalBusinessCompare = () => {
    const [annualRent, setAnnualRent] = useState('');
    const [propertyValue, setPropertyValue] = useState('');

    const result = useMemo(() => {
        const rent = parseNumber(annualRent);
        const value = parseNumber(propertyValue);
        if (!rent || !value) return null;

        //  
        const normalIncomeTax = rent * 0.154; //   15.4% ()
        const normalPropertyTax = value * 0.001; //  0.1% 

        //   ()
        const regIncomeTax = rent * 0.75 * 0.154; //  25% 
        const regPropertyTax = value * 0.0005; //  50% 

        const normalTotal = normalIncomeTax + normalPropertyTax;
        const regTotal = regIncomeTax + regPropertyTax;
        const savings = normalTotal - regTotal;

        return { normalIncomeTax, normalPropertyTax, normalTotal, regIncomeTax, regPropertyTax, regTotal, savings };
    }, [annualRent, propertyValue]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <Building className="w-5 h-5 mr-3 text-cyan-600" />  
                </h3>
                <div className="space-y-4">
                    <NumberInput label=" " value={annualRent} onChange={setAnnualRent} color="cyan" />
                    <NumberInput label=" " value={propertyValue} onChange={setPropertyValue} color="slate" />
                </div>
            </div>
            {result && (
                <div className="space-y-4">
                    <div className="grid grid-cols-2 gap-3">
                        <div className="bg-slate-50 p-4 rounded-2xl border border-slate-200">
                            <p className="text-xs font-bold text-slate-500 mb-2"> </p>
                            <p className="text-lg font-black text-slate-700">{formatCompactNumber(result.normalTotal)}</p>
                        </div>
                        <div className="bg-cyan-50 p-4 rounded-2xl border border-cyan-200">
                            <p className="text-xs font-bold text-cyan-600 mb-2"> </p>
                            <p className="text-lg font-black text-cyan-700">{formatCompactNumber(result.regTotal)}</p>
                        </div>
                    </div>
                    <div className="bg-green-50 p-5 rounded-2xl border border-green-200 text-center">
                        <p className="text-xs font-bold text-green-600 mb-1">  </p>
                        <p className="text-2xl font-black text-green-700">-{formatCompactNumber(result.savings)}</p>
                    </div>
                </div>
            )}
        </div>
    );
};
