
import React, { useState, useMemo } from 'react';
import { Users } from 'lucide-react';
import { parseNumber, formatCompactNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const JointOwnershipAnalyzer = () => {
    const [propertyPrice, setPropertyPrice] = useState('');
    const [capitalGain, setCapitalGain] = useState('');

    const result = useMemo(() => {
        const price = parseNumber(propertyPrice);
        const gain = parseNumber(capitalGain);

        if (!price || !gain) return null;

        //   (1 )
        const singleAcqTax = price * 0.01; //  1%
        const jointAcqTax = price * 0.01; //  1% ()

        //   ()
        const getTaxAmount = (taxableGain) => {
            if (taxableGain <= 14000000) return taxableGain * 0.06;
            if (taxableGain <= 50000000) return taxableGain * 0.15 - 1260000;
            if (taxableGain <= 88000000) return taxableGain * 0.24 - 5760000;
            if (taxableGain <= 150000000) return taxableGain * 0.35 - 15440000;
            return taxableGain * 0.38 - 19940000;
        };

        // :   
        const singleTax = getTaxAmount(gain - 2500000);

        //  (50:50):   
        const halfGain = (gain / 2) - 2500000;
        const jointTax = getTaxAmount(halfGain) * 2;

        const savings = singleTax - jointTax;

        return {
            price, gain,
            singleAcqTax, jointAcqTax,
            singleTax: Math.round(singleTax),
            jointTax: Math.round(jointTax),
            savings: Math.round(savings),
            isJointBetter: savings > 0
        };
    }, [propertyPrice, capitalGain]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <Users className="w-5 h-5 mr-3 text-pink-600" />   
                </h3>
                <div className="space-y-4">
                    <NumberInput label="" value={propertyPrice} onChange={setPropertyPrice} color="blue" />
                    <NumberInput label=" " value={capitalGain} onChange={setCapitalGain} color="green" />
                </div>
            </div>

            {result && (
                <div className="space-y-4">
                    {/*   */}
                    <div className="grid grid-cols-2 gap-3">
                        <div className={`p-5 rounded-2xl ${!result.isJointBetter ? 'bg-pink-50 border-2 border-pink-200' : 'bg-slate-50 border border-slate-100'}`}>
                            <p className="text-xs font-bold text-slate-500 mb-2"> </p>
                            <p className="text-xl font-black text-slate-800">{formatCompactNumber(result.singleTax)}</p>
                        </div>
                        <div className={`p-5 rounded-2xl ${result.isJointBetter ? 'bg-pink-50 border-2 border-pink-200' : 'bg-slate-50 border border-slate-100'}`}>
                            <p className="text-xs font-bold text-slate-500 mb-2">  (50:50)</p>
                            <p className="text-xl font-black text-slate-800">{formatCompactNumber(result.jointTax)}</p>
                        </div>
                    </div>

                    {/*   */}
                    <div className={`p-6 rounded-[2rem] text-center ${result.savings > 0 ? 'bg-green-50 border border-green-200' : 'bg-slate-50'}`}>
                        <p className="text-xs font-bold text-slate-500 mb-1">
                            {result.savings > 0 ? '  ' : ' '}
                        </p>
                        <p className={`text-3xl font-black ${result.savings > 0 ? 'text-green-600' : 'text-slate-600'}`}>
                            {result.savings > 0 ? '-' : ''}{formatCompactNumber(Math.abs(result.savings))}
                        </p>
                    </div>

                    <div className="p-4 bg-indigo-50 rounded-2xl text-xs text-indigo-600">
                                .
                    </div>
                </div>
            )}
        </div>
    );
};
