import React, { useState, useEffect, useRef } from 'react';
import { getHistory } from '../../utils/storage.js';
import { copyToClipboard, generateCalcShareUrl } from '../../utils/url.js';
import { formatNumber, formatCompactNumber, formatDate as formatDateFn } from '../../utils/formatters.js';
import { Share2, History, Info, Copy, Check, HelpCircle, PieChart, FileDown, Link2 } from './Icons.js';

// Copy Button Component with proper timer cleanup
export const CopyButton = ({ text, showToast, className = '' }) => {
    const [copied, setCopied] = useState(false);
    const timerRef = useRef(null);

    // Cleanup timer on unmount to prevent memory leaks
    useEffect(() => {
        return () => {
            if (timerRef.current) {
                clearTimeout(timerRef.current);
            }
        };
    }, []);

    const handleCopy = async () => {
        const success = await copyToClipboard(text, showToast);
        if (success) {
            setCopied(true);
            // Clear existing timer before setting new one
            if (timerRef.current) {
                clearTimeout(timerRef.current);
            }
            timerRef.current = setTimeout(() => setCopied(false), 2000);
        }
    };

    return (
        <button
            onClick={handleCopy}
            className={`p-2 rounded-xl transition-all active:scale-95 ${copied
                ? 'bg-green-100 text-green-600'
                : 'bg-white/60 text-slate-500 hover:bg-white'} ${className}`}
            title=" "
        >
            {copied ? <Check size={16} /> : <Copy size={16} />}
        </button>
    );
};

// Tooltip Component for help information
export const Tooltip = ({ children, content }) => {
    const [show, setShow] = useState(false);
    return (
        <div className="relative inline-flex items-center">
            <button
                type="button"
                onMouseEnter={() => setShow(true)}
                onMouseLeave={() => setShow(false)}
                onClick={() => setShow(!show)}
                className="p-1 text-slate-400 hover:text-slate-600:text-slate-300 transition-colors"
                aria-label=""
            >
                <HelpCircle size={14} />
            </button>
            {show && (
                <div className="absolute bottom-full left-1/2 -translate-x-1/2 mb-2 px-3 py-2 bg-slate-800 text-white text-xs rounded-lg shadow-lg z-50 w-48 animate-fade-in">
                    {content}
                    <div className="absolute top-full left-1/2 -translate-x-1/2 border-4 border-transparent border-t-slate-800"></div>
                </div>
            )}
        </div>
    );
};

// Share Button Component
export const ShareButton = ({ calcType, params, showToast }) => {
    const handleShare = async () => {
        const url = generateCalcShareUrl(calcType, params);

        if (navigator.share) {
            try {
                await navigator.share({
                    title: '  Pro  ',
                    text: `${calcType}   !`,
                    url: url
                });
            } catch (err) {
                if (err.name !== 'AbortError') {
                    await copyToClipboard(url, showToast);
                }
            }
        } else {
            await copyToClipboard(url, showToast);
            if (showToast) showToast('  !');
        }
    };

    return (
        <button
            onClick={handleShare}
            className="p-2 text-slate-400 hover:text-blue-500 hover:bg-blue-50 rounded-xl transition-all active:scale-95"
            title=" "
            aria-label="  "
        >
            <Share2 size={18} />
        </button>
    );
};

// Quick History Component (   )
export const QuickHistory = ({ onSelect }) => {
    const [history, setHistory] = useState([]);
    const [isOpen, setIsOpen] = useState(false);

    useEffect(() => {
        setHistory(getHistory().slice(0, 5)); //  5
    }, [isOpen]);

    if (history.length === 0) return null;

    const typeLabels = {
        cost: '',
        safety: '',
        fee: '',
        loan: '',
        buying: '',
        dsr: 'DSR',
        property: ''
    };

    return (
        <div className="relative">
            <button
                onClick={() => setIsOpen(!isOpen)}
                className="p-2 text-slate-400 hover:text-slate-600 hover:bg-slate-100 rounded-xl transition-all"
                title=" "
                aria-label="  "
            >
                <History size={18} />
            </button>
            {isOpen && (
                <>
                    <div className="fixed inset-0 z-40" onClick={() => setIsOpen(false)} />
                    <div className="absolute right-0 top-full mt-2 w-64 bg-white rounded-2xl shadow-xl border border-slate-100 z-50 animate-fade-in overflow-hidden">
                        <div className="p-3 border-b border-slate-100">
                            <p className="text-xs font-bold text-slate-400 uppercase"> </p>
                        </div>
                        <div className="max-h-60 overflow-y-auto">
                            {history.map((item, idx) => (
                                <button
                                    key={idx}
                                    onClick={() => {
                                        if (onSelect) onSelect(item);
                                        setIsOpen(false);
                                    }}
                                    className="w-full px-4 py-3 text-left hover:bg-slate-50:bg-slate-700 transition-colors border-b border-slate-50 last:border-0"
                                >
                                    <div className="flex justify-between items-center">
                                        <span className="text-xs font-bold text-blue-500">{typeLabels[item.type] || item.type}</span>
                                        <span className="text-[10px] text-slate-400">{formatDateFn(item.timestamp)}</span>
                                    </div>
                                    <p className="text-sm font-bold text-slate-700 mt-1 truncate">
                                        {item.result || ' '}
                                    </p>
                                </button>
                            ))}
                        </div>
                    </div>
                </>
            )}
        </div>
    );
};

