// Error Boundary Component
export class ErrorBoundary extends React.Component {
    constructor(props) {
        super(props);
        this.state = { hasError: false, error: null, errorInfo: null };
    }

    static getDerivedStateFromError(error) {
        return { hasError: true, error };
    }

    componentDidCatch(error, errorInfo) {
        console.error('ErrorBoundary caught an error:', error, errorInfo);
        this.setState({ errorInfo });

        //  localStorage  ()
        try {
            const errorLog = JSON.parse(localStorage.getItem('errorLog') || '[]');
            errorLog.unshift({
                timestamp: new Date().toISOString(),
                message: error.message,
                stack: error.stack?.substring(0, 500)
            });
            localStorage.setItem('errorLog', JSON.stringify(errorLog.slice(0, 10)));
        } catch (e) {
            // localStorage   
        }
    }

    handleReset = () => {
        this.setState({ hasError: false, error: null, errorInfo: null });
    };

    handleClearCache = () => {
        try {
            localStorage.clear();
            window.location.reload();
        } catch (e) {
            window.location.reload();
        }
    };

    render() {
        if (this.state.hasError) {
            return (
                <div className="p-8 m-4 bg-red-50 border border-red-200 rounded-3xl text-center">
                    <div className="text-4xl mb-4"></div>
                    <h2 className="text-lg font-bold text-red-800 mb-2"> </h2>
                    <p className="text-sm text-red-600 mb-4">
                        {this.state.error?.message || '    .'}
                    </p>
                    <div className="flex flex-col gap-2 justify-center items-center">
                        <button
                            onClick={this.handleReset}
                            className="px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-xl font-bold text-sm transition-colors"
                            aria-label=" "
                        >
                             
                        </button>
                        <button
                            onClick={() => window.location.reload()}
                            className="px-4 py-2 bg-red-600 hover:bg-red-700 text-white rounded-xl font-bold text-sm transition-colors"
                            aria-label=""
                        >
                            
                        </button>
                        <button
                            onClick={this.handleClearCache}
                            className="px-4 py-2 bg-slate-600 hover:bg-slate-700 text-white rounded-xl font-bold text-sm transition-colors"
                            aria-label=" "
                        >
                               
                        </button>
                    </div>
                </div>
            );
        }
        return this.props.children;
    }
}

