// Kakao Share Button Component with improved error handling
export const KakaoShareButton = ({ title, description, buttonText = ' ', showToast }) => {
    const [isInitialized, setIsInitialized] = useState(false);
    const [shareMode, setShareMode] = useState('auto'); // 'auto', 'kakao', 'clipboard'

    useEffect(() => {
        // Check Kakao SDK availability
        if (window.Kakao) {
            if (!window.Kakao.isInitialized()) {
                // Check for environment-provided key or localStorage config
                const appKey = window.KAKAO_APP_KEY || localStorage.getItem('kakaoAppKey');
                if (appKey && appKey !== 'YOUR_KAKAO_APP_KEY') {
                    try {
                        window.Kakao.init(appKey);
                        setIsInitialized(true);
                        setShareMode('kakao');
                    } catch (e) {
                        setShareMode('clipboard');
                    }
                } else {
                    // No valid key - use clipboard fallback
                    setShareMode('clipboard');
                }
            } else {
                setIsInitialized(true);
                setShareMode('kakao');
            }
        } else {
            setShareMode('clipboard');
        }
    }, []);

    const handleKakaoShare = async () => {
        const shareText = `${title || '  Pro  '}\n${description || ''}\n${window.location.href}`;

        // Try Kakao Share first if available
        if (shareMode === 'kakao' && window.Kakao?.Share) {
            try {
                await window.Kakao.Share.sendDefault({
                    objectType: 'feed',
                    content: {
                        title: title || '  Pro  ',
                        description: description || '   !',
                        imageUrl: 'https://via.placeholder.com/300x200?text=Pro',
                        link: {
                            webUrl: window.location.href,
                            mobileWebUrl: window.location.href
                        }
                    },
                    buttons: [{
                        title: buttonText,
                        link: {
                            webUrl: window.location.href,
                            mobileWebUrl: window.location.href
                        }
                    }]
                });
                return;
            } catch (e) {
                // Fall through to clipboard
            }
        }

        // Fallback: Copy to clipboard with toast notification
        try {
            await navigator.clipboard.writeText(shareText);
            if (showToast) {
                showToast('   !   .');
            } else {
                alert('   !\n  .');
            }
        } catch (e) {
            // Last resort fallback
            const textArea = document.createElement('textarea');
            textArea.value = shareText;
            document.body.appendChild(textArea);
            textArea.select();
            document.execCommand('copy');
            document.body.removeChild(textArea);
            if (showToast) {
                showToast('  !');
            }
        }
    };

    return (
        <button
            onClick={handleKakaoShare}
            className="flex items-center gap-2 px-4 py-2.5 bg-[#FEE500] hover:bg-[#FDD835] text-[#3C1E1E] rounded-xl font-bold text-sm transition-all active:scale-95 shadow-sm"
            title={shareMode === 'kakao' ? ' ' : '   '}
        >
            <svg viewBox="0 0 24 24" width="18" height="18" fill="currentColor">
                <path d="M12 3C6.48 3 2 6.48 2 10.5c0 2.53 1.67 4.76 4.17 6.07l-.87 3.25c-.08.3.23.55.5.39l3.85-2.56c.78.1 1.58.15 2.35.15 5.52 0 10-3.48 10-7.8S17.52 3 12 3z" />
            </svg>
            {shareMode === 'kakao' ? ' ' : ''}
        </button>
    );
};

// Loan Pie Chart Component using Chart.js
export const LoanPieChart = ({ principal, interest, title = " vs " }) => {
    const chartRef = useRef(null);
    const chartInstanceRef = useRef(null);
    const [chartLoaded, setChartLoaded] = useState(!!window.Chart);

    useEffect(() => {
        // Chart.js   
        if (!window.Chart) {
            setChartLoaded(false);
            return;
        }
        setChartLoaded(true);
        if (!chartRef.current) return;

        // Destroy existing chart
        if (chartInstanceRef.current) {
            chartInstanceRef.current.destroy();
        }

        const ctx = chartRef.current.getContext('2d');

        chartInstanceRef.current = new window.Chart(ctx, {
            type: 'doughnut',
            data: {
                labels: ['', ''],
                datasets: [{
                    data: [principal, interest],
                    backgroundColor: [
                        '#6366f1',
                        '#ec4899'
                    ],
                    borderColor: '#ffffff',
                    borderWidth: 3,
                    hoverOffset: 8
                }]
            },
            options: {
                responsive: true,
                maintainAspectRatio: true,
                cutout: '60%',
                plugins: {
                    legend: {
                        position: 'bottom',
                        labels: {
                            color: '#475569',
                            font: { weight: 'bold', size: 12 },
                            padding: 16
                        }
                    },
                    tooltip: {
                        callbacks: {
                            label: function (context) {
                                const value = context.parsed;
                                const total = context.dataset.data.reduce((a, b) => a + b, 0);
                                const percentage = ((value / total) * 100).toFixed(1);
                                return `${context.label}: ${formatNumber(value)} (${percentage}%)`;
                            }
                        }
                    }
                }
            }
        });

        return () => {
            if (chartInstanceRef.current) {
                chartInstanceRef.current.destroy();
            }
        };
    }, [principal, interest]);

    const total = principal + interest;
    const interestRatio = total > 0 ? ((interest / total) * 100).toFixed(1) : 0;

    // Chart.js    UI
    if (!chartLoaded) {
        return (
            <div className="bg-white p-5 rounded-2xl shadow-sm border border-slate-100">
                <h4 className="text-sm font-bold text-slate-700 text-center mb-4 flex items-center justify-center gap-2">
                    <PieChart size={16} className="text-indigo-500" />
                    {title}
                </h4>
                <div className="bg-slate-50 rounded-xl p-6 text-center">
                    <p className="text-sm text-slate-500 mb-3">   </p>
                    <div className="grid grid-cols-2 gap-3 text-xs">
                        <div className="p-3 bg-indigo-100 rounded-lg">
                            <span className="text-slate-500 block"></span>
                            <span className="font-bold text-indigo-700 text-lg">{formatCompactNumber(principal)}</span>
                        </div>
                        <div className="p-3 bg-pink-100 rounded-lg">
                            <span className="text-slate-500 block"></span>
                            <span className="font-bold text-pink-700 text-lg">{formatCompactNumber(interest)}</span>
                        </div>
                    </div>
                    <p className="text-xs text-slate-400 mt-3"> : {interestRatio}%</p>
                </div>
            </div>
        );
    }

    return (
        <div className="bg-white p-5 rounded-2xl shadow-sm border border-slate-100">
            <h4 className="text-sm font-bold text-slate-700 text-center mb-4 flex items-center justify-center gap-2">
                <PieChart size={16} className="text-indigo-500" />
                {title}
            </h4>
            <div className="relative w-48 h-48 mx-auto">
                <canvas ref={chartRef}></canvas>
                <div className="absolute inset-0 flex flex-col items-center justify-center pointer-events-none">
                    <span className="text-xs text-slate-400"> </span>
                    <span className="text-2xl font-black text-pink-500">{interestRatio}%</span>
                </div>
            </div>
            <div className="mt-4 grid grid-cols-2 gap-3 text-center text-xs">
                <div className="p-2 bg-indigo-50 rounded-lg">
                    <span className="text-slate-500 block"></span>
                    <span className="font-bold text-indigo-600">{formatCompactNumber(principal)}</span>
                </div>
                <div className="p-2 bg-pink-50 rounded-lg">
                    <span className="text-slate-500 block"></span>
                    <span className="font-bold text-pink-600">{formatCompactNumber(interest)}</span>
                </div>
            </div>
        </div>
    );
};

// Excel Export Button Component
export const ExcelExportButton = ({ data, filename = '_' }) => {
    const handleExport = () => {
        if (!window.XLSX) {
            alert('Excel   .    .');
            return;
        }

        try {
            // Get history data if no data provided
            const exportData = data || getHistory();

            if (!exportData || exportData.length === 0) {
                alert('  .');
                return;
            }

            // Format data for Excel
            const formattedData = exportData.map((item, idx) => ({
                '': idx + 1,
                '': item.type || '-',
                '': item.result || '-',
                '': item.timestamp ? new Date(item.timestamp).toLocaleString('ko-KR') : '-',
                '': JSON.stringify(item.data || {})
            }));

            // Create workbook and worksheet
            const ws = window.XLSX.utils.json_to_sheet(formattedData);
            const wb = window.XLSX.utils.book_new();
            window.XLSX.utils.book_append_sheet(wb, ws, ' ');

            // Set column widths
            ws['!cols'] = [
                { wch: 5 },
                { wch: 12 },
                { wch: 25 },
                { wch: 20 },
                { wch: 50 }
            ];

            // Download
            const date = new Date().toISOString().split('T')[0];
            window.XLSX.writeFile(wb, `${filename}_${date}.xlsx`);
        } catch (err) {
            console.error('Excel export error:', err);
            alert('Excel     .');
        }
    };

    return (
        <button
            onClick={handleExport}
            className="flex items-center gap-2 px-4 py-3 bg-green-600 hover:bg-green-700 text-white rounded-xl font-bold text-sm transition-all active:scale-95 shadow-lg shadow-green-200 w-full justify-center"
        >
            <FileDown size={18} />
            Excel 
        </button>
    );
};

