
import React, { useState, useMemo } from 'react';
import { Building } from 'lucide-react';
import { parseNumber, formatCompactNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const MultiPropertyTaxTab = () => {
    const [properties, setProperties] = useState([{ price: '', location: 'non-regulated' }]);

    const addProperty = () => setProperties([...properties, { price: '', location: 'non-regulated' }]);
    const removeProperty = (idx) => setProperties(properties.filter((_, i) => i !== idx));
    const updateProperty = (idx, field, value) => {
        const updated = [...properties];
        updated[idx][field] = value;
        setProperties(updated);
    };

    const result = useMemo(() => {
        const prices = properties.map(p => parseNumber(p.price)).filter(p => p > 0);
        if (prices.length === 0) return null;

        const totalPrice = prices.reduce((a, b) => a + b, 0);
        const count = prices.length;

        //  ( : 0.1% ~ 0.4%)
        let propertyTax = 0;
        prices.forEach(p => {
            if (p <= 600000000) propertyTax += p * 0.001;
            else if (p <= 1500000000) propertyTax += p * 0.0015;
            else propertyTax += p * 0.004;
        });

        //  ( )
        let jongbuTax = 0;
        const threshold = count >= 3 ? 600000000 : count >= 2 ? 900000000 : 1100000000;
        const excess = totalPrice - threshold;
        if (excess > 0) {
            const rate = count >= 3 ? 0.05 : count >= 2 ? 0.025 : 0.01;
            jongbuTax = excess * rate;
        }

        return { propertyTax: Math.floor(propertyTax), jongbuTax: Math.floor(jongbuTax), total: Math.floor(propertyTax + jongbuTax), count, totalPrice };
    }, [properties]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <Building className="w-5 h-5 mr-3 text-purple-600" />   
                </h3>
                <div className="space-y-4">
                    {properties.map((prop, idx) => (
                        <div key={idx} className="p-4 bg-slate-50 rounded-2xl">
                            <div className="flex justify-between items-center mb-3">
                                <span className="text-sm font-bold text-purple-600"> {idx + 1}</span>
                                {properties.length > 1 && <button onClick={() => removeProperty(idx)} className="text-red-500 text-xs font-bold"></button>}
                            </div>
                            <NumberInput label="" value={prop.price} onChange={(v) => updateProperty(idx, 'price', v)} color="violet" />
                        </div>
                    ))}
                    <button onClick={addProperty} className="w-full py-3 bg-purple-100 text-purple-600 rounded-xl font-bold text-sm">+  </button>
                </div>
            </div>
            {result && (
                <div className="bg-gradient-to-br from-purple-50 to-indigo-50 p-6 rounded-[2.5rem] border border-purple-100 shadow-lg">
                    <div className="text-center mb-4">
                        <p className="text-sm font-bold text-purple-500 mb-1">{result.count}  </p>
                        <p className="text-4xl font-black text-purple-800">{formatCompactNumber(result.total)}</p>
                    </div>
                    <div className="bg-white rounded-2xl p-4 space-y-2 text-sm">
                        <div className="flex justify-between"><span className="text-slate-500"> </span><span className="font-bold">{formatCompactNumber(result.totalPrice)}</span></div>
                        <div className="flex justify-between"><span className="text-slate-500"></span><span className="font-bold">{formatCompactNumber(result.propertyTax)}</span></div>
                        <div className="flex justify-between"><span className="text-slate-500"></span><span className="font-bold">{formatCompactNumber(result.jongbuTax)}</span></div>
                    </div>
                    <p className="text-xs text-center text-purple-400 mt-4">*  ,     </p>
                </div>
            )}
        </div>
    );
};
