import React, { useState, useMemo } from 'react';
import { parseNumber, formatNumber } from '../../utils/math.js';
import { DollarSign } from '../ui/Icons.js';
import { NumberInput } from '../ui/Inputs.js';

export const BrokerFeeNegotiator = () => {
    const [price, setPrice] = useState('');
    const [dealType, setDealType] = useState('');
    const [propType, setPropType] = useState('');

    const result = useMemo(() => {
        const val = parseNumber(price);
        if (!val) return null;

        // Calculate legal max fee
        let maxRate = 0;
        let maxFee = 0;

        if (propType === '') {
            if (dealType === '') {
                if (val < 50000000) { maxRate = 0.5; maxFee = 200000; }
                else if (val < 100000000) { maxRate = 0.4; maxFee = 300000; }
                else if (val < 600000000) { maxRate = 0.3; }
                else { maxRate = 0.4; }
            } else {
                if (val < 50000000) { maxRate = 0.6; maxFee = 250000; }
                else if (val < 200000000) { maxRate = 0.5; maxFee = 800000; }
                else if (val < 900000000) { maxRate = 0.4; }
                else { maxRate = 0.5; }
            }
        } else {
            maxRate = propType === '' ? 0.5 : 0.9;
        }

        let legalMax = Math.floor(val * (maxRate / 100));
        if (maxFee > 0 && legalMax > maxFee) legalMax = maxFee;

        const negotiated90 = Math.floor(legalMax * 0.9);
        const negotiated80 = Math.floor(legalMax * 0.8);
        const negotiated70 = Math.floor(legalMax * 0.7);

        return { legalMax, negotiated90, negotiated80, negotiated70, maxRate };
    }, [price, dealType, propType]);

    return (
        <div className="bg-white p-6 rounded-[2rem] shadow-sm border border-slate-100 card-depth mt-2">
            <h4 className="text-sm font-bold text-slate-800 mb-4 flex items-center">
                <DollarSign className="w-4 h-4 mr-2 text-green-600" />   
            </h4>
            <div className="space-y-4">
                <div className="flex gap-2">
                    <button onClick={() => setDealType('')} className={`flex-1 py-2 rounded-lg text-xs font-bold ${dealType === '' ? 'bg-slate-800 text-white' : 'bg-slate-100 text-slate-500'}`}></button>
                    <button onClick={() => setDealType('')} className={`flex-1 py-2 rounded-lg text-xs font-bold ${dealType === '' ? 'bg-slate-800 text-white' : 'bg-slate-100 text-slate-500'}`}></button>
                </div>
                <NumberInput label="" value={price} onChange={setPrice} color="green" />
            </div>
            {result && (
                <div className="mt-4 space-y-3">
                    <div className="p-4 bg-red-50 rounded-xl text-center">
                        <p className="text-xs text-red-500 font-bold">  ({result.maxRate}%)</p>
                        <p className="text-2xl font-black text-red-600">{formatNumber(result.legalMax)}</p>
                    </div>
                    <div className="grid grid-cols-3 gap-2">
                        <div className="p-3 bg-green-50 rounded-xl text-center">
                            <p className="text-[10px] text-slate-500">10% </p>
                            <p className="text-sm font-bold text-green-600">{formatNumber(result.negotiated90)}</p>
                        </div>
                        <div className="p-3 bg-emerald-50 rounded-xl text-center">
                            <p className="text-[10px] text-slate-500">20% </p>
                            <p className="text-sm font-bold text-emerald-600">{formatNumber(result.negotiated80)}</p>
                        </div>
                        <div className="p-3 bg-teal-50 rounded-xl text-center">
                            <p className="text-[10px] text-slate-500">30% </p>
                            <p className="text-sm font-bold text-teal-600">{formatNumber(result.negotiated70)}</p>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};
