
import React, { useState } from 'react';
import { Bookmark, X } from 'lucide-react';
import { usePersistedState } from '../../utils/hooks';
import { getSafeLocalStorage, setSafeLocalStorage } from '../../utils/storage';

export const PresetManager = ({ onLoadPreset, showToast }) => {
    const [presets, setPresets] = usePersistedState('userPresets', []);
    const [name, setName] = useState('');

    const savePreset = () => {
        if (!name) return;
        //    
        const currentData = {
            jDeposit: getSafeLocalStorage('jDeposit'),
            jRate: getSafeLocalStorage('jRate'),
            wDeposit: getSafeLocalStorage('wDeposit'),
            wMonthly: getSafeLocalStorage('wMonthly'),
        };
        const newPreset = { id: Date.now(), name, date: new Date().toLocaleDateString(), data: currentData };
        const updated = [newPreset, ...presets.slice(0, 9)]; //  10
        setPresets(updated);
        setName('');
        if (showToast) showToast(`'${name}'  .`);
    };

    const loadPreset = (preset) => {
        if (preset.data) {
            // localStorage  
            Object.entries(preset.data).forEach(([key, value]) => {
                if (value) setSafeLocalStorage(key, value);
            });
            //   (  )
            if (onLoadPreset) onLoadPreset(preset);
            if (showToast) showToast(`'${preset.name}'  .`);
        } else {
            if (showToast) showToast('    .');
        }
    };

    const deletePreset = (id) => {
        setPresets(presets.filter(p => p.id !== id));
    };

    return (
        <div className="bg-white p-6 rounded-[2rem] shadow-sm border border-slate-100 card-depth mt-2">
            <h4 className="text-sm font-bold text-slate-800 mb-4 flex items-center">
                <Bookmark className="w-4 h-4 mr-2 text-yellow-500" /> /
            </h4>
            <div className="flex gap-2 mb-4">
                <input
                    value={name}
                    onChange={e => setName(e.target.value)}
                    placeholder="  (:   )"
                    className="flex-1 px-3 py-2 bg-slate-50 rounded-xl text-sm outline-none"
                />
                <button
                    onClick={savePreset}
                    disabled={!name}
                    className="bg-slate-800 text-white px-4 py-2 rounded-xl text-xs font-bold disabled:opacity-50"
                >
                    
                </button>
            </div>
            <div className="space-y-2">
                {presets.length === 0 ? (
                    <div className="text-center py-4 text-slate-400 text-xs">  .</div>
                ) : (
                    presets.map(p => (
                        <div key={p.id} className="flex justify-between items-center p-3 bg-slate-50 rounded-xl">
                            <div>
                                <p className="text-sm font-bold text-slate-700">{p.name}</p>
                                <p className="text-[10px] text-slate-400">{p.date}</p>
                            </div>
                            <div className="flex gap-2">
                                <button
                                    onClick={() => loadPreset(p)}
                                    className="text-xs font-bold text-blue-600 bg-blue-50 px-2 py-1 rounded hover:bg-blue-100 transition-colors"
                                >
                                    
                                </button>
                                <button onClick={() => deletePreset(p.id)} className="text-slate-300 hover:text-red-500"><X size={14} /></button>
                            </div>
                        </div>
                    ))
                )}
            </div>
        </div>
    );
};
