
import React, { useState, useMemo } from 'react';
import { Building } from 'lucide-react';
import { parseNumber, formatNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

// :    (2026.01 )
const BANK_RATES_DATA = [
    { name: '', rate: 3.85, color: 'from-yellow-50 to-amber-50', border: 'border-yellow-200' },
    { name: '', rate: 3.92, color: 'from-blue-50 to-indigo-50', border: 'border-blue-200' },
    { name: '', rate: 3.88, color: 'from-sky-50 to-cyan-50', border: 'border-sky-200' },
    { name: '', rate: 3.79, color: 'from-teal-50 to-emerald-50', border: 'border-teal-200' },
    { name: '', rate: 3.65, color: 'from-yellow-50 to-orange-50', border: 'border-yellow-300' },
    { name: '', rate: 3.72, color: 'from-indigo-50 to-purple-50', border: 'border-indigo-200' },
];

export const BankRateComparison = () => {
    const [loanAmount, setLoanAmount] = useState('');

    const results = useMemo(() => {
        const amount = parseNumber(loanAmount) || 0;
        if (amount <= 0) return null;
        return BANK_RATES_DATA.map(bank => {
            const monthlyInterest = Math.floor(amount * (bank.rate / 100) / 12);
            const yearlyInterest = Math.floor(amount * (bank.rate / 100));
            // NaN/Infinity 
            return {
                ...bank,
                monthlyInterest: isFinite(monthlyInterest) ? monthlyInterest : 0,
                yearlyInterest: isFinite(yearlyInterest) ? yearlyInterest : 0
            };
        }).sort((a, b) => a.rate - b.rate);
    }, [loanAmount]);

    return (
        <div className="bg-white p-6 rounded-[2rem] shadow-sm border border-slate-100 card-depth mt-2">
            <h4 className="text-sm font-bold text-slate-800 mb-4 flex items-center">
                <Building className="w-4 h-4 mr-2 text-blue-600" />    (2026.01 )
            </h4>
            <NumberInput label=" " value={loanAmount} onChange={setLoanAmount} color="blue" />
            {results ? (
                <div className="mt-4 space-y-2">
                    {results.map((bank, idx) => (
                        <div key={bank.name} className={`p-4 rounded-xl bg-gradient-to-r ${bank.color} ${bank.border} border flex justify-between items-center ${idx === 0 ? 'ring-2 ring-green-300' : ''}`}>
                            <div>
                                <p className="font-bold text-sm">{bank.name} {idx === 0 && <span className="text-xs text-green-600 ml-1">!</span>}</p>
                                <p className="text-xs text-slate-500"> {bank.rate}%</p>
                            </div>
                            <div className="text-right">
                                <p className="text-lg font-black text-slate-800">{formatNumber(bank.monthlyInterest)}</p>
                                <p className="text-xs text-slate-500"> </p>
                            </div>
                        </div>
                    ))}
                    <p className="text-xs text-center text-slate-400 mt-3">*  /    </p>
                </div>
            ) : (
                <div className="text-center py-8 text-slate-400 text-sm mt-4">  </div>
            )}
        </div>
    );
};
