
import React, { useState } from 'react';
import { Plus, Edit3, Trash2, Briefcase } from 'lucide-react';
import { usePersistedState } from '../../utils/hooks';
import { parseNumber, formatKoreanNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const PortfolioManager = () => {
    const [properties, setProperties] = usePersistedState('portfolio', []);
    const [isAdding, setIsAdding] = useState(false);
    const [editId, setEditId] = useState(null);
    const [form, setForm] = useState({
        name: '', type: '', price: '', deposit: '', monthlyRent: '',
        location: '', area: '', floor: '', memo: ''
    });

    const handleSubmit = () => {
        if (!form.name || !form.price) {
            alert('  ');
            return;
        }

        const newProperty = {
            ...form,
            id: editId || Date.now().toString(),
            price: parseNumber(form.price),
            deposit: parseNumber(form.deposit),
            monthlyRent: parseNumber(form.monthlyRent),
            area: parseFloat(form.area) || 0,
            createdAt: editId ? properties.find(p => p.id === editId)?.createdAt : new Date().toISOString(),
            expectedYield: calculateYield(form)
        };

        if (editId) {
            setProperties(properties.map(p => p.id === editId ? newProperty : p));
        } else {
            if (properties.length >= 10) {
                alert(' 10   ');
                return;
            }
            setProperties([...properties, newProperty]);
        }

        setForm({ name: '', type: '', price: '', deposit: '', monthlyRent: '', location: '', area: '', floor: '', memo: '' });
        setIsAdding(false);
        setEditId(null);
    };

    const calculateYield = (p) => {
        const price = parseNumber(p.price);
        const monthlyRent = parseNumber(p.monthlyRent);
        if (price <= 0 || monthlyRent <= 0) return 0;
        return ((monthlyRent * 12) / price * 100).toFixed(2);
    };

    const handleEdit = (property) => {
        setForm({
            ...property,
            price: String(property.price),
            deposit: String(property.deposit || ''),
            monthlyRent: String(property.monthlyRent || ''),
            area: String(property.area || '')
        });
        setEditId(property.id);
        setIsAdding(true);
    };

    const handleDelete = (id) => {
        if (confirm(' ?')) {
            setProperties(properties.filter(p => p.id !== id));
        }
    };

    const totalValue = properties.reduce((sum, p) => sum + (p.price || 0), 0);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            {/*  */}
            <div className="bg-gradient-to-br from-indigo-600 to-purple-700 text-white p-6 rounded-3xl shadow-lg">
                <div className="flex justify-between items-start mb-4">
                    <div>
                        <p className="text-indigo-200 text-xs font-bold uppercase tracking-wider mb-1">  </p>
                        <p className="text-3xl font-black">{formatKoreanNumber(totalValue)}</p>
                    </div>
                    <div className="text-right">
                        <p className="text-indigo-200 text-xs font-bold mb-1"> </p>
                        <p className="text-2xl font-black">{properties.length}<span className="text-lg">/10</span></p>
                    </div>
                </div>
                <button
                    onClick={() => { setIsAdding(true); setEditId(null); setForm({ name: '', type: '', price: '', deposit: '', monthlyRent: '', location: '', area: '', floor: '', memo: '' }); }}
                    className="w-full py-3 bg-white/20 hover:bg-white/30 rounded-2xl font-bold text-sm transition-all flex items-center justify-center gap-2"
                >
                    <Plus size={18} />   
                </button>
            </div>

            {/*  /  */}
            {isAdding && (
                <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                    <h4 className="text-sm font-bold text-slate-800 mb-4">{editId ? ' ' : '  '}</h4>
                    <div className="space-y-4">
                        <input placeholder=" *" value={form.name} onChange={e => setForm({ ...form, name: e.target.value })} className="w-full p-4 bg-slate-50 rounded-2xl outline-none focus:ring-2 focus:ring-indigo-500" />
                        <select value={form.type} onChange={e => setForm({ ...form, type: e.target.value })} className="w-full p-4 bg-slate-50 rounded-2xl outline-none">
                            <option></option><option></option><option></option><option></option>
                        </select>
                        <NumberInput label=" *" value={form.price} onChange={v => setForm({ ...form, price: v })} color="blue" />
                        <NumberInput label="" value={form.deposit} onChange={v => setForm({ ...form, deposit: v })} color="green" />
                        <NumberInput label="" value={form.monthlyRent} onChange={v => setForm({ ...form, monthlyRent: v })} color="orange" />
                        <input placeholder=" (:  )" value={form.location} onChange={e => setForm({ ...form, location: e.target.value })} className="w-full p-4 bg-slate-50 rounded-2xl outline-none focus:ring-2 focus:ring-indigo-500" />
                        <div className="flex gap-3">
                            <input placeholder=" ()" value={form.area} onChange={e => setForm({ ...form, area: e.target.value })} className="flex-1 p-4 bg-slate-50 rounded-2xl outline-none" />
                            <input placeholder="" value={form.floor} onChange={e => setForm({ ...form, floor: e.target.value })} className="flex-1 p-4 bg-slate-50 rounded-2xl outline-none" />
                        </div>
                        <textarea placeholder="" value={form.memo} onChange={e => setForm({ ...form, memo: e.target.value })} className="w-full p-4 bg-slate-50 rounded-2xl outline-none h-24 resize-none" />
                        <div className="flex gap-3">
                            <button onClick={() => { setIsAdding(false); setEditId(null); }} className="flex-1 py-3 bg-slate-100 hover:bg-slate-200 rounded-2xl font-bold text-slate-600 transition-all"></button>
                            <button onClick={handleSubmit} className="flex-1 py-3 bg-indigo-600 hover:bg-indigo-700 text-white rounded-2xl font-bold transition-all">{editId ? '' : ''}</button>
                        </div>
                    </div>
                </div>
            )}

            {/*   */}
            {properties.length > 0 ? (
                <div className="space-y-3">
                    {properties.map(p => (
                        <div key={p.id} className="bg-white p-5 rounded-2xl shadow-sm border border-slate-100 hover:border-indigo-200 transition-all">
                            <div className="flex justify-between items-start mb-3">
                                <div>
                                    <span className="text-xs font-bold text-indigo-600 bg-indigo-50 px-2 py-1 rounded-lg mr-2">{p.type}</span>
                                    <h5 className="text-lg font-bold text-slate-800 inline">{p.name}</h5>
                                </div>
                                <div className="flex gap-2">
                                    <button onClick={() => handleEdit(p)} className="p-2 text-slate-400 hover:text-indigo-600 hover:bg-indigo-50 rounded-lg transition-all"><Edit3 size={16} /></button>
                                    <button onClick={() => handleDelete(p.id)} className="p-2 text-slate-400 hover:text-red-600 hover:bg-red-50 rounded-lg transition-all"><Trash2 size={16} /></button>
                                </div>
                            </div>
                            <p className="text-xl font-black text-slate-800 mb-2">{formatKoreanNumber(p.price)}</p>
                            {p.location && <p className="text-sm text-slate-500 mb-1"> {p.location}</p>}
                            {p.area > 0 && <p className="text-sm text-slate-500"> {p.area} ({(p.area * 0.3025).toFixed(1)}) {p.floor && `/ ${p.floor}`}</p>}
                            {p.expectedYield > 0 && (
                                <div className="mt-3 pt-3 border-t border-slate-100 flex justify-between items-center">
                                    <span className="text-xs text-slate-400"> </span>
                                    <span className="text-sm font-bold text-emerald-600">{p.expectedYield}%</span>
                                </div>
                            )}
                        </div>
                    ))}
                </div>
            ) : !isAdding && (
                <div className="flex flex-col items-center justify-center py-20 bg-white rounded-3xl border-2 border-dashed border-slate-200">
                    <Briefcase className="w-16 h-16 mb-4 text-slate-200" />
                    <p className="text-sm font-bold text-slate-400 mb-2">  </p>
                    <p className="text-xs text-slate-300">    </p>
                </div>
            )}
        </div>
    );
};
