
import React from 'react';
import { FileDown } from 'lucide-react';

export const ExcelExportButton = () => {
    const handleExcelExport = () => {
        // Mock data export logic - in real app, this would gather data from storage or state
        try {
            // Check if XLSX is available
            if (typeof window.XLSX === 'undefined') {
                alert('Excel   .');
                return;
            }

            const history = JSON.parse(localStorage.getItem('calcHistory') || '[]');
            if (history.length === 0) {
                alert('   .');
                return;
            }

            const ws = window.XLSX.utils.json_to_sheet(history);
            const wb = window.XLSX.utils.book_new();
            window.XLSX.utils.book_append_sheet(wb, ws, "");
            window.XLSX.writeFile(wb, `__${new Date().toISOString().slice(0, 10)}.xlsx`);
        } catch (e) {
            console.error(e);
            alert('    .');
        }
    };

    return (
        <div className="bg-white p-6 rounded-[2rem] shadow-sm border border-slate-100 card-depth mt-2 text-center">
            <h4 className="text-sm font-bold text-slate-800 mb-4 flex items-center justify-center">
                <FileDown className="w-4 h-4 mr-2 text-green-600" />  
            </h4>
            <p className="text-xs text-slate-500 mb-4">    .</p>
            <button
                onClick={handleExcelExport}
                className="w-full py-4 bg-green-600 hover:bg-green-700 text-white rounded-xl font-bold shadow-lg shadow-green-200 transition-all active:scale-95"
            >
                 
            </button>
        </div>
    );
};
