import { REGEX_COMMA, KOREAN_UNITS } from './constants.js';

export const formatNumber = (num) => {
    if (!num && num !== 0) return '';
    return Math.round(num).toString().replace(REGEX_COMMA, ',');
};

//    (1 5 ) - uses cached KOREAN_UNITS
export const formatKoreanNumber = (num) => {
    if (!num && num !== 0) return '';
    const value = Math.round(parseFloat(num) || 0);
    if (value === 0) return '0';

    let result = '';
    let remaining = value;

    for (const { value: unitValue, unit } of KOREAN_UNITS) {
        if (remaining >= unitValue) {
            const count = Math.floor(remaining / unitValue);
            result += count + unit + ' ';
            remaining = remaining % unitValue;
        }
    }

    if (remaining > 0 && value < 10000) {
        result += remaining;
    }

    return result.trim() + '';
};

//    ( )
export const formatCompactNumber = (num) => {
    if (!num && num !== 0) return '';
    const value = Math.abs(parseFloat(num) || 0);
    const sign = parseFloat(num) < 0 ? '-' : '';

    if (value >= 1e16) return sign + (value / 1e16).toFixed(1) + '';
    if (value >= 1e12) return sign + (value / 1e12).toFixed(1) + '';
    if (value >= 1e8) return sign + (value / 1e8).toFixed(1) + '';
    if (value >= 1e4) return sign + (value / 1e4).toFixed(1) + '';
    return sign + formatNumber(value);
};

export const formatDate = (timestamp) => {
    const date = new Date(timestamp);
    const month = date.getMonth() + 1;
    const day = date.getDate();
    const hours = date.getHours();
    const minutes = date.getMinutes().toString().padStart(2, '0');
    return `${month}/${day} ${hours}:${minutes}`;
};

// parseNumber -    ( )
export const parseNumber = (str) => {
    if (typeof str === 'number') return str;
    if (!str) return 0;
    return parseInt(str.toString().replace(/,/g, ''), 10) || 0;
};

// safeDivide -   (0  )
export const safeDivide = (numerator, denominator, defaultValue = 0) => {
    if (!denominator || denominator === 0) return defaultValue;
    const result = numerator / denominator;
    return isFinite(result) ? result : defaultValue;
};
