
import React, { useState } from 'react';
import { ClipboardList } from 'lucide-react';
import { usePersistedState } from '../../utils/hooks';

export const RequiredDocuments = () => {
    const [dealType, setDealType] = useState('buy');
    const [role, setRole] = useState('buyer');
    const [checked, setChecked] = usePersistedState('requiredDocsChecked', {});

    const toggle = (id) => setChecked(prev => ({ ...prev, [id]: !prev[id] }));

    const documents = {
        buy: {
            buyer: [
                { id: 'b1', name: ' ()', required: true },
                { id: 'b2', name: '', required: true },
                { id: 'b3', name: '', required: true },
                { id: 'b4', name: '', required: false },
                { id: 'b5', name: '', required: true },
                { id: 'b6', name: '/  ', required: true },
                { id: 'b7', name: ' (9 )', required: false },
            ],
            seller: [
                { id: 's1', name: ' ()', required: true },
                { id: 's2', name: ' ()', required: true },
                { id: 's3', name: '', required: true },
                { id: 's4', name: '', required: true },
                { id: 's5', name: '', required: true },
                { id: 's6', name: '/', required: true },
            ],
        },
        jeonse: {
            tenant: [
                { id: 't1', name: '', required: true },
                { id: 't2', name: '', required: true },
                { id: 't3', name: '', required: false },
                { id: 't4', name: '/ ( )', required: false },
            ],
            landlord: [
                { id: 'l1', name: '', required: true },
                { id: 'l2', name: '', required: true },
                { id: 'l3', name: '', required: true },
                { id: 'l4', name: '  ', required: true },
            ],
        },
    };

    const currentDocs = documents[dealType]?.[role] || [];
    const doneCount = currentDocs.filter(d => checked[d.id]).length;

    return (
        <div className="bg-white p-6 rounded-[2rem] shadow-sm border border-slate-100 card-depth mt-2">
            <h4 className="text-sm font-bold text-slate-800 mb-4 flex items-center">
                <ClipboardList className="w-4 h-4 mr-2 text-indigo-600" />   
            </h4>
            <div className="flex gap-2 mb-3">
                <button onClick={() => { setDealType('buy'); setRole('buyer'); }} className={`flex-1 py-2 rounded-xl text-xs font-bold ${dealType === 'buy' ? 'bg-indigo-500 text-white' : 'bg-slate-100 text-slate-600'}`}></button>
                <button onClick={() => { setDealType('jeonse'); setRole('tenant'); }} className={`flex-1 py-2 rounded-xl text-xs font-bold ${dealType === 'jeonse' ? 'bg-indigo-500 text-white' : 'bg-slate-100 text-slate-600'}`}>/</button>
            </div>
            <div className="flex gap-2 mb-4">
                {dealType === 'buy' ? (
                    <>
                        <button onClick={() => setRole('buyer')} className={`flex-1 py-2 rounded-lg text-xs font-bold ${role === 'buyer' ? 'bg-slate-800 text-white' : 'bg-slate-100'}`}></button>
                        <button onClick={() => setRole('seller')} className={`flex-1 py-2 rounded-lg text-xs font-bold ${role === 'seller' ? 'bg-slate-800 text-white' : 'bg-slate-100'}`}></button>
                    </>
                ) : (
                    <>
                        <button onClick={() => setRole('tenant')} className={`flex-1 py-2 rounded-lg text-xs font-bold ${role === 'tenant' ? 'bg-slate-800 text-white' : 'bg-slate-100'}`}></button>
                        <button onClick={() => setRole('landlord')} className={`flex-1 py-2 rounded-lg text-xs font-bold ${role === 'landlord' ? 'bg-slate-800 text-white' : 'bg-slate-100'}`}></button>
                    </>
                )}
            </div>
            <div className="w-full bg-slate-200 rounded-full h-2 mb-3">
                <div className="bg-indigo-500 h-2 rounded-full transition-all" style={{ width: `${(doneCount / currentDocs.length) * 100}%` }}></div>
            </div>
            <p className="text-xs text-slate-500 text-right mb-3">{doneCount}/{currentDocs.length} </p>
            <div className="space-y-2">
                {currentDocs.map(doc => (
                    <button key={doc.id} onClick={() => toggle(doc.id)} className={`w-full p-3 rounded-xl text-left flex items-center gap-3 transition-all ${checked[doc.id] ? 'bg-green-50 text-green-700' : 'bg-slate-50 text-slate-700'}`}>
                        <span className={`w-5 h-5 rounded-full flex items-center justify-center text-xs ${checked[doc.id] ? 'bg-green-500 text-white' : 'bg-slate-200'}`}>
                            {checked[doc.id] ? '' : ''}
                        </span>
                        <span className="font-medium text-sm">{doc.name}</span>
                        {doc.required && <span className="ml-auto text-xs text-red-500 font-bold"></span>}
                    </button>
                ))}
            </div>
        </div>
    );
};
