import React, { useState, useMemo } from 'react';
import { parseNumber, formatCompactNumber } from '../../utils/math.js';
import { Banknote } from '../ui/Icons.js';
import { NumberInput } from '../ui/Inputs.js';

// NEW:   
export const JeonseLoanLimitTab = () => {
    const [deposit, setDeposit] = useState('');
    const [annualIncome, setAnnualIncome] = useState('');
    const [creditGrade, setCreditGrade] = useState('good');

    const result = useMemo(() => {
        const dep = parseNumber(deposit);
        const income = parseNumber(annualIncome);
        if (!dep) return null;

        // DSR/DTI     (  )
        //      
        let maxLoanByIncome = Infinity;
        if (income > 0) {
            // DSR 40% ,   =  * 0.4 / 12
            const maxMonthlyPayment = (income * 0.4) / 12;
            // :   ,   
            const yearlyRate = 0.04; //  4% 
            if (yearlyRate > 0) {
                //   =  *  / 12 =>  =  * 12 / 
                maxLoanByIncome = (maxMonthlyPayment * 12) / yearlyRate;
            } else {
                maxLoanByIncome = Infinity; //  
            }
        }

        //  ( 2,   )
        const buteomokBase = Math.min(dep * 0.8, 200000000);
        const buteomokLimit = income > 0 ? Math.min(buteomokBase, maxLoanByIncome) : buteomokBase;
        const buteomokRate = 2.1; //  

        //   ( 5)
        const kakaoBase = Math.min(dep * 0.8, 500000000);
        const kakaoLimit = income > 0 ? Math.min(kakaoBase, maxLoanByIncome) : kakaoBase;
        const kakaoRate = creditGrade === 'excellent' ? 3.8 : creditGrade === 'good' ? 4.2 : 4.8;

        //    ( 4)
        const bankBase = Math.min(dep * 0.8, 400000000);
        const bankLimit = income > 0 ? Math.min(bankBase, maxLoanByIncome) : bankBase;
        const bankRate = creditGrade === 'excellent' ? 4.0 : creditGrade === 'good' ? 4.5 : 5.0;

        //    
        const isIncomeLimited = income > 0 && (buteomokLimit < buteomokBase || kakaoLimit < kakaoBase || bankLimit < bankBase);

        return { buteomokLimit, buteomokRate, kakaoLimit, kakaoRate, bankLimit, bankRate, isIncomeLimited, maxLoanByIncome: income > 0 ? maxLoanByIncome : null };
    }, [deposit, annualIncome, creditGrade]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <Banknote className="w-5 h-5 mr-3 text-green-600" />  
                </h3>
                <div className="space-y-5">
                    <NumberInput label=" " value={deposit} onChange={setDeposit} color="green" />
                    <NumberInput label="  ()" value={annualIncome} onChange={setAnnualIncome} color="green" />
                    <div>
                        <label className="text-xs font-bold text-slate-500 uppercase tracking-widest block mb-2 ml-1"></label>
                        <div className="flex gap-2">
                            {[{ id: 'excellent', label: ' (1-3)' }, { id: 'good', label: ' (4-6)' }, { id: 'normal', label: ' (7-9)' }].map(g => (
                                <button key={g.id} onClick={() => setCreditGrade(g.id)} className={`flex-1 py-3 rounded-xl text-xs font-bold transition-all ${creditGrade === g.id ? 'bg-green-500 text-white' : 'bg-slate-100 text-slate-500'}`}>{g.label}</button>
                            ))}
                        </div>
                    </div>
                </div>
            </div>

            {result ? (
                <div className="space-y-4">
                    <div className="bg-gradient-to-r from-blue-50 to-sky-50 p-5 rounded-2xl border border-blue-100">
                        <div className="flex justify-between items-center">
                            <div>
                                <p className="text-xs font-bold text-blue-600 mb-1"> </p>
                                <p className="text-2xl font-black text-blue-900">{formatCompactNumber(result.buteomokLimit)}</p>
                            </div>
                            <div className="text-right">
                                <p className="text-xs text-slate-500"> </p>
                                <p className="text-lg font-bold text-blue-600">{result.buteomokRate}%</p>
                            </div>
                        </div>
                    </div>
                    <div className="bg-gradient-to-r from-yellow-50 to-amber-50 p-5 rounded-2xl border border-yellow-100">
                        <div className="flex justify-between items-center">
                            <div>
                                <p className="text-xs font-bold text-amber-600 mb-1"> </p>
                                <p className="text-2xl font-black text-amber-900">{formatCompactNumber(result.kakaoLimit)}</p>
                            </div>
                            <div className="text-right">
                                <p className="text-xs text-slate-500"> </p>
                                <p className="text-lg font-bold text-amber-600">{result.kakaoRate}%</p>
                            </div>
                        </div>
                    </div>
                    <div className="bg-gradient-to-r from-slate-50 to-gray-50 p-5 rounded-2xl border border-slate-200">
                        <div className="flex justify-between items-center">
                            <div>
                                <p className="text-xs font-bold text-slate-600 mb-1"> </p>
                                <p className="text-2xl font-black text-slate-900">{formatCompactNumber(result.bankLimit)}</p>
                            </div>
                            <div className="text-right">
                                <p className="text-xs text-slate-500"> </p>
                                <p className="text-lg font-bold text-slate-600">{result.bankRate}%</p>
                            </div>
                        </div>
                    </div>
                    <p className="text-xs text-center text-slate-400">*         </p>
                </div>
            ) : (
                <div className="text-center py-20 text-slate-300 bg-white rounded-[2rem] border-2 border-dashed border-slate-200"> </div>
            )}
        </div>
    );
};
