
import React, { useState, useMemo } from 'react';
import { TrendingUp } from 'lucide-react';
import { parseNumber, formatCompactNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const SensitivityAnalysis = () => {
    const [investmentAmount, setInvestmentAmount] = useState('50000');
    const [annualRent, setAnnualRent] = useState('2400');
    const [baseInterestRate, setBaseInterestRate] = useState('4.5');

    const result = useMemo(() => {
        const investment = parseNumber(investmentAmount);
        const rent = parseNumber(annualRent);
        const baseRate = parseFloat(baseInterestRate) || 4.5;

        if (!investment || !rent) return null;

        //    (-2% ~ +3%)
        const rateScenarios = [-2, -1, 0, 1, 2, 3].map(delta => {
            const rate = baseRate + delta;
            const loanCost = investment * 0.6 * (rate / 100); // LTV 60% 
            const netReturn = rent - loanCost;
            const yield_rate = (netReturn / (investment * 0.4)) * 100; //  
            return { rate: rate.toFixed(1), netReturn: Math.round(netReturn), yieldRate: yield_rate.toFixed(1) };
        });

        //    (0% ~ 20%)
        const vacancyScenarios = [0, 5, 10, 15, 20].map(vacancy => {
            const effectiveRent = rent * (1 - vacancy / 100);
            const yield_rate = (effectiveRent / investment) * 100;
            return { vacancy, effectiveRent: Math.round(effectiveRent), yieldRate: yield_rate.toFixed(2) };
        });

        return { rateScenarios, vacancyScenarios, baseYield: ((rent / investment) * 100).toFixed(2) };
    }, [investmentAmount, annualRent, baseInterestRate]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <TrendingUp className="w-5 h-5 mr-3 text-orange-600" />  
                </h3>
                <div className="space-y-4">
                    <NumberInput label="" value={investmentAmount} onChange={setInvestmentAmount} color="blue" />
                    <NumberInput label=" " value={annualRent} onChange={setAnnualRent} color="green" />
                    <div>
                        <label className="text-xs font-bold text-slate-500 uppercase tracking-widest block mb-2 ml-1">  (%)</label>
                        <input type="number" step="0.1" value={baseInterestRate} onChange={(e) => setBaseInterestRate(e.target.value)} className="w-full px-4 py-3 text-right bg-slate-50 rounded-2xl font-bold text-slate-700 outline-none focus:ring-2 focus:ring-orange-500" />
                    </div>
                </div>
            </div>

            {result && (
                <div className="space-y-4">
                    {/*   */}
                    <div className="bg-white p-5 rounded-2xl shadow-sm border border-slate-100">
                        <h4 className="text-sm font-bold text-slate-600 mb-4">   </h4>
                        <div className="space-y-2">
                            {result.rateScenarios.map((s, i) => (
                                <div key={i} className={`flex justify-between items-center p-3 rounded-xl ${s.rate == baseInterestRate ? 'bg-orange-50 border border-orange-200' : 'bg-slate-50'}`}>
                                    <span className="text-sm font-bold text-slate-700"> {s.rate}%</span>
                                    <div className="text-right">
                                        <span className={`text-sm font-bold ${parseFloat(s.yieldRate) >= 0 ? 'text-emerald-600' : 'text-red-500'}`}>
                                             {s.yieldRate}%
                                        </span>
                                        <span className="text-xs text-slate-400 ml-2">({formatCompactNumber(s.netReturn)})</span>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>

                    {/*   */}
                    <div className="bg-white p-5 rounded-2xl shadow-sm border border-slate-100">
                        <h4 className="text-sm font-bold text-slate-600 mb-4">   </h4>
                        <div className="space-y-2">
                            {result.vacancyScenarios.map((s, i) => (
                                <div key={i} className="flex justify-between items-center p-3 bg-slate-50 rounded-xl">
                                    <span className="text-sm font-bold text-slate-700"> {s.vacancy}%</span>
                                    <div className="text-right">
                                        <span className="text-sm font-bold text-indigo-600"> {s.yieldRate}%</span>
                                        <span className="text-xs text-slate-400 ml-2">({formatCompactNumber(s.effectiveRent)})</span>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};
