
import React, { useState } from 'react';
import { AlertTriangle } from 'lucide-react';
import { parseNumber, formatCompactNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const ContractDisputeGuide = () => {
    const [scenario, setScenario] = useState('buyer_cancel');
    const [depositAmount, setDepositAmount] = useState('');

    const scenarios = {
        buyer_cancel: { title: '  ', penalty: ' ', desc: '      ,     .' },
        seller_cancel: { title: '  ', penalty: '  ', desc: '      ,   2  .' },
        mutual: { title: ' ', penalty: ' ', desc: '    ,    .' },
        breach: { title: ' ', penalty: ' ', desc: '    ,      .' },
    };

    const current = scenarios[scenario];
    const deposit = parseNumber(depositAmount);
    const penalty = scenario === 'seller_cancel' ? deposit * 2 : deposit;

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <AlertTriangle className="w-5 h-5 mr-3 text-red-500" />   
                </h3>
                <div className="space-y-4">
                    <NumberInput label="" value={depositAmount} onChange={setDepositAmount} color="red" />
                    <div>
                        <label className="text-xs font-bold text-slate-500 block mb-2"> </label>
                        <div className="grid grid-cols-2 gap-2">
                            {Object.entries(scenarios).map(([k, v]) => (
                                <button key={k} onClick={() => setScenario(k)} className={`py-3 rounded-xl text-xs font-bold ${scenario === k ? 'bg-red-500 text-white' : 'bg-slate-100'}`}>{v.title}</button>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
            <div className="bg-red-50 p-5 rounded-2xl border border-red-200">
                <h4 className="text-sm font-bold text-red-700 mb-2">{current.title}</h4>
                <p className="text-sm text-slate-600 mb-3">{current.desc}</p>
                <div className="bg-white/70 p-3 rounded-xl">
                    <p className="text-xs text-slate-500"> </p>
                    <p className="text-xl font-black text-red-600">{deposit ? formatCompactNumber(penalty) : '-'}</p>
                </div>
            </div>
        </div>
    );
};
