
import React, { useState, useMemo } from 'react';
import { Gavel } from 'lucide-react';
import { parseNumber, formatKoreanNumber, formatCompactNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const AuctionBidCalculator = () => {
    const [appraisalPrice, setAppraisalPrice] = useState('');
    const [failCount, setFailCount] = useState('0');
    const [expectedRent, setExpectedRent] = useState('');
    const [repairCost, setRepairCost] = useState('');

    const result = useMemo(() => {
        const appraisal = parseNumber(appraisalPrice);
        const fails = parseInt(failCount) || 0;
        const rent = parseNumber(expectedRent);
        const repair = parseNumber(repairCost);

        if (!appraisal) return null;

        //     (1 20% )
        const minBidRate = Math.pow(0.8, fails);
        const minBidPrice = Math.round(appraisal * minBidRate);

        //    ( ~ +10%)
        const suggestedMin = Math.round(minBidPrice * 1.0);
        const suggestedMax = Math.round(minBidPrice * 1.1);

        //    
        const totalInvestment = ((suggestedMin + suggestedMax) / 2) + repair;
        const annualRent = rent * 12;
        const yield_rate = totalInvestment > 0 ? (annualRent / totalInvestment) * 100 : 0;

        return {
            appraisal,
            minBidRate: (minBidRate * 100).toFixed(0),
            minBidPrice,
            suggestedMin,
            suggestedMax,
            totalInvestment,
            annualRent,
            yieldRate: yield_rate.toFixed(2)
        };
    }, [appraisalPrice, failCount, expectedRent, repairCost]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <Gavel className="w-5 h-5 mr-3 text-amber-600" />   
                </h3>
                <div className="space-y-4">
                    <NumberInput label="" value={appraisalPrice} onChange={setAppraisalPrice} color="blue" />
                    <div>
                        <label className="text-xs font-bold text-slate-500 uppercase tracking-widest block mb-2 ml-1"> </label>
                        <input type="number" value={failCount} onChange={(e) => setFailCount(e.target.value)} className="w-full px-4 py-3 text-right bg-slate-50 rounded-2xl font-bold text-slate-700 outline-none focus:ring-2 focus:ring-amber-500" />
                    </div>
                    <NumberInput label="  ( )" value={expectedRent} onChange={setExpectedRent} color="green" />
                    <NumberInput label=" " value={repairCost} onChange={setRepairCost} color="orange" />
                </div>
            </div>

            {result && (
                <div className="space-y-4">
                    <div className="bg-gradient-to-br from-amber-50 to-yellow-50 p-6 rounded-[2rem] border border-amber-100">
                        <div className="text-center mb-4">
                            <p className="text-xs font-bold text-amber-600 mb-1">  ({result.minBidRate}%)</p>
                            <p className="text-3xl font-black text-amber-800">{formatKoreanNumber(result.minBidPrice)}</p>
                        </div>
                        <div className="bg-white/70 backdrop-blur p-4 rounded-2xl">
                            <p className="text-xs font-bold text-slate-500 mb-2 text-center">   </p>
                            <p className="text-lg font-black text-center text-amber-700">
                                {formatCompactNumber(result.suggestedMin)} ~ {formatCompactNumber(result.suggestedMax)}
                            </p>
                        </div>
                    </div>

                    {result.annualRent > 0 && (
                        <div className="bg-white p-5 rounded-2xl shadow-sm border border-slate-100">
                            <h4 className="text-sm font-bold text-slate-600 mb-3">   </h4>
                            <div className="space-y-2 text-sm">
                                <div className="flex justify-between"><span className="text-slate-500"> </span><span className="font-bold">{formatCompactNumber(result.totalInvestment)}</span></div>
                                <div className="flex justify-between"><span className="text-slate-500"> </span><span className="font-bold text-emerald-600">{formatCompactNumber(result.annualRent)}</span></div>
                                <div className="h-px bg-slate-200"></div>
                                <div className="flex justify-between"><span className="text-slate-700 font-bold"> </span><span className="font-black text-indigo-600">{result.yieldRate}%</span></div>
                            </div>
                        </div>
                    )}
                </div>
            )}
        </div>
    );
};
