
import React from 'react';
import { Link2 } from 'lucide-react';

export const ExternalLinksPanel = () => {
    const links = [
        { name: ' ', url: 'http://www.iros.go.kr', desc: ' /' },
        { name: ' ', url: 'http://rt.molit.go.kr', desc: '  ' },
        { name: '24', url: 'https://www.gov.kr', desc: ', ' },
        { name: 'HUG ', url: 'https://www.khug.or.kr', desc: ' ' },
        { name: '', url: 'https://www.hometax.go.kr', desc: ' /' },
        { name: 'LH ', url: 'https://apply.lh.or.kr', desc: '/ ' },
        { name: 'Home', url: 'https://www.applyhome.co.kr', desc: '  ' },
        { name: 'KB', url: 'https://kbland.kr', desc: 'KB  ( )' },
    ];

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <Link2 className="w-5 h-5 mr-3 text-indigo-600" />   
                </h3>
                <div className="grid gap-3">
                    {links.map((link, i) => (
                        <a
                            key={i}
                            href={link.url}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="bg-slate-50 p-4 rounded-2xl border border-slate-100 hover:bg-indigo-50 hover:border-indigo-100 transition-colors flex justify-between items-center group"
                        >
                            <div>
                                <p className="font-bold text-slate-800 group-hover:text-indigo-700">{link.name}</p>
                                <p className="text-xs text-slate-500">{link.desc}</p>
                            </div>
                            <Link2 className="w-4 h-4 text-slate-300 group-hover:text-indigo-400" />
                        </a>
                    ))}
                </div>
            </div>
        </div>
    );
};
