import React, { useState, useMemo } from 'react';
import { parseNumber, formatNumber, formatCompactNumber } from '../../utils/math.js';
import { BOK_RATE_DEFAULT } from '../../utils/constants.js';
import { ArrowRightLeft } from '../ui/Icons.js';
import { NumberInput } from '../ui/Inputs.js';

// NEW:  
export const SemiJeonseTab = () => {
    const [fullDeposit, setFullDeposit] = useState('');
    const [targetDeposit, setTargetDeposit] = useState('');
    const [targetMonthly, setTargetMonthly] = useState('');
    const [conversionRate, setConversionRate] = useState((BOK_RATE_DEFAULT + 3.5).toFixed(1));

    const result = useMemo(() => {
        const full = parseNumber(fullDeposit);
        const targetDep = parseNumber(targetDeposit);
        const targetMon = parseNumber(targetMonthly);
        const rate = parseFloat(conversionRate) || 0;

        if (!full) return null;

        //     
        if (targetDep > 0 && targetDep < full) {
            const depositDiff = full - targetDep;
            const calculatedMonthly = Math.floor((depositDiff * (rate / 100)) / 12);
            return { mode: 'toMonthly', deposit: targetDep, monthly: calculatedMonthly, diff: depositDiff };
        }
        //     
        if (targetMon > 0) {
            const requiredDeposit = Math.floor((targetMon * 12) / (rate / 100));
            const newDeposit = full - requiredDeposit;
            return { mode: 'toDeposit', deposit: Math.max(newDeposit, 0), monthly: targetMon, diff: requiredDeposit };
        }
        return null;
    }, [fullDeposit, targetDeposit, targetMonthly, conversionRate]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <ArrowRightLeft className="w-5 h-5 mr-3 text-violet-600" />  
                </h3>
                <div className="space-y-5">
                    <NumberInput label="  " value={fullDeposit} onChange={setFullDeposit} color="violet" />
                    <div className="p-4 bg-slate-50 rounded-2xl space-y-4">
                        <p className="text-xs font-bold text-slate-500">  ( )</p>
                        <NumberInput label="  ()" value={targetDeposit} onChange={(v) => { setTargetDeposit(v); setTargetMonthly(''); }} color="violet" />
                        <div className="text-center text-xs text-slate-400 font-bold"></div>
                        <NumberInput label=" " value={targetMonthly} onChange={(v) => { setTargetMonthly(v); setTargetDeposit(''); }} color="violet" />
                    </div>
                    <div>
                        <label className="text-xs font-bold text-slate-500 uppercase tracking-widest block mb-2 ml-1"> (%)</label>
                        <input type="number" value={conversionRate} onChange={(e) => setConversionRate(e.target.value)} className="w-full px-4 py-3 text-right bg-slate-50 rounded-2xl font-bold text-slate-700 outline-none focus:ring-2 focus:ring-violet-500" />
                        <p className="text-xs text-slate-400 mt-1 ml-1">*  : ({BOK_RATE_DEFAULT}%) + 3.5% = {(BOK_RATE_DEFAULT + 3.5).toFixed(1)}%</p>
                    </div>
                </div>
            </div>

            {result ? (
                <div className="bg-gradient-to-br from-violet-50 to-purple-50 p-6 rounded-[2.5rem] border border-violet-100 shadow-lg">
                    <div className="text-center mb-6">
                        <p className="text-sm font-bold text-violet-400 uppercase tracking-widest mb-2"> </p>
                        <p className="text-3xl font-black text-violet-900"> {formatCompactNumber(result.deposit)} +  {formatNumber(result.monthly)}</p>
                    </div>
                    <div className="bg-white rounded-2xl p-4 space-y-2 text-sm">
                        <div className="flex justify-between"><span className="text-slate-500"> </span><span className="font-bold">{formatCompactNumber(result.diff)}</span></div>
                        <div className="flex justify-between"><span className="text-slate-500">  </span><span className="font-bold">{formatNumber(result.monthly * 12)}</span></div>
                    </div>
                </div>
            ) : (
                <div className="text-center py-20 text-slate-300 bg-white rounded-[2rem] border-2 border-dashed border-slate-200">    </div>
            )}
        </div>
    );
};
