
import React, { useState, useMemo } from 'react';
import { Home } from 'lucide-react';
import { parseNumber, formatCompactNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const SubwayDistanceCalculator = () => {
    const [distance, setDistance] = useState('');
    const [propertyPrice, setPropertyPrice] = useState('');

    const result = useMemo(() => {
        const dist = parseFloat(distance) || 0;
        const price = parseNumber(propertyPrice);
        if (!dist) return null;

        let grade = '';
        let premium = 0;
        let walkTime = Math.round(dist / 80); //  80m

        if (dist <= 200) { grade = ''; premium = 15; }
        else if (dist <= 500) { grade = ''; premium = 10; }
        else if (dist <= 1000) { grade = ''; premium = 5; }
        else { grade = ' '; premium = 0; }

        const premiumValue = price ? Math.round(price * (premium / 100)) : 0;

        return { grade, premium, walkTime, premiumValue };
    }, [distance, propertyPrice]);

    return (
        <div className="bg-white p-6 rounded-[2rem] shadow-sm border border-slate-100 card-depth mt-2">
            <h4 className="text-sm font-bold text-slate-800 mb-4 flex items-center">
                <Home className="w-4 h-4 mr-2 text-blue-600" />   
            </h4>
            <div className="space-y-4">
                <div>
                    <label className="text-xs font-bold text-slate-500 block mb-1">  (m)</label>
                    <input type="number" value={distance} onChange={e => setDistance(e.target.value)} placeholder=": 300" className="w-full px-4 py-3 bg-slate-50 rounded-xl text-right font-bold outline-none" />
                </div>
                <NumberInput label="  ()" value={propertyPrice} onChange={setPropertyPrice} color="blue" />
            </div>
            {result && (
                <div className="mt-4 space-y-3">
                    <div className={`p-4 rounded-xl text-center ${result.premium >= 10 ? 'bg-blue-50' : result.premium > 0 ? 'bg-slate-50' : 'bg-red-50'}`}>
                        <p className="text-xs text-slate-600 font-bold mb-1"> </p>
                        <p className={`text-2xl font-black ${result.premium >= 10 ? 'text-blue-600' : result.premium > 0 ? 'text-slate-700' : 'text-red-500'}`}>{result.grade}</p>
                        <p className="text-sm text-slate-500 mt-1">   {result.walkTime}</p>
                    </div>
                    {result.premium > 0 && (
                        <div className="p-3 bg-green-50 rounded-xl text-center">
                            <p className="text-xs text-green-600 font-bold"> </p>
                            <p className="text-lg font-black text-green-700">+{result.premium}%</p>
                            {result.premiumValue > 0 && <p className="text-xs text-slate-500"> {formatCompactNumber(result.premiumValue)}  </p>}
                        </div>
                    )}
                    <div className="p-3 bg-slate-50 rounded-xl text-xs text-slate-600">
                        <p className="font-bold mb-1">  </p>
                        <p> : 200m </p>
                        <p> : 500m </p>
                        <p> : 1km </p>
                    </div>
                </div>
            )}
        </div>
    );
};
