
import React, { useState } from 'react';
import { MessageCircle } from 'lucide-react';

export const LeaseDisputeGuide = () => {
    const [issue, setIssue] = useState('deposit_return');

    const issues = {
        deposit_return: { title: ' ', solution: ' :       .     5%  .', tip: '        ' },
        rent_increase: { title: ' ', solution: '5% :   5%    (  ). ,     .', tip: ' 10  (2020.7.31  )' },
        maintenance: { title: ' ', solution: ' : , ,    .  : ,    .', tip: '         ' },
        early_termination: { title: ' ', solution: '   3    .       .', tip: '       ' },
    };

    const current = issues[issue];

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <MessageCircle className="w-5 h-5 mr-3 text-purple-600" />   
                </h3>
                <div className="grid grid-cols-2 gap-2">
                    {Object.entries(issues).map(([k, v]) => (
                        <button key={k} onClick={() => setIssue(k)} className={`py-3 rounded-xl text-xs font-bold ${issue === k ? 'bg-purple-500 text-white' : 'bg-slate-100'}`}>{v.title}</button>
                    ))}
                </div>
            </div>
            <div className="bg-purple-50 p-5 rounded-2xl border border-purple-200">
                <h4 className="text-sm font-bold text-purple-700 mb-3">{current.title}</h4>
                <p className="text-sm text-slate-600 mb-4">{current.solution}</p>
                <div className="bg-white/70 p-3 rounded-xl">
                    <p className="text-xs font-bold text-purple-600"> Tip</p>
                    <p className="text-xs text-slate-600">{current.tip}</p>
                </div>
            </div>
        </div>
    );
};
