
import React, { useState, useMemo } from 'react';
import { parseNumber, formatNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const MaintenanceFeeAnalyzer = () => {
    const [fee, setFee] = useState('');
    const [area, setArea] = useState('');

    const result = useMemo(() => {
        const f = parseNumber(fee);
        const a = parseFloat(area) || 0;
        if (!f || !a) return null;
        const pyung = a / 3.3058;
        const perPyung = Math.round(f / pyung);
        let status = '', color = 'emerald';
        if (perPyung > 15000) { status = ''; color = 'red'; }
        else if (perPyung > 10000) { status = ''; color = 'orange'; }
        return { perPyung, status, color, pyung: pyung.toFixed(1) };
    }, [fee, area]);

    return (
        <div className="bg-white p-6 rounded-[2rem] shadow-sm border border-slate-100 mt-2">
            <h4 className="text-sm font-bold text-slate-800 mb-4">  </h4>
            <div className="space-y-4">
                <NumberInput label=" " value={fee} onChange={setFee} color="green" />
                <div>
                    <label className="text-xs font-bold text-slate-500 block mb-1"> ()</label>
                    <input type="number" value={area} onChange={e => setArea(e.target.value)} className="w-full px-3 py-2 bg-slate-50 rounded-xl text-right font-bold outline-none" />
                </div>
                {result && (
                    <div className={`p-4 bg-${result.color}-50 rounded-2xl`}>
                        <div className="flex justify-between items-center">
                            <span className="text-sm text-slate-600"> </span>
                            <span className="text-xl font-black">{formatNumber(result.perPyung)}</span>
                        </div>
                        <p className="text-xs text-slate-500 mt-2"> {result.pyung}  | : <span className="font-bold">{result.status}</span></p>
                    </div>
                )}
            </div>
        </div>
    );
};
