
import React, { useState } from 'react';
import { Home, TrendingUp, DollarSign, Calculator, BarChart3, Lightbulb, Shield, PenTool } from 'lucide-react';
import { usePersistedState } from './utils/hooks';

// UI Components
import { OfflineIndicator } from './components/ui/OfflineIndicator';
import { Toast } from './components/ui/Toast';
import { ToolsTab } from './components/ui/ToolsTab';

// Rent Tabs
import { CostCompareTab } from './components/calculators/CostCompareTab';
import { SafetyTab } from './components/calculators/SafetyTab';
import { SemiJeonseTab } from './components/calculators/SemiJeonseTab';
import { FeeTab } from './components/calculators/FeeTab';

// Buy Tabs
import { BuyingTab } from './components/calculators/BuyingTab';
import { RentVsBuyTab } from './components/calculators/RentVsBuyTab';
import { AffordabilityTab } from './components/calculators/AffordabilityTab';
import { RentalYieldTab } from './components/calculators/RentalYieldTab';
import { GapInvestmentTab } from './components/calculators/GapInvestmentTab';
import { MultiPropertyTaxTab } from './components/calculators/MultiPropertyTaxTab';
import { ReconstructionTab } from './components/calculators/ReconstructionTab';

// Bank Tabs
import { LoanTab } from './components/calculators/LoanTab';
import { LoanCompareTab } from './components/calculators/LoanCompareTab';
import { AmortizationScheduleTab } from './components/calculators/AmortizationScheduleTab';
import { MortgageLimitTab } from './components/calculators/MortgageLimitTab';
import { ReverseRentTab } from './components/calculators/ReverseRentTab';
import { JeonseLoanLimitTab } from './components/calculators/JeonseLoanLimitTab';
import { DSRTab } from './components/calculators/DSRTab';
import { DTITab } from './components/calculators/DTITab';
import { LTVTab } from './components/calculators/LTVTab';

// Tax Tabs
import { CapitalGainsTaxTab } from './components/calculators/CapitalGainsTaxTab';
import { PropertyTaxTab } from './components/calculators/PropertyTaxTab';
import { TaxOptimizer } from './components/calculators/TaxOptimizer';
import { JointOwnershipAnalyzer } from './components/calculators/JointOwnershipAnalyzer';
import { RentalBusinessCompare } from './components/calculators/RentalBusinessCompare';
import { SingleHomeExemption } from './components/calculators/SingleHomeExemption';
import { GiftTaxTab } from './components/calculators/GiftTaxTab';
import { InheritanceTaxTab } from './components/calculators/InheritanceTaxTab';

// Subscription Tabs
import { SubscriptionScoreTab } from './components/calculators/SubscriptionScoreTab';

// Analysis Tabs
import { MarketAnalysisTab } from './components/calculators/MarketAnalysisTab';
import { PortfolioDashboard } from './components/calculators/PortfolioDashboard';
import { PortfolioManager } from './components/calculators/PortfolioManager';
import { CashFlowAnalyzer } from './components/calculators/CashFlowAnalyzer';
import { InvestmentNPVIRR } from './components/calculators/InvestmentNPVIRR';
import { RegionComparison } from './components/calculators/RegionComparison';
import { SensitivityAnalysis } from './components/calculators/SensitivityAnalysis';
import { PriceForecastSimulator } from './components/calculators/PriceForecastSimulator';
import { CostCompositionChart } from './components/calculators/CostCompositionChart';
import { RegulationChecker } from './components/calculators/RegulationChecker';

// Invest Tabs
import { InvestmentRecommender } from './components/calculators/InvestmentRecommender';
import { RiskScoreCalculator } from './components/calculators/RiskScoreCalculator';
import { AuctionBidCalculator } from './components/calculators/AuctionBidCalculator';
import { AuctionYieldAnalyzer } from './components/calculators/AuctionYieldAnalyzer';
import { EvictionCostCalculator } from './components/calculators/EvictionCostCalculator';
import { RightsAnalysisChecklist } from './components/calculators/RightsAnalysisChecklist';
import { LivingCostSimulator } from './components/calculators/LivingCostSimulator';
import { LivingQualityScore } from './components/calculators/LivingQualityScore';

// Legal Tabs
import { RegistryAnalyzer } from './components/calculators/RegistryAnalyzer';
import { MortgageSettingCalculator } from './components/calculators/MortgageSettingCalculator';
import { ContractDisputeGuide } from './components/calculators/ContractDisputeGuide';
import { LeaseDisputeGuide } from './components/calculators/LeaseDisputeGuide';

// Styles
import './styles.css';

const App = () => {
    const [activeCategory, setActiveCategory] = useState('rent');
    // activeSubTab as object to persist state between category switches
    const [activeSubTab, setActiveSubTab] = useState({
        rent: 0,
        buy: 0,
        bank: 0,
        tax: 0,
        subscription: 0,
        analysis: 0,
        invest: 0,
        legal: 0,
        tools: 0
    });
    const [toastMessage, setToastMessage] = useState('');

    const showToast = (message) => {
        setToastMessage(message);
    };

    const categories = [
        { id: 'rent', label: '', icon: Home },
        { id: 'buy', label: '', icon: TrendingUp },
        { id: 'bank', label: '', icon: DollarSign },
        { id: 'tax', label: '', icon: Calculator },
        { id: 'analysis', label: '', icon: BarChart3 },
        { id: 'invest', label: '', icon: Lightbulb },
        { id: 'legal', label: '', icon: Shield },
        { id: 'tools', label: '', icon: PenTool },
    ];

    const tabs = {
        rent: [
            { id: 0, label: '', component: CostCompareTab },
            { id: 1, label: '', component: SafetyTab },
            { id: 2, label: '', component: SemiJeonseTab },
            { id: 3, label: '', component: FeeTab },
        ],
        buy: [
            { id: 0, label: '/', component: BuyingTab },
            { id: 1, label: 'vs', component: RentVsBuyTab },
            { id: 2, label: '', component: AffordabilityTab },
            { id: 3, label: '', component: RentalYieldTab },
            { id: 4, label: 'ROI', component: GapInvestmentTab },
            { id: 5, label: '', component: MultiPropertyTaxTab },
            { id: 6, label: '', component: ReconstructionTab },
            { id: 7, label: '', component: FeeTab },
        ],
        bank: [
            { id: 0, label: '', component: LoanTab },
            { id: 1, label: '', component: LoanCompareTab },
            { id: 2, label: '', component: AmortizationScheduleTab },
            { id: 3, label: '', component: MortgageLimitTab },
            { id: 4, label: '', component: ReverseRentTab },
            { id: 5, label: '', component: JeonseLoanLimitTab },
            { id: 6, label: 'DSR', component: DSRTab },
            { id: 7, label: 'DTI', component: DTITab },
            { id: 8, label: 'LTV', component: LTVTab },
        ],
        tax: [
            { id: 0, label: '', component: CapitalGainsTaxTab },
            { id: 1, label: '/', component: PropertyTaxTab },
            { id: 2, label: '', component: TaxOptimizer },
            { id: 3, label: '', component: JointOwnershipAnalyzer },
            { id: 4, label: '', component: RentalBusinessCompare },
            { id: 5, label: '1', component: SingleHomeExemption },
            { id: 6, label: '', component: GiftTaxTab },
            { id: 7, label: '', component: InheritanceTaxTab },
        ],
        subscription: [
            { id: 0, label: '', component: SubscriptionScoreTab },
        ],
        analysis: [
            { id: 0, label: 'AI', component: MarketAnalysisTab },
            { id: 1, label: '', component: PortfolioDashboard },
            { id: 2, label: '', component: PortfolioManager },
            { id: 3, label: '', component: CashFlowAnalyzer },
            { id: 4, label: 'NPV/IRR', component: InvestmentNPVIRR },
            { id: 5, label: '', component: RegionComparison },
            { id: 6, label: '', component: SensitivityAnalysis },
            { id: 7, label: '', component: PriceForecastSimulator },
            { id: 8, label: '', component: CostCompositionChart },
            { id: 9, label: '', component: RegulationChecker },
        ],
        invest: [
            { id: 0, label: '', component: InvestmentRecommender },
            { id: 1, label: '', component: RiskScoreCalculator },
            { id: 2, label: '', component: AuctionBidCalculator },
            { id: 3, label: '', component: AuctionYieldAnalyzer },
            { id: 4, label: '', component: EvictionCostCalculator },
            { id: 5, label: '', component: RightsAnalysisChecklist },
            { id: 6, label: '', component: LivingCostSimulator },
            { id: 7, label: '', component: LivingQualityScore },
        ],
        legal: [
            { id: 0, label: '', component: RegistryAnalyzer },
            { id: 1, label: '', component: MortgageSettingCalculator },
            { id: 2, label: '', component: ContractDisputeGuide },
            { id: 3, label: '', component: LeaseDisputeGuide },
        ],
        tools: [
            { id: 0, label: '', component: ToolsTab }
        ]
    };

    const ActiveComponent = tabs[activeCategory]
        ? (tabs[activeCategory].find(t => t.id === activeSubTab[activeCategory]) || tabs[activeCategory][0]).component
        : null;

    return (
        <div className="max-w-md mx-auto min-h-screen bg-slate-50 relative shadow-2xl overflow-hidden flex flex-col">
            <OfflineIndicator />

            {/* Header */}
            <div className="bg-white/80 backdrop-blur-md sticky top-0 z-30 border-b border-slate-100">
                <div className="flex px-4 py-3 items-center justify-between">
                    <h1 className="text-lg font-black bg-gradient-to-r from-blue-600 to-indigo-600 bg-clip-text text-transparent">
                          Pro
                    </h1>
                </div>

                {/* Sub Tabs (Horizontal Scroll) */}
                {activeCategory !== 'tools' && tabs[activeCategory] && (
                    <div className="px-4 pb-2 overflow-x-auto no-scrollbar flex gap-2">
                        {tabs[activeCategory].map(tab => (
                            <button
                                key={tab.id}
                                onClick={() => setActiveSubTab({ ...activeSubTab, [activeCategory]: tab.id })}
                                className={`px-4 py-2 rounded-full text-sm font-bold whitespace-nowrap transition-all ${activeSubTab[activeCategory] === tab.id
                                        ? 'bg-slate-800 text-white shadow-lg shadow-slate-200'
                                        : 'bg-slate-100/80 text-slate-500 hover:bg-slate-200'
                                    }`}
                            >
                                {tab.label}
                            </button>
                        ))}
                    </div>
                )}
            </div>

            {/* Main Content */}
            <main className="flex-1 p-4 pb-24 overflow-y-auto relative">
                {ActiveComponent ? <ActiveComponent /> : <div className="text-center py-20 text-slate-400"> </div>}
            </main>

            {/* Bottom Navigation */}
            <nav className="bg-white/90 backdrop-blur-lg border-t border-slate-200 fixed bottom-0 max-w-md w-full z-40 pb-safe">
                <div className="flex justify-around items-center px-2 py-2">
                    {categories.map(cat => {
                        const isActive = activeCategory === cat.id;
                        return (
                            <button
                                key={cat.id}
                                onClick={() => setActiveCategory(cat.id)}
                                className={`flex flex-col items-center p-2 rounded-xl transition-all ${isActive ? 'text-blue-600 scale-105' : 'text-slate-400 hover:text-slate-600'
                                    }`}
                            >
                                <cat.icon className={`w-6 h-6 mb-1 ${isActive ? 'fill-blue-100' : ''}`} strokeWidth={isActive ? 2.5 : 2} />
                                <span className={`text-[10px] font-bold ${isActive ? 'text-blue-600' : 'text-slate-400'}`}>
                                    {cat.label}
                                </span>
                            </button>
                        );
                    })}
                </div>
            </nav>

            {toastMessage && <Toast message={toastMessage} onClose={() => setToastMessage('')} />}
        </div>
    );
};

export default App;
