
import React, { useState } from 'react';
import { CheckCircle } from 'lucide-react';

export const LivingQualityScore = () => {
    const [scores, setScores] = useState({
        subway: 3, bus: 3, school: 3, mart: 3, hospital: 3, park: 3, noise: 3, view: 3
    });

    const items = [
        { key: 'subway', label: ' ', icon: '' },
        { key: 'bus', label: ' ', icon: '' },
        { key: 'school', label: '/', icon: '' },
        { key: 'mart', label: '', icon: '' },
        { key: 'hospital', label: '', icon: '' },
        { key: 'park', label: '/', icon: '' },
        { key: 'noise', label: ' ', icon: '' },
        { key: 'view', label: '/', icon: '' },
    ];

    const totalScore = Object.values(scores).reduce((a, b) => a + b, 0);
    const maxScore = Object.keys(scores).length * 5;
    const percentage = Math.round((totalScore / maxScore) * 100);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <CheckCircle className="w-5 h-5 mr-3 text-teal-600" />   
                </h3>
                <div className="space-y-4">
                    {items.map(item => (
                        <div key={item.key} className="flex items-center gap-3">
                            <span className="text-lg">{item.icon}</span>
                            <span className="text-sm font-bold text-slate-600 w-24">{item.label}</span>
                            <div className="flex-1 flex gap-1">
                                {[1, 2, 3, 4, 5].map(v => (
                                    <button key={v} onClick={() => setScores({ ...scores, [item.key]: v })} className={`flex-1 py-2 rounded-lg text-xs font-bold ${scores[item.key] >= v ? 'bg-teal-500 text-white' : 'bg-slate-100 text-slate-400'}`}>{v}</button>
                                ))}
                            </div>
                        </div>
                    ))}
                </div>
            </div>
            <div className={`p-6 rounded-[2rem] text-center ${percentage >= 70 ? 'bg-teal-50 border border-teal-200' : percentage >= 50 ? 'bg-yellow-50 border border-yellow-200' : 'bg-red-50 border border-red-200'}`}>
                <p className="text-xs font-bold text-slate-500 mb-2">  </p>
                <p className={`text-5xl font-black ${percentage >= 70 ? 'text-teal-600' : percentage >= 50 ? 'text-yellow-600' : 'text-red-600'}`}>{percentage}</p>
                <p className="text-sm text-slate-500 mt-2">{totalScore} / {maxScore}</p>
            </div>
        </div>
    );
};
