
import React, { useState, useMemo } from 'react';
import { Home } from 'lucide-react';
import { parseNumber, formatNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const LivingCostSimulator = () => {
    const [housingCost, setHousingCost] = useState('');
    const [managementFee, setManagementFee] = useState('');
    const [transportCost, setTransportCost] = useState('');
    const [utilityCost, setUtilityCost] = useState('');
    const [monthlyIncome, setMonthlyIncome] = useState('');

    const result = useMemo(() => {
        const housing = parseNumber(housingCost);
        const mgmt = parseNumber(managementFee);
        const transport = parseNumber(transportCost);
        const utility = parseNumber(utilityCost);
        const income = parseNumber(monthlyIncome);

        const total = housing + mgmt + transport + utility;
        const ratio = income > 0 ? (total / income) * 100 : 0;

        return {
            housing, mgmt, transport, utility, total, income, ratio: ratio.toFixed(1),
            isHealthy: ratio <= 30
        };
    }, [housingCost, managementFee, transportCost, utilityCost, monthlyIncome]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <Home className="w-5 h-5 mr-3 text-teal-600" />  
                </h3>
                <div className="space-y-4">
                    <NumberInput label="  (/)" value={housingCost} onChange={setHousingCost} color="blue" />
                    <NumberInput label=" " value={managementFee} onChange={setManagementFee} color="orange" />
                    <NumberInput label=" " value={transportCost} onChange={setTransportCost} color="purple" />
                    <NumberInput label="  (//)" value={utilityCost} onChange={setUtilityCost} color="green" />
                    <div className="h-px bg-slate-200 my-2"></div>
                    <NumberInput label=" " value={monthlyIncome} onChange={setMonthlyIncome} color="slate" />
                </div>
            </div>

            {result.total > 0 && (
                <div className="space-y-4">
                    <div className={`p-6 rounded-[2rem] ${result.isHealthy ? 'bg-teal-50 border border-teal-200' : 'bg-red-50 border border-red-200'}`}>
                        <div className="text-center mb-4">
                            <p className="text-sm font-bold text-slate-500 mb-1">  </p>
                            <p className="text-4xl font-black text-slate-800">{formatNumber(result.total)}</p>
                            {result.income > 0 && (
                                <p className={`text-lg font-bold mt-2 ${result.isHealthy ? 'text-teal-600' : 'text-red-600'}`}>
                                      {result.ratio}%
                                </p>
                            )}
                        </div>
                    </div>

                    {/*   */}
                    <div className="bg-white p-5 rounded-2xl shadow-sm border border-slate-100">
                        <h4 className="text-sm font-bold text-slate-600 mb-3"> </h4>
                        {[
                            { label: '', value: result.housing, color: 'bg-blue-500' },
                            { label: '', value: result.mgmt, color: 'bg-orange-500' },
                            { label: '', value: result.transport, color: 'bg-purple-500' },
                            { label: '', value: result.utility, color: 'bg-green-500' },
                        ].map((item, i) => (
                            <div key={i} className="flex items-center gap-3 mb-2">
                                <span className="text-sm text-slate-500 w-16">{item.label}</span>
                                <div className="flex-1 bg-slate-100 rounded-full h-3 overflow-hidden">
                                    <div className={`h-full ${item.color} rounded-full`} style={{ width: `${(item.value / result.total) * 100}%` }} />
                                </div>
                                <span className="text-sm font-bold w-20 text-right">{formatNumber(item.value)}</span>
                            </div>
                        ))}
                    </div>

                    <div className="p-4 bg-indigo-50 rounded-2xl text-xs text-indigo-600">
                           30%    .
                    </div>
                </div>
            )}
        </div>
    );
};
