import { setSafeLocalStorage } from './storage.js';

// ========== URL   ==========
export const generateShareUrl = (data) => {
    const params = new URLSearchParams();
    Object.entries(data).forEach(([key, value]) => {
        if (value !== null && value !== undefined && value !== '') {
            params.set(key, String(value));
        }
    });
    return `${window.location.origin}${window.location.pathname}?${params.toString()}`;
};

export const parseShareUrl = () => {
    const params = new URLSearchParams(window.location.search);
    const data = {};
    params.forEach((value, key) => {
        data[key] = value;
    });
    return data;
};

export const applyUrlParams = () => {
    const params = parseShareUrl();
    if (Object.keys(params).length === 0) return false;

    // localStorage   
    const keyMappings = {
        jd: 'jDeposit', jr: 'jRate', wd: 'wDeposit', wm: 'wMonthly',
        mp: 'marketPrice', rb: 'rankingBond', dp: 'deposit'
    };
    Object.entries(params).forEach(([key, value]) => {
        const mappedKey = keyMappings[key] || key;
        if (value) setSafeLocalStorage(mappedKey, value);
    });

    // URL  ( )
    if (window.history.replaceState) {
        window.history.replaceState({}, '', window.location.pathname);
    }
    return true;
};

// --- URL   () ---
export const generateCalcShareUrl = (calcType, params) => {
    const url = new URL(window.location.href.split('?')[0]);
    url.searchParams.set('calc', calcType);
    Object.entries(params).forEach(([key, value]) => {
        if (value) url.searchParams.set(key, value);
    });
    return url.toString();
};

//   
export const copyToClipboard = async (text, showToastFn) => {
    try {
        await navigator.clipboard.writeText(text);
        if (showToastFn) showToastFn(' !');
        return true;
    } catch (err) {
        console.error('Failed to copy:', err);
        return false;
    }
};
