
import React, { useState } from 'react';
import { CheckSquare, Check } from 'lucide-react';

export const SingleHomeExemption = () => {
    const [checks, setChecks] = useState({
        singleHouse: false, holdingPeriod2: false, residencePeriod2: false, priceUnder12: false, registeredResident: false
    });

    const items = [
        { key: 'singleHouse', label: '1 1 ', desc: '       ', required: true },
        { key: 'holdingPeriod2', label: '2  ', desc: ' 2  ', required: true },
        { key: 'residencePeriod2', label: '2   ()', desc: '   2  ', required: false },
        { key: 'priceUnder12', label: ' 12 ', desc: '12   ', required: false },
        { key: 'registeredResident', label: ' ', desc: '   ', required: true },
    ];

    const requiredItems = items.filter(i => i.required);
    const passedRequired = requiredItems.filter(i => checks[i.key]).length;
    const isExempt = passedRequired === requiredItems.length;

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-2 flex items-center">
                    <CheckSquare className="w-5 h-5 mr-3 text-green-600" /> 1 1  
                </h3>
                <p className="text-xs text-slate-400 mb-6">   </p>
                <div className="space-y-3">
                    {items.map(item => (
                        <button key={item.key} onClick={() => setChecks({ ...checks, [item.key]: !checks[item.key] })} className={`w-full p-4 rounded-2xl border-2 text-left transition-all ${checks[item.key] ? 'bg-green-50 border-green-200' : 'bg-slate-50 border-slate-100'}`}>
                            <div className="flex items-center justify-between">
                                <div>
                                    <p className="font-bold text-sm">{item.label} {item.required && <span className="text-red-500">*</span>}</p>
                                    <p className="text-xs opacity-70">{item.desc}</p>
                                </div>
                                <div className={`w-6 h-6 rounded-full border-2 flex items-center justify-center ${checks[item.key] ? 'bg-green-500 border-green-500' : 'border-slate-300'}`}>
                                    {checks[item.key] && <Check className="w-4 h-4 text-white" />}
                                </div>
                            </div>
                        </button>
                    ))}
                </div>
            </div>
            <div className={`p-6 rounded-[2rem] text-center ${isExempt ? 'bg-green-50 border border-green-200' : 'bg-red-50 border border-red-200'}`}>
                <p className={`text-4xl font-black ${isExempt ? 'text-green-600' : 'text-red-600'}`}>{isExempt ? ' ' : ' '}</p>
                <p className="text-sm text-slate-500 mt-2">{isExempt ? '1 1   ' : `  ${passedRequired}/${requiredItems.length} `}</p>
            </div>
        </div>
    );
};
