
import React, { useState, useMemo } from 'react';
import { TrendingUp } from 'lucide-react';
import { parseNumber, formatCompactNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const PriceForecastSimulator = () => {
    const [currentPrice, setCurrentPrice] = useState('');
    const [annualGrowth, setAnnualGrowth] = useState('3');
    const [forecastYears, setForecastYears] = useState('5');

    const result = useMemo(() => {
        const price = parseNumber(currentPrice);
        const growth = parseFloat(annualGrowth) || 3;
        const years = parseInt(forecastYears) || 5;
        if (!price) return null;

        const forecasts = [];
        for (let i = 1; i <= years; i++) {
            const futurePrice = Math.round(price * Math.pow(1 + growth / 100, i));
            const gain = futurePrice - price;
            forecasts.push({ year: i, price: futurePrice, gain, gainRate: ((gain / price) * 100).toFixed(1) });
        }
        return forecasts;
    }, [currentPrice, annualGrowth, forecastYears]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <TrendingUp className="w-5 h-5 mr-3 text-blue-600" />   
                </h3>
                <div className="space-y-4">
                    <NumberInput label=" " value={currentPrice} onChange={setCurrentPrice} color="blue" />
                    <div>
                        <label className="text-xs font-bold text-slate-500 block mb-2">  (%)</label>
                        <input type="number" step="0.5" value={annualGrowth} onChange={(e) => setAnnualGrowth(e.target.value)} className="w-full p-3 bg-slate-50 rounded-xl text-right font-bold" />
                    </div>
                    <div>
                        <label className="text-xs font-bold text-slate-500 block mb-2">  ()</label>
                        <div className="flex gap-2">
                            {[3, 5, 7, 10].map(y => (
                                <button key={y} onClick={() => setForecastYears(String(y))} className={`flex-1 py-2 rounded-xl text-sm font-bold ${forecastYears == y ? 'bg-blue-500 text-white' : 'bg-slate-100'}`}>{y}</button>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
            {result && (
                <div className="bg-white p-5 rounded-2xl shadow-sm border border-slate-100">
                    <h4 className="text-sm font-bold text-slate-600 mb-4">  </h4>
                    <div className="space-y-2">
                        {result.map((f, i) => (
                            <div key={i} className="flex justify-between items-center p-3 bg-slate-50 rounded-xl">
                                <span className="text-sm font-bold text-slate-700">{f.year} </span>
                                <div className="text-right">
                                    <span className="text-sm font-bold text-slate-800">{formatCompactNumber(f.price)}</span>
                                    <span className="text-xs text-emerald-600 ml-2">(+{f.gainRate}%)</span>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            )}
        </div>
    );
};
