
import React, { useState, useMemo } from 'react';
import { Banknote } from 'lucide-react';
import { parseNumber, formatNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const CombinedLoanCalculator = () => {
    const [annualIncome, setAnnualIncome] = useState('');
    const [mortgageAmount, setMortgageAmount] = useState('');
    const [mortgageRate, setMortgageRate] = useState('4.5');
    const [mortgagePeriod, setMortgagePeriod] = useState('360');
    const [creditAmount, setCreditAmount] = useState('');
    const [creditRate, setCreditRate] = useState('6.5');
    const [creditPeriod, setCreditPeriod] = useState('60');

    const result = useMemo(() => {
        const income = parseNumber(annualIncome);
        const mortgage = parseNumber(mortgageAmount);
        const credit = parseNumber(creditAmount);
        if (income <= 0) return null;

        const calcMonthlyPayment = (principal, rate, months) => {
            if (principal <= 0 || months <= 0) return 0;
            const r = (parseFloat(rate) || 0) / 100 / 12;
            if (r <= 0) return principal / months;
            const factor = Math.pow(1 + r, months);
            return (principal * r * factor) / (factor - 1);
        };

        const mortgageMonthly = calcMonthlyPayment(mortgage, mortgageRate, parseInt(mortgagePeriod));
        const creditMonthly = calcMonthlyPayment(credit, creditRate, parseInt(creditPeriod));
        const totalMonthly = mortgageMonthly + creditMonthly;
        const annualPayment = totalMonthly * 12;
        const dsr = (annualPayment / income) * 100;
        const limit = 40;
        const remaining = Math.max(0, (income * (limit / 100) - annualPayment) / 12);

        return { mortgageMonthly, creditMonthly, totalMonthly, dsr, remaining, isOver: dsr > limit };
    }, [annualIncome, mortgageAmount, mortgageRate, mortgagePeriod, creditAmount, creditRate, creditPeriod]);

    return (
        <div className="bg-white p-6 rounded-[2rem] shadow-sm border border-slate-100 card-depth mt-2">
            <h4 className="text-sm font-bold text-slate-800 mb-4 flex items-center">
                <Banknote className="w-4 h-4 mr-2 text-emerald-500" />    (DSR )
            </h4>
            <div className="space-y-4">
                <NumberInput label=" " value={annualIncome} onChange={setAnnualIncome} color="emerald" />
                <div className="p-4 bg-indigo-50 rounded-xl">
                    <p className="text-xs font-bold text-indigo-600 mb-2"> </p>
                    <div className="grid grid-cols-3 gap-2">
                        <div className="col-span-3"><NumberInput label="" value={mortgageAmount} onChange={setMortgageAmount} color="indigo" /></div>
                        <div><label className="text-[10px] text-slate-500">(%)</label><input type="number" value={mortgageRate} onChange={e => setMortgageRate(e.target.value)} className="w-full px-2 py-1.5 bg-white rounded-lg text-sm font-bold text-right outline-none" /></div>
                        <div className="col-span-2"><label className="text-[10px] text-slate-500">()</label><input type="number" value={mortgagePeriod} onChange={e => setMortgagePeriod(e.target.value)} className="w-full px-2 py-1.5 bg-white rounded-lg text-sm font-bold text-right outline-none" /></div>
                    </div>
                </div>
                <div className="p-4 bg-orange-50 rounded-xl">
                    <p className="text-xs font-bold text-orange-600 mb-2"> </p>
                    <div className="grid grid-cols-3 gap-2">
                        <div className="col-span-3"><NumberInput label="" value={creditAmount} onChange={setCreditAmount} color="orange" /></div>
                        <div><label className="text-[10px] text-slate-500">(%)</label><input type="number" value={creditRate} onChange={e => setCreditRate(e.target.value)} className="w-full px-2 py-1.5 bg-white rounded-lg text-sm font-bold text-right outline-none" /></div>
                        <div className="col-span-2"><label className="text-[10px] text-slate-500">()</label><input type="number" value={creditPeriod} onChange={e => setCreditPeriod(e.target.value)} className="w-full px-2 py-1.5 bg-white rounded-lg text-sm font-bold text-right outline-none" /></div>
                    </div>
                </div>
            </div>
            {result && (
                <div className="mt-4 space-y-3">
                    <div className={`p-4 rounded-xl text-center ${result.isOver ? 'bg-red-50' : 'bg-green-50'}`}>
                        <p className="text-xs text-slate-500 mb-1"> DSR</p>
                        <p className={`text-3xl font-black ${result.isOver ? 'text-red-600' : 'text-green-600'}`}>{result.dsr.toFixed(1)}%</p>
                        <p className="text-xs mt-1">{result.isOver ? '   (40% )' : '  '}</p>
                    </div>
                    <div className="grid grid-cols-2 gap-2 text-xs">
                        <div className="p-3 bg-indigo-50 rounded-xl"><p className="text-slate-500"> </p><p className="font-bold text-indigo-600">{formatNumber(Math.round(result.mortgageMonthly))}</p></div>
                        <div className="p-3 bg-orange-50 rounded-xl"><p className="text-slate-500"> </p><p className="font-bold text-orange-600">{formatNumber(Math.round(result.creditMonthly))}</p></div>
                    </div>
                    <div className="p-3 bg-slate-50 rounded-xl text-center">
                        <p className="text-xs text-slate-500">   ()</p>
                        <p className="text-lg font-black text-slate-700">{formatNumber(Math.round(result.remaining))}</p>
                    </div>
                </div>
            )}
        </div>
    );
};
