
import React from 'react';
import { FileText } from 'lucide-react';

export const RegistryAnalyzer = () => {
    const sections = [
        { title: ' ()', items: [' -   ', ' -  ,   ', ' -    ', ' -  ,  '], color: 'blue' },
        { title: ' ( )', items: [' - ,  ', ' -   ', ' -  ', ' -  '], color: 'purple' },
        { title: '', items: ['/ ', '  ', '/'], color: 'green' },
    ];

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-2 flex items-center">
                    <FileText className="w-5 h-5 mr-3 text-violet-600" />   
                </h3>
                <p className="text-xs text-slate-400 mb-4">    </p>
            </div>
            {sections.map((section, i) => (
                <div key={i} className={`bg-${section.color}-50 p-5 rounded-2xl border border-${section.color}-200`}>
                    <h4 className={`text-sm font-bold text-${section.color}-700 mb-3`}>{section.title}</h4>
                    <ul className="space-y-2">
                        {section.items.map((item, j) => (
                            <li key={j} className="text-sm text-slate-600 flex items-start gap-2">
                                <span className={`text-${section.color}-400`}></span> {item}
                            </li>
                        ))}
                    </ul>
                </div>
            ))}
        </div>
    );
};
