
import React, { useState, useMemo } from 'react';
import { Building } from 'lucide-react';
import { parseNumber, formatCompactNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const ReconstructionTab = () => {
    const [currentValue, setCurrentValue] = useState('');
    const [contribution, setContribution] = useState('');
    const [expectedValue, setExpectedValue] = useState('');
    const [duration, setDuration] = useState('5');

    const result = useMemo(() => {
        const current = parseNumber(currentValue);
        const contrib = parseNumber(contribution);
        const expected = parseNumber(expectedValue);
        const years = parseInt(duration) || 1;

        if (!current || !expected) return null;

        const totalInvestment = current + contrib;
        const profit = expected - totalInvestment;
        const roi = totalInvestment > 0 ? (profit / totalInvestment) * 100 : 0;
        const annualRoi = roi / years;

        return { totalInvestment, profit, roi, annualRoi, isPositive: profit > 0 };
    }, [currentValue, contribution, expectedValue, duration]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <Building className="w-5 h-5 mr-3 text-amber-600" /> / 
                </h3>
                <div className="space-y-5">
                    <NumberInput label="  ()" value={currentValue} onChange={setCurrentValue} color="orange" />
                    <NumberInput label=" " value={contribution} onChange={setContribution} color="orange" />
                    <NumberInput label="   " value={expectedValue} onChange={setExpectedValue} color="orange" />
                    <div>
                        <label className="text-xs font-bold text-slate-500 block mb-1">  ()</label>
                        <input type="number" value={duration} onChange={e => setDuration(e.target.value)} className="w-full px-3 py-2 bg-slate-50 rounded-xl text-right font-bold outline-none" />
                    </div>
                </div>
            </div>
            {result ? (
                <div className={`p-6 rounded-[2.5rem] shadow-lg ${result.isPositive ? 'bg-gradient-to-br from-amber-50 to-orange-50 border border-amber-100' : 'bg-red-50 border border-red-200'}`}>
                    <div className="text-center mb-4">
                        <p className="text-sm font-bold mb-1">{result.isPositive ? ' ' : ' '}</p>
                        <p className={`text-4xl font-black ${result.isPositive ? 'text-amber-700' : 'text-red-600'}`}>{result.roi.toFixed(1)}%</p>
                        <p className="text-sm text-slate-500">  {result.annualRoi.toFixed(1)}%</p>
                    </div>
                    <div className="bg-white rounded-2xl p-4 space-y-2 text-sm">
                        <div className="flex justify-between"><span className="text-slate-500"> </span><span className="font-bold">{formatCompactNumber(result.totalInvestment)}</span></div>
                        <div className="flex justify-between"><span className="text-slate-500">{result.isPositive ? ' ' : ' '}</span><span className={`font-bold ${result.isPositive ? 'text-green-600' : 'text-red-600'}`}>{result.isPositive ? '+' : ''}{formatCompactNumber(result.profit)}</span></div>
                    </div>
                </div>
            ) : (
                <div className="text-center py-20 text-slate-300 bg-white rounded-[2rem] border-2 border-dashed border-slate-200">    </div>
            )}
        </div>
    );
};
