
import React, { useState, useMemo } from 'react';
import { Calculator } from 'lucide-react';
import { parseNumber, formatCompactNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const TaxOptimizer = () => {
    const [purchasePrice, setPurchasePrice] = useState('');
    const [purchaseDate, setPurchaseDate] = useState('');
    const [expectedSalePrice, setExpectedSalePrice] = useState('');
    const [isOneHouse, setIsOneHouse] = useState(true);
    const [isRegulated, setIsRegulated] = useState(false);

    const result = useMemo(() => {
        const purchase = parseNumber(purchasePrice);
        const sale = parseNumber(expectedSalePrice);
        const pDate = purchaseDate ? new Date(purchaseDate) : null;

        if (!purchase || !sale || !pDate) return null;

        const now = new Date();
        const holdingMonths = Math.floor((now - pDate) / (1000 * 60 * 60 * 24 * 30));
        const holdingYears = holdingMonths / 12;

        const gain = sale - purchase;
        if (gain <= 0) return { gain: 0, scenarios: [], message: '    .' };

        // 1 1   (2  ,  )
        const isTaxExempt = isOneHouse && holdingYears >= 2 && gain <= 1200000000; // 12 

        //  
        const getLongTermDeduction = (years, isRegulatedArea) => {
            if (years < 3) return 0;
            if (isRegulatedArea) {
                // :  4% ( 40%)
                return Math.min(years * 4, 40);
            } else {
                // : 3 24% +   4% ( 80%)
                if (years >= 3 && years < 4) return 24;
                return Math.min(24 + (years - 3) * 4, 80);
            }
        };

        //  (2026 )
        const getTaxRate = (taxableGain) => {
            if (taxableGain <= 14000000) return { rate: 6, deduction: 0 };
            if (taxableGain <= 50000000) return { rate: 15, deduction: 1260000 };
            if (taxableGain <= 88000000) return { rate: 24, deduction: 5760000 };
            if (taxableGain <= 150000000) return { rate: 35, deduction: 15440000 };
            if (taxableGain <= 300000000) return { rate: 38, deduction: 19940000 };
            if (taxableGain <= 500000000) return { rate: 40, deduction: 25940000 };
            if (taxableGain <= 1000000000) return { rate: 42, deduction: 35940000 };
            return { rate: 45, deduction: 65940000 };
        };

        //   
        const calculateTax = (years) => {
            if (isTaxExempt && years >= 2) return { tax: 0, deductionRate: 0, isExempt: true };

            const deductionRate = getLongTermDeduction(years, isRegulated);
            const deduction = gain * (deductionRate / 100);
            const taxableGain = gain - deduction - 2500000; //  250
            const taxInfo = getTaxRate(Math.max(0, taxableGain));
            const tax = Math.max(0, Math.floor(taxableGain * (taxInfo.rate / 100) - taxInfo.deduction));

            return { tax, deductionRate, taxableGain, isExempt: false };
        };

        //  +   
        const currentYears = Math.floor(holdingYears);
        const scenarios = [];

        for (let addYears = 0; addYears <= 5; addYears++) {
            const targetYears = currentYears + addYears;
            const result = calculateTax(targetYears);
            scenarios.push({
                years: targetYears,
                label: addYears === 0 ? '' : `+${addYears} `,
                ...result
            });
        }

        //    
        const optimal = scenarios.reduce((best, s) => s.tax < best.tax ? s : best, scenarios[0]);

        return {
            gain,
            holdingYears: holdingYears.toFixed(1),
            holdingMonths,
            isTaxExempt,
            scenarios,
            optimal
        };
    }, [purchasePrice, purchaseDate, expectedSalePrice, isOneHouse, isRegulated]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <Calculator className="w-5 h-5 mr-3 text-amber-600" />  
                </h3>
                <div className="space-y-5">
                    <NumberInput label="" value={purchasePrice} onChange={setPurchasePrice} color="blue" />
                    <div>
                        <label className="text-xs font-bold text-slate-500 uppercase tracking-widest block mb-2 ml-1"></label>
                        <input type="date" value={purchaseDate} onChange={(e) => setPurchaseDate(e.target.value)} className="w-full px-4 py-3 bg-slate-50 rounded-2xl font-bold text-slate-700 outline-none focus:ring-2 focus:ring-amber-500" />
                    </div>
                    <NumberInput label=" " value={expectedSalePrice} onChange={setExpectedSalePrice} color="green" />
                    <div className="flex gap-3">
                        <button
                            onClick={() => setIsOneHouse(!isOneHouse)}
                            className={`flex-1 py-3 rounded-xl font-bold text-sm transition-all ${isOneHouse ? 'bg-amber-500 text-white' : 'bg-slate-100 text-slate-500'}`}
                        >
                            1 1
                        </button>
                        <button
                            onClick={() => setIsRegulated(!isRegulated)}
                            className={`flex-1 py-3 rounded-xl font-bold text-sm transition-all ${isRegulated ? 'bg-red-500 text-white' : 'bg-slate-100 text-slate-500'}`}
                        >
                            
                        </button>
                    </div>
                </div>
            </div>

            {result ? (
                result.message ? (
                    <div className="bg-green-50 p-6 rounded-[2rem] border border-green-200 text-center">
                        <p className="text-lg font-bold text-green-700"> {result.message}</p>
                    </div>
                ) : (
                    <div className="space-y-4">
                        {/*   */}
                        <div className="bg-gradient-to-br from-amber-50 to-yellow-50 p-5 rounded-2xl border border-amber-100">
                            <div className="flex justify-between items-center mb-4">
                                <div>
                                    <p className="text-xs font-bold text-amber-600"> </p>
                                    <p className="text-2xl font-black text-amber-800">{result.holdingYears}</p>
                                </div>
                                <div className="text-right">
                                    <p className="text-xs font-bold text-slate-500"> </p>
                                    <p className="text-xl font-black text-slate-800">{formatCompactNumber(result.gain)}</p>
                                </div>
                            </div>
                            {result.isTaxExempt && (
                                <div className="p-3 bg-green-100 rounded-xl text-center">
                                    <p className="text-sm font-bold text-green-700"> 1 1   !</p>
                                </div>
                            )}
                        </div>

                        {/*   */}
                        <div className="bg-white p-5 rounded-2xl shadow-sm border border-slate-100">
                            <h4 className="text-sm font-bold text-slate-600 mb-4">    </h4>
                            <div className="space-y-2">
                                {result.scenarios.map((s, i) => (
                                    <div
                                        key={i}
                                        className={`p-3 rounded-xl flex justify-between items-center ${s === result.optimal ? 'bg-emerald-50 border border-emerald-200' : 'bg-slate-50'}`}
                                    >
                                        <div>
                                            <span className="text-sm font-bold text-slate-700">{s.label}</span>
                                            <span className="text-xs text-slate-400 ml-2">({s.years} )</span>
                                            {s === result.optimal && <span className="text-xs text-emerald-600 ml-2 font-bold"> </span>}
                                        </div>
                                        <div className="text-right">
                                            <p className={`font-bold ${s.isExempt ? 'text-green-600' : 'text-slate-800'}`}>
                                                {s.isExempt ? '' : formatCompactNumber(s.tax)}
                                            </p>
                                            {!s.isExempt && s.deductionRate > 0 && (
                                                <p className="text-xs text-slate-400"> {s.deductionRate}%</p>
                                            )}
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>

                        {/*   */}
                        <div className="p-4 bg-indigo-50 rounded-2xl">
                            <p className="text-sm font-bold text-indigo-700 mb-2">  </p>
                            <ul className="text-xs text-indigo-600 space-y-1">
                                <li> 2     </li>
                                <li> 1 1 2 +  </li>
                                <li> :  80%  (10 )</li>
                            </ul>
                        </div>
                    </div>
                )
            ) : (
                <div className="text-center py-20 text-slate-300 bg-white rounded-[2rem] border-2 border-dashed border-slate-200">, ,   </div>
            )}
        </div>
    );
};
