
import React, { useState, useEffect } from 'react';
import { Wifi, WifiOff } from 'lucide-react';

export const OfflineIndicator = () => {
    const [isOnline, setIsOnline] = useState(navigator.onLine);
    const [showBanner, setShowBanner] = useState(false);

    useEffect(() => {
        const handleOnline = () => {
            setIsOnline(true);
            setShowBanner(true);
            setTimeout(() => setShowBanner(false), 3000);
        };
        const handleOffline = () => {
            setIsOnline(false);
            setShowBanner(true);
        };

        window.addEventListener('online', handleOnline);
        window.addEventListener('offline', handleOffline);

        return () => {
            window.removeEventListener('online', handleOnline);
            window.removeEventListener('offline', handleOffline);
        };
    }, []);

    if (!showBanner && isOnline) return null;

    return (
        <div className={`fixed top-16 left-0 right-0 z-50 flex justify-center pointer-events-none animate-slide-up`}>
            <div className={`mx-4 px-4 py-2 rounded-full shadow-lg flex items-center gap-2 text-sm font-bold ${isOnline
                ? 'bg-green-500 text-white'
                : 'bg-slate-800 text-white'
                }`}>
                {isOnline ? (
                    <>
                        <Wifi size={16} />
                         
                    </>
                ) : (
                    <>
                        <WifiOff size={16} />
                          -    
                    </>
                )}
            </div>
        </div>
    );
};
