
import React, { useState, useMemo } from 'react';
import { Lightbulb } from 'lucide-react';
import { parseNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const InvestmentRecommender = () => {
    const [profile, setProfile] = useState({
        budget: '',
        riskTolerance: 'medium',
        investmentGoal: 'income',
        timeHorizon: '5'
    });

    const recommendation = useMemo(() => {
        const budget = parseNumber(profile.budget);
        if (!budget) return null;

        const recommendations = [];

        //  
        if (budget < 30000) {
            recommendations.push({ type: ' ', desc: '   ', risk: '', expectedYield: '8-12%' });
            recommendations.push({ type: '', desc: '  ', risk: '', expectedYield: '4-6%' });
        } else if (budget < 70000) {
            recommendations.push({ type: ' ', desc: '  ', risk: '-', expectedYield: '3-5%' });
            recommendations.push({ type: '', desc: ' ', risk: '-', expectedYield: '5-8%' });
        } else {
            recommendations.push({ type: ' ', desc: '  ', risk: '', expectedYield: '2-4%' });
            recommendations.push({ type: '', desc: ' ', risk: '', expectedYield: '4-6%' });
        }

        //   
        if (profile.investmentGoal === 'income') {
            recommendations.push({ type: ' ', desc: '   ', risk: '-', expectedYield: '5-7%' });
        } else {
            recommendations.push({ type: '', desc: '   ', risk: '', expectedYield: '10-20%' });
        }

        return recommendations;
    }, [profile]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <Lightbulb className="w-5 h-5 mr-3 text-yellow-500" />   
                </h3>
                <div className="space-y-4">
                    <NumberInput label=" " value={profile.budget} onChange={(v) => setProfile({ ...profile, budget: v })} color="blue" />
                    <div>
                        <label className="text-xs font-bold text-slate-500 block mb-2"> </label>
                        <div className="flex gap-2">
                            <button onClick={() => setProfile({ ...profile, investmentGoal: 'income' })} className={`flex-1 py-3 rounded-xl font-bold text-sm ${profile.investmentGoal === 'income' ? 'bg-yellow-500 text-white' : 'bg-slate-100 text-slate-500'}`}>
                                 
                            </button>
                            <button onClick={() => setProfile({ ...profile, investmentGoal: 'capital' })} className={`flex-1 py-3 rounded-xl font-bold text-sm ${profile.investmentGoal === 'capital' ? 'bg-yellow-500 text-white' : 'bg-slate-100 text-slate-500'}`}>
                                 
                            </button>
                        </div>
                    </div>
                    <div>
                        <label className="text-xs font-bold text-slate-500 block mb-2">  </label>
                        <div className="flex gap-2">
                            {['low', 'medium', 'high'].map(v => (
                                <button key={v} onClick={() => setProfile({ ...profile, riskTolerance: v })} className={`flex-1 py-2 rounded-xl text-sm font-bold ${profile.riskTolerance === v ? 'bg-indigo-500 text-white' : 'bg-slate-100 text-slate-500'}`}>
                                    {v === 'low' ? '' : v === 'medium' ? '' : ''}
                                </button>
                            ))}
                        </div>
                    </div>
                </div>
            </div>

            {recommendation && (
                <div className="space-y-3">
                    <h4 className="text-sm font-bold text-slate-600 px-1">   </h4>
                    {recommendation.map((rec, i) => (
                        <div key={i} className="bg-white p-5 rounded-2xl shadow-sm border border-slate-100">
                            <div className="flex justify-between items-start mb-2">
                                <h5 className="font-bold text-slate-800">{rec.type}</h5>
                                <span className="text-xs font-bold text-emerald-600 bg-emerald-50 px-2 py-1 rounded-lg">{rec.expectedYield}</span>
                            </div>
                            <p className="text-sm text-slate-500">{rec.desc}</p>
                            <p className="text-xs text-slate-400 mt-2">: {rec.risk}</p>
                        </div>
                    ))}
                </div>
            )}
        </div>
    );
};
