// LocalStorage cache to minimize redundant access
const localStorageCache = {};

export const getSafeLocalStorage = (key, defaultValue = '') => {
    // Check cache first
    if (localStorageCache[key] !== undefined) {
        return localStorageCache[key];
    }
    try {
        const saved = localStorage.getItem(key);
        const value = (saved === null || saved === '') ? defaultValue : saved;
        localStorageCache[key] = value;
        return value;
    } catch (e) {
        console.warn('localStorage access failed:', e);
        return defaultValue;
    }
};

// Check localStorage quota and cleanup if approaching limit
const checkAndCleanupStorage = () => {
    try {
        let totalSize = 0;
        for (let key in localStorage) {
            if (localStorage.hasOwnProperty(key)) {
                totalSize += (localStorage[key].length + key.length) * 2; // UTF-16
            }
        }
        const limitBytes = 5 * 1024 * 1024; // 5MB typical limit
        if (totalSize > limitBytes * 0.85) { // 85% threshold
            cleanupOldStorageData();
        }
    } catch (e) {
        // Ignore quota check errors
    }
};

// Cleanup old storage data to free space
const cleanupOldStorageData = () => {
    try {
        // Clean old history entries
        const history = JSON.parse(localStorage.getItem('calcHistory') || '[]');
        if (history.length > 10) {
            localStorage.setItem('calcHistory', JSON.stringify(history.slice(0, 10)));
        }
        // Clean old error logs
        const errorLog = JSON.parse(localStorage.getItem('errorLog') || '[]');
        if (errorLog.length > 5) {
            localStorage.setItem('errorLog', JSON.stringify(errorLog.slice(0, 5)));
        }
        // Clear cache to reflect changes
        delete localStorageCache['calcHistory'];
        delete localStorageCache['errorLog'];
    } catch (e) {
        // Ignore cleanup errors
    }
};

export const setSafeLocalStorage = (key, value) => {
    try {
        // value null  
        if (value === null) {
            localStorage.removeItem(key);
            delete localStorageCache[key];
            return;
        }

        // Check quota before writing
        checkAndCleanupStorage();
        const stringValue = typeof value === 'string' ? value : JSON.stringify(value);
        localStorage.setItem(key, stringValue);
        localStorageCache[key] = stringValue; // Update cache
    } catch (e) {
        if (e.name === 'QuotaExceededError') {
            // Auto-cleanup and retry
            cleanupOldStorageData();
            try {
                const stringValue = typeof value === 'string' ? value : JSON.stringify(value);
                localStorage.setItem(key, stringValue);
                localStorageCache[key] = stringValue;
            } catch (retryError) {
                console.warn('localStorage write failed after cleanup:', retryError);
            }
        } else {
            console.warn('localStorage write failed:', e);
        }
    }
};

// Debounced localStorage write (reduces write frequency)
export const debouncedSetStorage = (() => {
    const timers = {};
    return (key, value, delay = 500) => {
        clearTimeout(timers[key]);
        timers[key] = setTimeout(() => setSafeLocalStorage(key, value), delay);
    };
})();

// ==========    ==========
export const CALC_HISTORY_KEY = 'calcHistory';
export const MAX_HISTORY_ITEMS = 50;

export const saveToHistory = (type, data) => {
    try {
        const historyRaw = getSafeLocalStorage(CALC_HISTORY_KEY, '[]');
        const history = JSON.parse(historyRaw) || [];
        const newEntry = {
            id: Date.now(),
            type,
            data,
            timestamp: new Date().toISOString(),
            date: new Date().toLocaleDateString('ko-KR')
        };
        const updated = [newEntry, ...history].slice(0, MAX_HISTORY_ITEMS);
        setSafeLocalStorage(CALC_HISTORY_KEY, JSON.stringify(updated));
    } catch (e) {
        console.warn('  :', e);
    }
};

export const getHistory = () => {
    try {
        const historyRaw = getSafeLocalStorage(CALC_HISTORY_KEY, '[]');
        return JSON.parse(historyRaw) || [];
    } catch (e) {
        return [];
    }
};

export const clearHistory = () => {
    setSafeLocalStorage(CALC_HISTORY_KEY, '[]');
};
