
import React, { useState, useMemo } from 'react';
import { Gavel } from 'lucide-react';
import { parseNumber, formatCompactNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const AuctionYieldAnalyzer = () => {
    const [bidPrice, setBidPrice] = useState('');
    const [acquisitionCost, setAcquisitionCost] = useState('');
    const [repairCost, setRepairCost] = useState('');
    const [evictionCost, setEvictionCost] = useState('');
    const [expectedSalePrice, setExpectedSalePrice] = useState('');
    const [expectedMonthlyRent, setExpectedMonthlyRent] = useState('');

    const result = useMemo(() => {
        const bid = parseNumber(bidPrice);
        const acq = parseNumber(acquisitionCost);
        const repair = parseNumber(repairCost);
        const eviction = parseNumber(evictionCost);
        const sale = parseNumber(expectedSalePrice);
        const rent = parseNumber(expectedMonthlyRent);
        if (!bid) return null;

        const totalInvestment = bid + acq + repair + eviction;
        const saleProfit = sale ? sale - totalInvestment : 0;
        const saleYield = sale ? ((saleProfit / totalInvestment) * 100).toFixed(1) : 0;
        const annualRent = rent * 12;
        const rentalYield = totalInvestment > 0 ? ((annualRent / totalInvestment) * 100).toFixed(2) : 0;

        return { totalInvestment, saleProfit, saleYield, annualRent, rentalYield };
    }, [bidPrice, acquisitionCost, repairCost, evictionCost, expectedSalePrice, expectedMonthlyRent]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <Gavel className="w-5 h-5 mr-3 text-amber-600" />   
                </h3>
                <div className="space-y-4">
                    <NumberInput label="" value={bidPrice} onChange={setBidPrice} color="amber" />
                    <NumberInput label=" (, )" value={acquisitionCost} onChange={setAcquisitionCost} color="slate" />
                    <NumberInput label="" value={repairCost} onChange={setRepairCost} color="slate" />
                    <NumberInput label="" value={evictionCost} onChange={setEvictionCost} color="slate" />
                    <div className="h-px bg-slate-200"></div>
                    <NumberInput label=" " value={expectedSalePrice} onChange={setExpectedSalePrice} color="green" />
                    <NumberInput label=" " value={expectedMonthlyRent} onChange={setExpectedMonthlyRent} color="blue" />
                </div>
            </div>
            {result && (
                <div className="space-y-4">
                    <div className="bg-amber-50 p-5 rounded-2xl border border-amber-200">
                        <p className="text-xs font-bold text-amber-600 mb-1"> </p>
                        <p className="text-2xl font-black text-amber-800">{formatCompactNumber(result.totalInvestment)}</p>
                    </div>
                    <div className="grid grid-cols-2 gap-3">
                        <div className="bg-green-50 p-4 rounded-2xl border border-green-200 text-center">
                            <p className="text-xs font-bold text-green-600 mb-1"> </p>
                            <p className="text-xl font-black text-green-700">{result.saleYield}%</p>
                            <p className="text-xs text-slate-500">{formatCompactNumber(result.saleProfit)}</p>
                        </div>
                        <div className="bg-blue-50 p-4 rounded-2xl border border-blue-200 text-center">
                            <p className="text-xs font-bold text-blue-600 mb-1"> </p>
                            <p className="text-xl font-black text-blue-700">{result.rentalYield}%</p>
                            <p className="text-xs text-slate-500">{formatCompactNumber(result.annualRent)}/</p>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};
