import React, { useState, useMemo } from 'react';
import { parseNumber, formatNumber, formatCompactNumber } from '../../utils/math.js';
import { Home } from '../ui/Icons.js';
import { NumberInput } from '../ui/Inputs.js';

// NEW:  
export const ReverseRentTab = () => {
    const [propertyValue, setPropertyValue] = useState('');
    const [age, setAge] = useState('65');
    const [pensionType, setPensionType] = useState('lifetime');

    const result = useMemo(() => {
        const property = parseNumber(propertyValue);
        //   : 55~84 ( )
        const ageNum = Math.min(Math.max(parseInt(age) || 55, 55), 84);
        if (!property || ageNum < 55) return null;

        //     ( )
        //   (100  )
        const payRates = {
            55: 0.0018, 60: 0.0022, 65: 0.0028, 70: 0.0037, 75: 0.0050, 80: 0.0070
        };
        const nearestAge = [55, 60, 65, 70, 75, 80].reduce((prev, curr) =>
            Math.abs(curr - ageNum) < Math.abs(prev - ageNum) ? curr : prev
        );
        const baseRate = payRates[nearestAge] || 0.0028;

        //   (12 )
        const cappedProperty = Math.min(property, 1200000000);

        let monthlyPension = Math.floor(cappedProperty * baseRate);
        if (pensionType === 'fixedTerm') monthlyPension = Math.floor(monthlyPension * 1.3); //  30%  

        const annualPension = monthlyPension * 12;
        const totalPension10Y = annualPension * 10;

        return { monthlyPension, annualPension, totalPension10Y, cappedProperty };
    }, [propertyValue, age, pensionType]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <Home className="w-5 h-5 mr-3 text-amber-600" />  
                </h3>
                <div className="space-y-5">
                    <NumberInput label=" " value={propertyValue} onChange={setPropertyValue} color="orange" />
                    <div>
                        <label className="text-xs font-bold text-slate-500 uppercase tracking-widest block mb-2 ml-1"> </label>
                        <input
                            type="number"
                            value={age}
                            onChange={(e) => setAge(e.target.value)}
                            min="55"
                            max="84"
                            className={`w-full px-4 py-3 text-right bg-slate-50 rounded-2xl font-bold text-slate-700 outline-none focus:ring-2 transition-all ${parseInt(age) < 55 || parseInt(age) > 84
                                ? 'ring-2 ring-red-400 bg-red-50'
                                : 'focus:ring-amber-500'
                                }`}
                            aria-describedby="age-validation-msg"
                        />
                        {parseInt(age) < 55 ? (
                            <p id="age-validation-msg" className="text-xs text-red-500 mt-1 ml-1 font-bold">  55   </p>
                        ) : parseInt(age) > 84 ? (
                            <p id="age-validation-msg" className="text-xs text-red-500 mt-1 ml-1 font-bold">  84   </p>
                        ) : (
                            <p id="age-validation-msg" className="text-xs text-slate-400 mt-1 ml-1"> 55 ~ 84  </p>
                        )}
                    </div>
                    <div className="flex gap-2">
                        <button onClick={() => setPensionType('lifetime')} className={`flex-1 py-3 rounded-xl text-sm font-bold transition-all ${pensionType === 'lifetime' ? 'bg-amber-500 text-white' : 'bg-slate-100 text-slate-500'}`}></button>
                        <button onClick={() => setPensionType('fixedTerm')} className={`flex-1 py-3 rounded-xl text-sm font-bold transition-all ${pensionType === 'fixedTerm' ? 'bg-amber-500 text-white' : 'bg-slate-100 text-slate-500'}`}></button>
                    </div>
                </div>
            </div>

            {result ? (
                <div className="bg-gradient-to-br from-amber-50 to-orange-50 p-6 rounded-[2.5rem] border border-amber-100 shadow-lg">
                    <div className="text-center mb-6">
                        <p className="text-sm font-bold text-amber-500 uppercase tracking-widest mb-2">  </p>
                        <p className="text-4xl font-black text-amber-900">{formatNumber(result.monthlyPension)}</p>
                    </div>
                    <div className="bg-white rounded-2xl p-4 space-y-2 text-sm">
                        <div className="flex justify-between"><span className="text-slate-500"> </span><span className="font-bold">{formatCompactNumber(result.annualPension)}</span></div>
                        <div className="flex justify-between"><span className="text-slate-500">10  </span><span className="font-bold">{formatCompactNumber(result.totalPension10Y)}</span></div>
                        <div className="flex justify-between"><span className="text-slate-500"> </span><span className="font-bold">{formatCompactNumber(result.cappedProperty)}</span></div>
                    </div>
                    <p className="text-xs text-center text-amber-400 mt-4">* HF    </p>
                </div>
            ) : (
                <div className="text-center py-20 text-slate-300 bg-white rounded-[2rem] border-2 border-dashed border-slate-200">   </div>
            )}
        </div>
    );
};
