
import React, { useState, useMemo } from 'react';
import { TrendingUp } from 'lucide-react';
import { parseNumber, formatNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const InterestRateCutSimulator = () => {
    const [loanAmount, setLoanAmount] = useState('');
    const [currentRate, setCurrentRate] = useState('4.5');
    const [period, setPeriod] = useState('360');

    const result = useMemo(() => {
        const amount = parseNumber(loanAmount);
        const rate = parseFloat(currentRate) || 0;
        const months = parseInt(period) || 360;
        if (amount <= 0 || rate <= 0) return null;

        const calcMonthly = (r) => {
            const monthlyRate = r / 100 / 12;
            if (monthlyRate <= 0) return 0;
            const factor = Math.pow(1 + monthlyRate, months);
            return (amount * monthlyRate * factor) / (factor - 1);
        };

        const scenarios = [0, 0.25, 0.5, 1.0, 1.5].map(cut => {
            const newRate = Math.max(rate - cut, 0.5);
            const monthly = calcMonthly(newRate);
            const baseMonthly = calcMonthly(rate);
            return { cut, rate: newRate, monthly, saved: baseMonthly - monthly, yearlySaved: (baseMonthly - monthly) * 12 };
        });

        return scenarios;
    }, [loanAmount, currentRate, period]);

    return (
        <div className="bg-white p-6 rounded-[2rem] shadow-sm border border-slate-100 card-depth mt-2">
            <h4 className="text-sm font-bold text-slate-800 mb-4 flex items-center">
                <TrendingUp className="w-4 h-4 mr-2 text-cyan-500" />   
            </h4>
            <div className="space-y-4">
                <NumberInput label=" " value={loanAmount} onChange={setLoanAmount} color="cyan" />
                <div className="grid grid-cols-2 gap-3">
                    <div><label className="text-xs font-bold text-slate-500 block mb-1">  (%)</label><input type="number" value={currentRate} onChange={e => setCurrentRate(e.target.value)} step="0.1" className="w-full px-4 py-3 bg-slate-50 rounded-xl text-right font-bold outline-none" /></div>
                    <div><label className="text-xs font-bold text-slate-500 block mb-1">  ()</label><input type="number" value={period} onChange={e => setPeriod(e.target.value)} className="w-full px-4 py-3 bg-slate-50 rounded-xl text-right font-bold outline-none" /></div>
                </div>
            </div>
            {result && (
                <div className="mt-4 space-y-2">
                    {result.map((s, idx) => (
                        <div key={idx} className={`p-4 rounded-xl flex justify-between items-center ${idx === 0 ? 'bg-slate-100' : 'bg-cyan-50'}`}>
                            <div>
                                <p className="font-bold text-sm">{s.cut === 0 ? '' : `-${s.cut}%p `}</p>
                                <p className="text-xs text-slate-500"> {s.rate.toFixed(2)}%</p>
                            </div>
                            <div className="text-right">
                                <p className="text-lg font-black text-slate-800">{formatNumber(Math.round(s.monthly))}</p>
                                {s.saved > 0 && <p className="text-xs text-cyan-600 font-bold"> -{formatNumber(Math.round(s.saved))}</p>}
                            </div>
                        </div>
                    ))}
                    <p className="text-xs text-center text-slate-400 mt-2">       </p>
                </div>
            )}
        </div>
    );
};
