
import React, { useState, useMemo } from 'react';
import { Home } from 'lucide-react';
import { parseNumber, formatCompactNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const EvictionCostCalculator = () => {
    const [occupantType, setOccupantType] = useState('tenant');
    const [monthlyRent, setMonthlyRent] = useState('');
    const [movingDifficulty, setMovingDifficulty] = useState('normal');

    const result = useMemo(() => {
        const rent = parseNumber(monthlyRent) || 0;
        let baseCost = 0;
        let legalCost = 500; //   50

        if (occupantType === 'tenant') {
            baseCost = rent * (movingDifficulty === 'easy' ? 2 : movingDifficulty === 'normal' ? 4 : 6);
        } else if (occupantType === 'owner') {
            baseCost = rent * (movingDifficulty === 'easy' ? 3 : movingDifficulty === 'normal' ? 6 : 10);
            legalCost = 1000;
        } else {
            baseCost = rent * (movingDifficulty === 'easy' ? 1 : movingDifficulty === 'normal' ? 3 : 5);
        }

        return {
            movingCost: baseCost,
            legalCost,
            total: baseCost + legalCost,
            range: { min: Math.round((baseCost + legalCost) * 0.7), max: Math.round((baseCost + legalCost) * 1.3) }
        };
    }, [occupantType, monthlyRent, movingDifficulty]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <Home className="w-5 h-5 mr-3 text-orange-600" />  
                </h3>
                <div className="space-y-4">
                    <div>
                        <label className="text-xs font-bold text-slate-500 block mb-2"> </label>
                        <div className="flex gap-2">
                            {[['tenant', ''], ['owner', ''], ['other', '']].map(([v, l]) => (
                                <button key={v} onClick={() => setOccupantType(v)} className={`flex-1 py-2 rounded-xl text-sm font-bold ${occupantType === v ? 'bg-orange-500 text-white' : 'bg-slate-100'}`}>{l}</button>
                            ))}
                        </div>
                    </div>
                    <NumberInput label="  ( )" value={monthlyRent} onChange={setMonthlyRent} color="orange" />
                    <div>
                        <label className="text-xs font-bold text-slate-500 block mb-2"> </label>
                        <div className="flex gap-2">
                            {[['easy', ''], ['normal', ''], ['hard', '']].map(([v, l]) => (
                                <button key={v} onClick={() => setMovingDifficulty(v)} className={`flex-1 py-2 rounded-xl text-sm font-bold ${movingDifficulty === v ? 'bg-indigo-500 text-white' : 'bg-slate-100'}`}>{l}</button>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
            {result.total > 0 && (
                <div className="bg-gradient-to-br from-orange-50 to-amber-50 p-6 rounded-[2rem] border border-orange-100">
                    <div className="text-center mb-4">
                        <p className="text-xs font-bold text-orange-600 mb-1"> </p>
                        <p className="text-3xl font-black text-orange-800">{formatCompactNumber(result.total)}</p>
                        <p className="text-xs text-slate-500 mt-1">{formatCompactNumber(result.range.min)} ~ {formatCompactNumber(result.range.max)}</p>
                    </div>
                    <div className="bg-white/70 p-4 rounded-2xl space-y-2 text-sm">
                        <div className="flex justify-between"><span className="text-slate-500">/</span><span className="font-bold">{formatCompactNumber(result.movingCost)}</span></div>
                        <div className="flex justify-between"><span className="text-slate-500"></span><span className="font-bold">{formatCompactNumber(result.legalCost)}</span></div>
                    </div>
                </div>
            )}
        </div>
    );
};
