
import React, { useState, useMemo } from 'react';
import { Shield } from 'lucide-react';
import { parseNumber, formatNumber, formatKoreanNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const MortgageSettingCalculator = () => {
    const [loanAmount, setLoanAmount] = useState('');
    const [marginRate, setMarginRate] = useState('120');

    const result = useMemo(() => {
        const loan = parseNumber(loanAmount);
        const margin = parseFloat(marginRate) || 120;
        if (!loan) return null;
        const maxDebt = Math.round(loan * (margin / 100));
        const registrationFee = Math.round(maxDebt * 0.002); //  0.2%
        return { maxDebt, registrationFee, total: maxDebt };
    }, [loanAmount, marginRate]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <Shield className="w-5 h-5 mr-3 text-indigo-600" />   
                </h3>
                <div className="space-y-4">
                    <NumberInput label="" value={loanAmount} onChange={setLoanAmount} color="indigo" />
                    <div>
                        <label className="text-xs font-bold text-slate-500 block mb-2">  (%)</label>
                        <div className="flex gap-2">
                            {['110', '120', '130'].map(v => (
                                <button key={v} onClick={() => setMarginRate(v)} className={`flex-1 py-2 rounded-xl text-sm font-bold ${marginRate === v ? 'bg-indigo-500 text-white' : 'bg-slate-100'}`}>{v}%</button>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
            {result && (
                <div className="bg-indigo-50 p-6 rounded-[2rem] border border-indigo-200 text-center">
                    <p className="text-xs font-bold text-indigo-600 mb-1"></p>
                    <p className="text-3xl font-black text-indigo-800">{formatKoreanNumber(result.maxDebt)}</p>
                    <p className="text-xs text-slate-500 mt-2"> (): {formatNumber(result.registrationFee)}</p>
                </div>
            )}
        </div>
    );
};
