import { INSURANCE_RATES } from './constants.js';
import { safeDivide } from './math.js';

export const calculateInsuranceFee = (deposit, years = 1, type = 'default') => {
    const rate = INSURANCE_RATES[type] || INSURANCE_RATES.default;
    return Math.floor(deposit * rate * years);
};

//   (  )
export const calculateConversion = (deposit, rate) => {
    //  = (  ) / 12
    if (!rate || rate <= 0) return 0;
    return Math.floor(safeDivide(deposit * (rate / 100), 12, 0));
};

export const calculateDepositFromMonthly = (monthly, rate) => {
    //  = (  12) / 
    if (rate <= 0) return 0;
    return Math.floor((monthly * 12) / (rate / 100));
};
