
import React, { useState, useMemo } from 'react';
import { ClipboardList } from 'lucide-react';
import { parseNumber, formatNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const RegistrationFeeCalc = () => {
    const [salePrice, setSalePrice] = useState('');
    const [loanAmount, setLoanAmount] = useState('');

    const result = useMemo(() => {
        const price = parseNumber(salePrice);
        const loan = parseNumber(loanAmount);

        if (!price) return null;

        //  (: 2%)
        const registrationTax = Math.floor(price * 0.02);

        //  ( 20%)
        const localEduTax = Math.floor(registrationTax * 0.2);

        //   ( 15,000)
        const courtStamp = 15000;

        //   ( 30~50 )
        const lawyerFee = 400000;

        //  (  )
        let mortgageFee = 0;
        if (loan > 0) {
            //   (  120%)
            const maxLoan = loan * 1.2;
            mortgageFee = Math.floor(maxLoan * 0.002); //  0.2%
        }

        const total = registrationTax + localEduTax + courtStamp + lawyerFee + mortgageFee;

        return { registrationTax, localEduTax, courtStamp, lawyerFee, mortgageFee, total };
    }, [salePrice, loanAmount]);

    return (
        <div className="bg-white p-6 rounded-[2rem] shadow-sm border border-slate-100 mt-2">
            <h4 className="text-sm font-bold text-slate-800 mb-4 flex items-center">
                <ClipboardList className="w-4 h-4 mr-2 text-indigo-600" />  
            </h4>
            <div className="space-y-4">
                <NumberInput label="" value={salePrice} onChange={setSalePrice} color="violet" />
                <NumberInput label=" ()" value={loanAmount} onChange={setLoanAmount} color="violet" />

                {result && (
                    <div className="mt-4 p-4 bg-indigo-50 rounded-2xl space-y-2 text-sm">
                        <div className="flex justify-between text-slate-600">
                            <span></span>
                            <span>{formatNumber(result.registrationTax)}</span>
                        </div>
                        <div className="flex justify-between text-slate-600">
                            <span></span>
                            <span>{formatNumber(result.localEduTax)}</span>
                        </div>
                        <div className="flex justify-between text-slate-600">
                            <span> </span>
                            <span>{formatNumber(result.courtStamp)}</span>
                        </div>
                        <div className="flex justify-between text-slate-600">
                            <span>  ()</span>
                            <span> {formatNumber(result.lawyerFee)}</span>
                        </div>
                        {result.mortgageFee > 0 && (
                            <div className="flex justify-between text-slate-600">
                                <span> </span>
                                <span>{formatNumber(result.mortgageFee)}</span>
                            </div>
                        )}
                        <div className="h-px bg-indigo-200 my-2"></div>
                        <div className="flex justify-between font-bold text-indigo-800">
                            <span> </span>
                            <span> {formatNumber(result.total)}</span>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
};
