
import React, { useEffect, useRef, useMemo } from 'react';
import { PieChart } from 'lucide-react';
import { usePersistedState } from '../../utils/hooks';
import { formatCompactNumber } from '../../utils/formatters';

export const PortfolioDashboard = () => {
    const [properties] = usePersistedState('portfolio', []);
    const chartRef = useRef(null);
    const chartInstanceRef = useRef(null);

    const stats = useMemo(() => {
        if (properties.length === 0) return null;

        const totalValue = properties.reduce((sum, p) => sum + (p.price || 0), 0);
        const totalMonthlyRent = properties.reduce((sum, p) => sum + (p.monthlyRent || 0), 0);
        const annualIncome = totalMonthlyRent * 12;
        const avgYield = totalValue > 0 ? (annualIncome / totalValue) * 100 : 0;

        //  
        const byType = {};
        properties.forEach(p => {
            byType[p.type] = (byType[p.type] || 0) + (p.price || 0);
        });

        //  
        const byLocation = {};
        properties.forEach(p => {
            const loc = p.location || '';
            byLocation[loc] = (byLocation[loc] || 0) + (p.price || 0);
        });

        return {
            totalValue,
            totalMonthlyRent,
            annualIncome,
            avgYield: avgYield.toFixed(2),
            count: properties.length,
            byType,
            byLocation
        };
    }, [properties]);

    //  
    useEffect(() => {
        if (!stats || !chartRef.current || !window.Chart) return;

        if (chartInstanceRef.current) {
            chartInstanceRef.current.destroy();
        }

        const labels = Object.keys(stats.byType);
        const data = Object.values(stats.byType);
        const colors = ['#6366f1', '#8b5cf6', '#ec4899', '#f59e0b', '#10b981', '#3b82f6'];

        chartInstanceRef.current = new window.Chart(chartRef.current, {
            type: 'doughnut',
            data: {
                labels: labels,
                datasets: [{
                    data: data,
                    backgroundColor: colors.slice(0, labels.length),
                    borderWidth: 0
                }]
            },
            options: {
                responsive: true,
                maintainAspectRatio: false,
                plugins: {
                    legend: {
                        position: 'bottom',
                        labels: {
                            padding: 20,
                            usePointStyle: true,
                            font: { size: 11, weight: 'bold' }
                        }
                    },
                    tooltip: {
                        callbacks: {
                            label: (ctx) => {
                                const pct = ((ctx.raw / stats.totalValue) * 100).toFixed(1);
                                return `${ctx.label}: ${formatCompactNumber(ctx.raw)} (${pct}%)`;
                            }
                        }
                    }
                },
                cutout: '60%'
            }
        });

        return () => {
            if (chartInstanceRef.current) {
                chartInstanceRef.current.destroy();
            }
        };
    }, [stats]);

    if (!stats) {
        return (
            <div className="space-y-6 pb-32 animate-slide-up px-1">
                <div className="flex flex-col items-center justify-center py-20 bg-white rounded-3xl border-2 border-dashed border-slate-200">
                    <PieChart className="w-16 h-16 mb-4 text-slate-200" />
                    <p className="text-sm font-bold text-slate-400 mb-2"> </p>
                    <p className="text-xs text-slate-300">''   </p>
                </div>
            </div>
        );
    }

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            {/*   */}
            <div className="bg-gradient-to-br from-indigo-600 via-purple-600 to-pink-500 text-white p-6 rounded-3xl shadow-lg">
                <h3 className="text-indigo-200 text-xs font-bold uppercase tracking-wider mb-4">  </h3>
                <div className="grid grid-cols-2 gap-4">
                    <div className="bg-white/10 backdrop-blur rounded-2xl p-4">
                        <p className="text-white/70 text-xs font-bold mb-1"> </p>
                        <p className="text-2xl font-black">{formatCompactNumber(stats.totalValue)}</p>
                    </div>
                    <div className="bg-white/10 backdrop-blur rounded-2xl p-4">
                        <p className="text-white/70 text-xs font-bold mb-1"> </p>
                        <p className="text-2xl font-black">{formatCompactNumber(stats.totalMonthlyRent)}</p>
                    </div>
                    <div className="bg-white/10 backdrop-blur rounded-2xl p-4">
                        <p className="text-white/70 text-xs font-bold mb-1"> </p>
                        <p className="text-2xl font-black">{formatCompactNumber(stats.annualIncome)}</p>
                    </div>
                    <div className="bg-white/10 backdrop-blur rounded-2xl p-4">
                        <p className="text-white/70 text-xs font-bold mb-1"> </p>
                        <p className="text-2xl font-black">{stats.avgYield}%</p>
                    </div>
                </div>
            </div>

            {/*    */}
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h4 className="text-sm font-bold text-slate-600 mb-4">  </h4>
                <div className="h-64">
                    <canvas ref={chartRef}></canvas>
                </div>
            </div>

            {/*   */}
            <div className="bg-white p-5 rounded-2xl shadow-sm border border-slate-100">
                <h4 className="text-sm font-bold text-slate-600 mb-4">  </h4>
                <div className="space-y-2">
                    {Object.entries(stats.byLocation).map(([loc, value], i) => (
                        <div key={i} className="flex items-center gap-3">
                            <span className="text-sm text-slate-600 w-24 truncate">{loc}</span>
                            <div className="flex-1 bg-slate-100 rounded-full h-4 overflow-hidden">
                                <div
                                    className="h-full bg-gradient-to-r from-indigo-500 to-purple-500 rounded-full"
                                    style={{ width: `${(value / stats.totalValue) * 100}%` }}
                                />
                            </div>
                            <span className="text-sm font-bold text-slate-700 w-20 text-right">{formatCompactNumber(value)}</span>
                        </div>
                    ))}
                </div>
            </div>

            {/*   */}
            <div className="grid grid-cols-2 gap-3">
                <div className="bg-emerald-50 p-4 rounded-2xl text-center">
                    <p className="text-xs font-bold text-emerald-600 mb-1"> </p>
                    <p className="text-2xl font-black text-emerald-700">{stats.count}</p>
                </div>
                <div className="bg-purple-50 p-4 rounded-2xl text-center">
                    <p className="text-xs font-bold text-purple-600 mb-1"> </p>
                    <p className="text-2xl font-black text-purple-700">{Object.keys(stats.byType).length}</p>
                </div>
            </div>
        </div>
    );
};
