
import React, { useState, useMemo } from 'react';
import { PieChart } from 'lucide-react';
import { parseNumber, formatCompactNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const InvestmentNPVIRR = () => {
    const [initialInvestment, setInitialInvestment] = useState('');
    const [annualCashFlow, setAnnualCashFlow] = useState('');
    const [holdingPeriod, setHoldingPeriod] = useState('5');
    const [exitValue, setExitValue] = useState('');
    const [discountRate, setDiscountRate] = useState('5');

    const result = useMemo(() => {
        const investment = parseNumber(initialInvestment);
        const cashFlow = parseNumber(annualCashFlow);
        const years = parseInt(holdingPeriod) || 5;
        const exit = parseNumber(exitValue);
        const rate = parseFloat(discountRate) / 100 || 0.05;

        if (!investment || !cashFlow) return null;

        // NPV 
        let npv = -investment;
        for (let i = 1; i <= years; i++) {
            npv += cashFlow / Math.pow(1 + rate, i);
        }
        if (exit > 0) {
            npv += exit / Math.pow(1 + rate, years);
        }

        // IRR  (Newton-Raphson )
        const calculateIRR = () => {
            let irr = 0.1; //  10%
            const maxIterations = 100;
            const tolerance = 0.0001;

            for (let iter = 0; iter < maxIterations; iter++) {
                let npvCalc = -investment;
                let derivative = 0;

                for (let i = 1; i <= years; i++) {
                    npvCalc += cashFlow / Math.pow(1 + irr, i);
                    derivative -= i * cashFlow / Math.pow(1 + irr, i + 1);
                }
                if (exit > 0) {
                    npvCalc += exit / Math.pow(1 + irr, years);
                    derivative -= years * exit / Math.pow(1 + irr, years + 1);
                }

                if (Math.abs(npvCalc) < tolerance) break;
                if (derivative === 0) break;

                irr = irr - npvCalc / derivative;

                // IRR    
                if (irr < -0.99) irr = -0.99;
                if (irr > 10) irr = 10;
            }

            return irr * 100;
        };

        const irr = calculateIRR();
        const totalReturn = (cashFlow * years + exit) - investment;
        const roi = investment > 0 ? (totalReturn / investment) * 100 : 0;

        return {
            npv: Math.round(npv),
            irr: irr.toFixed(2),
            totalReturn: Math.round(totalReturn),
            roi: roi.toFixed(1),
            isPositiveNPV: npv > 0,
            years
        };
    }, [initialInvestment, annualCashFlow, holdingPeriod, exitValue, discountRate]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <PieChart className="w-5 h-5 mr-3 text-indigo-600" /> NPV/IRR 
                </h3>
                <div className="space-y-5">
                    <NumberInput label=" " value={initialInvestment} onChange={setInitialInvestment} color="blue" />
                    <NumberInput label="  ()" value={annualCashFlow} onChange={setAnnualCashFlow} color="green" />
                    <div className="flex gap-3">
                        <div className="flex-1">
                            <label className="text-xs font-bold text-slate-500 uppercase tracking-widest block mb-2 ml-1"> ()</label>
                            <input type="number" value={holdingPeriod} onChange={(e) => setHoldingPeriod(e.target.value)} className="w-full px-4 py-3 text-right bg-slate-50 rounded-2xl font-bold text-slate-700 outline-none focus:ring-2 focus:ring-indigo-500" />
                        </div>
                        <div className="flex-1">
                            <label className="text-xs font-bold text-slate-500 uppercase tracking-widest block mb-2 ml-1"> (%)</label>
                            <input type="number" value={discountRate} onChange={(e) => setDiscountRate(e.target.value)} className="w-full px-4 py-3 text-right bg-slate-50 rounded-2xl font-bold text-slate-700 outline-none focus:ring-2 focus:ring-indigo-500" />
                        </div>
                    </div>
                    <NumberInput label="  (Exit Value)" value={exitValue} onChange={setExitValue} color="purple" />
                </div>
            </div>

            {result ? (
                <div className="space-y-4">
                    {/* NPV  */}
                    <div className={`p-6 rounded-[2rem] shadow-lg ${result.isPositiveNPV ? 'bg-gradient-to-br from-indigo-50 to-purple-50 border border-indigo-100' : 'bg-gradient-to-br from-red-50 to-orange-50 border border-red-100'}`}>
                        <div className="grid grid-cols-2 gap-4">
                            <div className="text-center p-4 bg-white rounded-2xl">
                                <p className="text-xs font-bold text-slate-500 mb-1">NPV ()</p>
                                <p className={`text-2xl font-black ${result.isPositiveNPV ? 'text-indigo-700' : 'text-red-600'}`}>
                                    {formatCompactNumber(result.npv)}
                                </p>
                                <p className={`text-xs mt-1 ${result.isPositiveNPV ? 'text-emerald-600' : 'text-red-500'}`}>
                                    {result.isPositiveNPV ? '  ' : '  '}
                                </p>
                            </div>
                            <div className="text-center p-4 bg-white rounded-2xl">
                                <p className="text-xs font-bold text-slate-500 mb-1">IRR ()</p>
                                <p className="text-2xl font-black text-purple-700">{result.irr}%</p>
                                <p className="text-xs text-slate-400 mt-1"> </p>
                            </div>
                        </div>
                    </div>

                    {/*   */}
                    <div className="bg-white p-5 rounded-2xl shadow-sm border border-slate-100 space-y-3 text-sm">
                        <div className="flex justify-between">
                            <span className="text-slate-500">{result.years}  </span>
                            <span className={`font-bold ${result.totalReturn >= 0 ? 'text-emerald-600' : 'text-red-600'}`}>
                                {result.totalReturn >= 0 ? '+' : ''}{formatCompactNumber(result.totalReturn)}
                            </span>
                        </div>
                        <div className="flex justify-between">
                            <span className="text-slate-500"> ROI</span>
                            <span className="font-bold text-indigo-600">{result.roi}%</span>
                        </div>
                        <div className="h-px bg-slate-200"></div>
                        <div className="p-3 bg-slate-50 rounded-xl text-xs text-slate-500">
                             <strong>NPV </strong> = ({discountRate}%)   <br />
                             <strong>IRR</strong> =    
                        </div>
                    </div>
                </div>
            ) : (
                <div className="text-center py-20 text-slate-300 bg-white rounded-[2rem] border-2 border-dashed border-slate-200">   </div>
            )}
        </div>
    );
};
