
import React, { useState, useMemo } from 'react';
import { AlertTriangle } from 'lucide-react';

export const RiskScoreCalculator = () => {
    const [factors, setFactors] = useState({
        ltvRatio: '60',
        interestRate: '4.5',
        vacancyRisk: 'medium',
        regulation: 'normal',
        priceVolatility: 'medium',
        holdingPeriod: '5'
    });

    const score = useMemo(() => {
        let riskScore = 0;

        // LTV ( )
        const ltv = parseFloat(factors.ltvRatio) || 60;
        if (ltv > 70) riskScore += 30;
        else if (ltv > 50) riskScore += 15;
        else riskScore += 5;

        //  ( )
        const rate = parseFloat(factors.interestRate) || 4.5;
        if (rate > 6) riskScore += 25;
        else if (rate > 4) riskScore += 15;
        else riskScore += 5;

        //  
        if (factors.vacancyRisk === 'high') riskScore += 20;
        else if (factors.vacancyRisk === 'medium') riskScore += 10;
        else riskScore += 3;

        // 
        if (factors.regulation === 'strict') riskScore += 15;
        else if (factors.regulation === 'normal') riskScore += 8;
        else riskScore += 2;

        //  
        if (factors.priceVolatility === 'high') riskScore += 15;
        else if (factors.priceVolatility === 'medium') riskScore += 8;
        else riskScore += 3;

        //  ( )
        const period = parseInt(factors.holdingPeriod) || 5;
        if (period < 2) riskScore += 10;
        else if (period < 5) riskScore += 5;
        else riskScore += 0;

        return Math.min(100, riskScore);
    }, [factors]);

    const getRiskLevel = () => {
        if (score <= 30) return { level: '', color: 'text-green-600', bg: 'bg-green-50', desc: ' ' };
        if (score <= 50) return { level: '', color: 'text-yellow-600', bg: 'bg-yellow-50', desc: '  ' };
        if (score <= 70) return { level: '', color: 'text-orange-600', bg: 'bg-orange-50', desc: ' ' };
        return { level: ' ', color: 'text-red-600', bg: 'bg-red-50', desc: '  ' };
    };

    const risk = getRiskLevel();

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <AlertTriangle className="w-5 h-5 mr-3 text-red-500" />   
                </h3>
                <div className="space-y-4">
                    <div>
                        <label className="text-xs font-bold text-slate-500 block mb-2">LTV  (%)</label>
                        <input type="number" value={factors.ltvRatio} onChange={(e) => setFactors({ ...factors, ltvRatio: e.target.value })} className="w-full p-3 bg-slate-50 rounded-xl" />
                    </div>
                    <div>
                        <label className="text-xs font-bold text-slate-500 block mb-2">  (%)</label>
                        <input type="number" step="0.1" value={factors.interestRate} onChange={(e) => setFactors({ ...factors, interestRate: e.target.value })} className="w-full p-3 bg-slate-50 rounded-xl" />
                    </div>
                    <div>
                        <label className="text-xs font-bold text-slate-500 block mb-2"> </label>
                        <div className="flex gap-2">
                            {['low', 'medium', 'high'].map(v => (
                                <button key={v} onClick={() => setFactors({ ...factors, vacancyRisk: v })} className={`flex-1 py-2 rounded-xl text-sm font-bold ${factors.vacancyRisk === v ? 'bg-indigo-500 text-white' : 'bg-slate-100 text-slate-500'}`}>
                                    {v === 'low' ? '' : v === 'medium' ? '' : ''}
                                </button>
                            ))}
                        </div>
                    </div>
                    <div>
                        <label className="text-xs font-bold text-slate-500 block mb-2"> </label>
                        <div className="flex gap-2">
                            {['none', 'normal', 'strict'].map(v => (
                                <button key={v} onClick={() => setFactors({ ...factors, regulation: v })} className={`flex-1 py-2 rounded-xl text-sm font-bold ${factors.regulation === v ? 'bg-indigo-500 text-white' : 'bg-slate-100 text-slate-500'}`}>
                                    {v === 'none' ? '' : v === 'normal' ? '' : ''}
                                </button>
                            ))}
                        </div>
                    </div>
                </div>
            </div>

            {/*   */}
            <div className={`p-8 rounded-[2rem] ${risk.bg} text-center`}>
                <p className="text-sm font-bold text-slate-500 mb-2">  </p>
                <p className={`text-6xl font-black ${risk.color}`}>{score}</p>
                <p className={`text-lg font-bold ${risk.color} mt-2`}>{risk.level}</p>
                <p className="text-sm text-slate-500 mt-1">{risk.desc}</p>
            </div>
        </div>
    );
};
