
import React, { useState, useMemo } from 'react';
import { TrendingUp } from 'lucide-react';
import { parseNumber, formatCompactNumber } from '../../utils/formatters';
import { NumberInput } from '../ui/Inputs';

export const RentalYieldTab = () => {
    const [purchasePrice, setPurchasePrice] = useState('');
    const [monthlyRent, setMonthlyRent] = useState('');
    const [jeonseDeposit, setJeonseDeposit] = useState('');
    const [vacancyRate, setVacancyRate] = useState('5');
    const [repairCost, setRepairCost] = useState('');
    const [propertyTax, setPropertyTax] = useState('');

    const result = useMemo(() => {
        const price = parseNumber(purchasePrice);
        const rent = parseNumber(monthlyRent);
        const deposit = parseNumber(jeonseDeposit);
        const vacancy = parseFloat(vacancyRate) / 100 || 0;
        const repair = parseNumber(repairCost);
        const tax = parseNumber(propertyTax);

        if (!price || !rent) return null;

        const annualRent = rent * 12;
        const effectiveRent = annualRent * (1 - vacancy);
        const totalCosts = repair + tax;
        const netIncome = effectiveRent - totalCosts;
        const investment = price - deposit;
        const grossYield = (annualRent / price) * 100;
        const netYield = investment > 0 ? (netIncome / investment) * 100 : 0;

        return { annualRent, effectiveRent, netIncome, grossYield, netYield, totalCosts };
    }, [purchasePrice, monthlyRent, jeonseDeposit, vacancyRate, repairCost, propertyTax]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <TrendingUp className="w-5 h-5 mr-3 text-green-600" />  
                </h3>
                <div className="space-y-5">
                    <NumberInput label="" value={purchasePrice} onChange={setPurchasePrice} color="green" />
                    <NumberInput label=" " value={monthlyRent} onChange={setMonthlyRent} color="green" />
                    <NumberInput label=" ()" value={jeonseDeposit} onChange={setJeonseDeposit} color="green" />
                    <div className="flex gap-3">
                        <div className="flex-1">
                            <label className="text-xs font-bold text-slate-500 block mb-1"> (%)</label>
                            <input type="number" value={vacancyRate} onChange={e => setVacancyRate(e.target.value)} className="w-full px-3 py-2 bg-slate-50 rounded-xl text-right font-bold outline-none" />
                        </div>
                        <div className="flex-1">
                            <NumberInput label=" " value={repairCost} onChange={setRepairCost} color="green" enableUnitToggle={false} showKorean={false} />
                        </div>
                    </div>
                    <NumberInput label=" " value={propertyTax} onChange={setPropertyTax} color="green" enableUnitToggle={false} showKorean={false} />
                </div>
            </div>
            {result ? (
                <div className="bg-gradient-to-br from-green-50 to-emerald-50 p-6 rounded-[2.5rem] border border-green-100 shadow-lg">
                    <div className="grid grid-cols-2 gap-4 mb-4">
                        <div className="text-center p-4 bg-white rounded-2xl">
                            <p className="text-xs font-bold text-slate-500 mb-1"></p>
                            <p className="text-2xl font-black text-green-600">{result.grossYield.toFixed(2)}%</p>
                        </div>
                        <div className="text-center p-4 bg-white rounded-2xl">
                            <p className="text-xs font-bold text-slate-500 mb-1"></p>
                            <p className="text-2xl font-black text-emerald-600">{result.netYield.toFixed(2)}%</p>
                        </div>
                    </div>
                    <div className="bg-white rounded-2xl p-4 space-y-2 text-sm">
                        <div className="flex justify-between"><span className="text-slate-500"> </span><span className="font-bold">{formatCompactNumber(result.annualRent)}</span></div>
                        <div className="flex justify-between"><span className="text-slate-500"> </span><span className="font-bold">{formatCompactNumber(result.effectiveRent)}</span></div>
                        <div className="flex justify-between"><span className="text-slate-500"> </span><span className="font-bold text-red-500">-{formatCompactNumber(result.totalCosts)}</span></div>
                        <div className="h-px bg-slate-200"></div>
                        <div className="flex justify-between font-bold"><span></span><span className="text-green-600">{formatCompactNumber(result.netIncome)}</span></div>
                    </div>
                </div>
            ) : (
                <div className="text-center py-20 text-slate-300 bg-white rounded-[2rem] border-2 border-dashed border-slate-200">   </div>
            )}
        </div>
    );
};
