import React, { useState, useMemo } from 'react';
import { parseNumber, formatNumber, formatCompactNumber } from '../../utils/math.js';
import { Users } from '../ui/Icons.js';
import { NumberInput } from '../ui/Inputs.js';

// NEW:  
export const InheritanceTaxTab = () => {
    const [totalAssets, setTotalAssets] = useState('');
    const [hasSpouse, setHasSpouse] = useState(true);
    const [childCount, setChildCount] = useState('2');
    const [debts, setDebts] = useState('');

    const result = useMemo(() => {
        const assets = parseNumber(totalAssets);
        const debt = parseNumber(debts);
        if (!assets) return null;

        const netAssets = assets - debt;
        if (netAssets <= 0) return { tax: 0, finalTax: 0, deductions: 0, taxableAmount: 0 };

        //  2
        let deductions = 200000000;
        //   ( 5,  30 -  )
        if (hasSpouse) deductions += Math.min(Math.max(netAssets * 0.5, 500000000), 3000000000);
        //   (1 5)
        deductions += parseInt(childCount) * 50000000;
        //  5 vs + 
        const basicTotal = 200000000 + parseInt(childCount) * 50000000;
        if (!hasSpouse && basicTotal < 500000000) deductions = 500000000;

        const taxableAmount = Math.max(netAssets - deductions, 0);

        //  ( )
        let tax = 0;
        if (taxableAmount <= 100000000) tax = taxableAmount * 0.1;
        else if (taxableAmount <= 500000000) tax = 10000000 + (taxableAmount - 100000000) * 0.2;
        else if (taxableAmount <= 1000000000) tax = 90000000 + (taxableAmount - 500000000) * 0.3;
        else if (taxableAmount <= 3000000000) tax = 240000000 + (taxableAmount - 1000000000) * 0.4;
        else tax = 1040000000 + (taxableAmount - 3000000000) * 0.5;

        const discount = Math.floor(tax * 0.03);
        const finalTax = Math.floor(tax - discount);

        return { netAssets, deductions, taxableAmount, tax: Math.floor(tax), discount, finalTax };
    }, [totalAssets, hasSpouse, childCount, debts]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <Users className="w-5 h-5 mr-3 text-indigo-600" />  
                </h3>
                <div className="space-y-5">
                    <NumberInput label=" " value={totalAssets} onChange={setTotalAssets} color="violet" />
                    <NumberInput label="  " value={debts} onChange={setDebts} color="violet" />
                    <label className="flex items-center gap-3 p-3 bg-slate-50 rounded-xl cursor-pointer">
                        <input type="checkbox" checked={hasSpouse} onChange={(e) => setHasSpouse(e.target.checked)} className="w-5 h-5 rounded" />
                        <span className="text-sm font-medium text-slate-700">  (  )</span>
                    </label>
                    <div>
                        <label className="text-xs font-bold text-slate-500 uppercase tracking-widest block mb-2 ml-1"> </label>
                        <div className="flex gap-2">
                            {['0', '1', '2', '3', '4'].map(n => (
                                <button key={n} onClick={() => setChildCount(n)} className={`flex-1 py-3 rounded-xl text-sm font-bold transition-all ${childCount === n ? 'bg-indigo-500 text-white' : 'bg-slate-100 text-slate-500'}`}>{n}</button>
                            ))}
                        </div>
                    </div>
                </div>
            </div>

            {result ? (
                <div className="bg-gradient-to-br from-indigo-50 to-blue-50 p-6 rounded-[2.5rem] border border-indigo-100 shadow-lg">
                    <div className="text-center mb-6">
                        <p className="text-sm font-bold text-indigo-400 uppercase tracking-widest mb-2"> </p>
                        <p className="text-4xl font-black text-indigo-900">{formatNumber(result.finalTax)}</p>
                    </div>
                    <div className="bg-white rounded-2xl p-4 space-y-2 text-sm">
                        <div className="flex justify-between"><span className="text-slate-500"> </span><span className="font-bold">{formatCompactNumber(result.netAssets)}</span></div>
                        <div className="flex justify-between"><span className="text-slate-500"> </span><span className="font-bold text-green-600">-{formatCompactNumber(result.deductions)}</span></div>
                        <div className="flex justify-between"><span className="text-slate-500"></span><span className="font-bold">{formatCompactNumber(result.taxableAmount)}</span></div>
                        <div className="flex justify-between"><span className="text-slate-500"> (3%)</span><span className="font-bold text-green-600">-{formatNumber(result.discount)}</span></div>
                    </div>
                </div>
            ) : (
                <div className="text-center py-20 text-slate-300 bg-white rounded-[2rem] border-2 border-dashed border-slate-200"> </div>
            )}
        </div>
    );
};
