import React, { useState, useMemo } from 'react';
import { parseNumber, formatNumber, formatCompactNumber } from '../../utils/math.js';
import { Banknote } from '../ui/Icons.js';
import { NumberInput } from '../ui/Inputs.js';

// NEW:  
export const GiftTaxTab = () => {
    const [giftAmount, setGiftAmount] = useState('');
    const [relationship, setRelationship] = useState('child');
    const [isGenerationSkip, setIsGenerationSkip] = useState(false);

    const result = useMemo(() => {
        const amount = parseNumber(giftAmount);
        if (!amount) return null;

        //  
        const exemptions = { spouse: 600000000, child: 50000000, grandchild: 50000000, other: 10000000 };
        const exemption = exemptions[relationship] || 0;
        const taxableAmount = Math.max(amount - exemption, 0);

        //  (2026 )
        let tax = 0;
        if (taxableAmount <= 100000000) tax = taxableAmount * 0.1;
        else if (taxableAmount <= 500000000) tax = 10000000 + (taxableAmount - 100000000) * 0.2;
        else if (taxableAmount <= 1000000000) tax = 90000000 + (taxableAmount - 500000000) * 0.3;
        else if (taxableAmount <= 3000000000) tax = 240000000 + (taxableAmount - 1000000000) * 0.4;
        else tax = 1040000000 + (taxableAmount - 3000000000) * 0.5;

        //   (  )
        if (isGenerationSkip && (relationship === 'grandchild')) tax = Math.floor(tax * 1.3);

        //  3%
        const discount = Math.floor(tax * 0.03);
        const finalTax = Math.floor(tax - discount);

        return { exemption, taxableAmount, tax: Math.floor(tax), discount, finalTax };
    }, [giftAmount, relationship, isGenerationSkip]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <Banknote className="w-5 h-5 mr-3 text-pink-600" />  
                </h3>
                <div className="space-y-5">
                    <NumberInput label=" " value={giftAmount} onChange={setGiftAmount} color="orange" />
                    <div>
                        <label className="text-xs font-bold text-slate-500 uppercase tracking-widest block mb-2 ml-1"> </label>
                        <div className="grid grid-cols-2 gap-2">
                            {[{ id: 'spouse', label: ' (6)' }, { id: 'child', label: ' (5)' }, { id: 'grandchild', label: ' (5)' }, { id: 'other', label: ' (1)' }].map(r => (
                                <button key={r.id} onClick={() => setRelationship(r.id)} className={`py-3 rounded-xl text-xs font-bold transition-all ${relationship === r.id ? 'bg-pink-500 text-white' : 'bg-slate-100 text-slate-500'}`}>{r.label}</button>
                            ))}
                        </div>
                    </div>
                    {relationship === 'grandchild' && (
                        <label className="flex items-center gap-2 p-3 bg-yellow-50 rounded-xl cursor-pointer">
                            <input type="checkbox" checked={isGenerationSkip} onChange={(e) => setIsGenerationSkip(e.target.checked)} className="w-4 h-4 rounded" />
                            <span className="text-sm font-medium text-yellow-800">  (30% )</span>
                        </label>
                    )}
                </div>
            </div>

            {result ? (
                <div className="bg-gradient-to-br from-pink-50 to-rose-50 p-6 rounded-[2.5rem] border border-pink-100 shadow-lg">
                    <div className="text-center mb-6">
                        <p className="text-sm font-bold text-pink-400 uppercase tracking-widest mb-2"> </p>
                        <p className="text-4xl font-black text-pink-900">{formatNumber(result.finalTax)}</p>
                    </div>
                    <div className="bg-white rounded-2xl p-4 space-y-2 text-sm">
                        <div className="flex justify-between"><span className="text-slate-500"> </span><span className="font-bold text-green-600">-{formatCompactNumber(result.exemption)}</span></div>
                        <div className="flex justify-between"><span className="text-slate-500"></span><span className="font-bold">{formatCompactNumber(result.taxableAmount)}</span></div>
                        <div className="flex justify-between"><span className="text-slate-500"></span><span className="font-bold">{formatNumber(result.tax)}</span></div>
                        <div className="flex justify-between"><span className="text-slate-500"> (3%)</span><span className="font-bold text-green-600">-{formatNumber(result.discount)}</span></div>
                    </div>
                    <p className="text-xs text-center text-pink-300 mt-4">* 10     </p>
                </div>
            ) : (
                <div className="text-center py-20 text-slate-300 bg-white rounded-[2rem] border-2 border-dashed border-slate-200">  </div>
            )}
        </div>
    );
};
