import React, { useState, useMemo } from 'react';
import { parseNumber, formatCompactNumber } from '../../utils/math.js';
import { BarChart3 } from '../ui/Icons.js';
import { NumberInput } from '../ui/Inputs.js';

// NEW:  vs  
export const RentVsBuyTab = () => {
    const [purchasePrice, setPurchasePrice] = useState('');
    const [jeonseDeposit, setJeonseDeposit] = useState('');
    const [years, setYears] = useState('5');
    const [loanRate, setLoanRate] = useState('4.5');
    const [expectedRise, setExpectedRise] = useState('3');

    const result = useMemo(() => {
        const purchase = parseNumber(purchasePrice);
        const jeonse = parseNumber(jeonseDeposit);
        const yearNum = parseInt(years) || 5;
        const rate = parseFloat(loanRate) / 100 || 0.045;
        const rise = parseFloat(expectedRise) / 100 || 0.03;

        if (!purchase || !jeonse) return null;

        //  
        const acquisitionTax = Math.floor(purchase * 0.011); //   1.1%
        const loanAmount = purchase - jeonse; //    
        const annualInterest = loanAmount * rate;
        const totalInterest = annualInterest * yearNum;
        const propertyTax = Math.floor(purchase * 0.001 * yearNum); //   0.1%
        const sellingFee = Math.floor(purchase * 0.004); //   0.4%
        const capitalGainTax = Math.floor(Math.max((purchase * Math.pow(1 + rise, yearNum) - purchase) * 0.2, 0)); //  

        const buyCost = acquisitionTax + totalInterest + propertyTax + sellingFee + capitalGainTax;
        const futureValue = Math.floor(purchase * Math.pow(1 + rise, yearNum));
        const buyNetGain = futureValue - purchase - buyCost;

        //  
        const jeonseInterest = jeonse * rate * yearNum; // 
        const jeonseMoveFee = 1000000 * (yearNum / 2); //  (2)
        const rentCost = jeonseInterest + jeonseMoveFee;

        const difference = buyCost - rentCost;
        const winner = buyNetGain > 0 ? '' : '';
        const breakEvenYear = rise > 0 ? Math.ceil(buyCost / (purchase * rise)) : 0;

        return { buyCost, rentCost, difference, winner, futureValue, buyNetGain, breakEvenYear, capitalGainTax };
    }, [purchasePrice, jeonseDeposit, years, loanRate, expectedRise]);

    return (
        <div className="space-y-6 pb-32 animate-slide-up px-1">
            <div className="bg-white p-6 rounded-3xl shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                    <BarChart3 className="w-5 h-5 mr-3 text-cyan-600" />  vs  
                </h3>
                <div className="space-y-5">
                    <NumberInput label="" value={purchasePrice} onChange={setPurchasePrice} color="cyan" />
                    <NumberInput label="" value={jeonseDeposit} onChange={setJeonseDeposit} color="cyan" />
                    <div className="flex gap-3">
                        <div className="flex-1">
                            <label className="text-xs font-bold text-slate-500 uppercase block mb-2 ml-1"> ()</label>
                            <select value={years} onChange={(e) => setYears(e.target.value)} className="w-full px-4 py-3 bg-slate-50 rounded-2xl font-bold outline-none focus:ring-2 focus:ring-cyan-500">
                                <option value="3">3</option><option value="5">5</option><option value="10">10</option>
                            </select>
                        </div>
                        <div className="flex-1">
                            <label className="text-xs font-bold text-slate-500 uppercase block mb-2 ml-1">  (%)</label>
                            <input type="number" value={expectedRise} onChange={(e) => setExpectedRise(e.target.value)} className="w-full px-4 py-3 text-right bg-slate-50 rounded-2xl font-bold outline-none focus:ring-2 focus:ring-cyan-500" />
                        </div>
                    </div>
                </div>
            </div>

            {result ? (
                <div className="space-y-4">
                    <div className={`p-6 rounded-[2.5rem] shadow-lg ${result.winner === '' ? 'bg-gradient-to-br from-blue-50 to-indigo-50 border border-blue-100' : 'bg-gradient-to-br from-orange-50 to-amber-50 border border-orange-100'}`}>
                        <div className="text-center mb-4">
                            <p className="text-sm font-bold uppercase tracking-widest mb-2 opacity-60">{years}  </p>
                            <p className="text-3xl font-black">{result.winner === '' ? '  ' : '  '}</p>
                            <p className="text-lg font-bold mt-2">{result.buyNetGain > 0 ? ` ${formatCompactNumber(result.buyNetGain)}` : ` ${formatCompactNumber(Math.abs(result.difference))} `}</p>
                        </div>
                    </div>
                    <div className="grid grid-cols-2 gap-3">
                        <div className="bg-white p-4 rounded-2xl text-center shadow-sm border border-slate-100">
                            <p className="text-xs font-bold text-blue-500 mb-1"> </p>
                            <p className="text-lg font-black text-slate-700">{formatCompactNumber(result.buyCost)}</p>
                        </div>
                        <div className="bg-white p-4 rounded-2xl text-center shadow-sm border border-slate-100">
                            <p className="text-xs font-bold text-orange-500 mb-1"> </p>
                            <p className="text-lg font-black text-slate-700">{formatCompactNumber(result.rentCost)}</p>
                        </div>
                    </div>
                    {result.breakEvenYear > 0 && result.breakEvenYear < 30 && (
                        <p className="text-center text-sm text-slate-500"> :  {result.breakEvenYear}</p>
                    )}
                </div>
            ) : (
                <div className="text-center py-20 text-slate-300 bg-white rounded-[2rem] border-2 border-dashed border-slate-200">  </div>
            )}
        </div>
    );
};
